import { useState } from 'react';
import { useQuery, useMutation } from 'convex/react';
import { api } from '../../convex/_generated/api';
import { useAuth } from '@/hooks/useAuth';
import { PageLoader } from '../components/ui/PageLoader';
import { Badge } from '@/components/ui/Badge';
import { Button } from '@/components/ui/Button';
import { Check, X, MessageSquare, User } from 'lucide-react';
import { BackButton } from '../components/ui/BackButton';

export default function AdminTaskReview() {
    const { user } = useAuth();
    const [selectedTask, setSelectedTask] = useState<string | null>(null);
    const [grade, setGrade] = useState('');
    const [feedback, setFeedback] = useState('');
    const [isSubmitting, setIsSubmitting] = useState(false);

    // Only fetch if admin
    const pendingReviews = useQuery(
        api.assignments.getPendingReviews,
        user && (user.role === 'admin' || user.role === 'super_admin')
            ? { adminId: user._id }
            : 'skip'
    );

    const reviewAssignment = useMutation(api.assignments.reviewAssignment);

    if (!user || (user.role !== 'admin' && user.role !== 'super_admin')) {
        return <div className="p-8 text-center">Unauthorized</div>;
    }

    if (pendingReviews === undefined) return <PageLoader />;

    const handleReview = async (status: 'approved' | 'rejected') => {
        if (!selectedTask || !user) return;

        setIsSubmitting(true);
        try {
            // @ts-ignore
            await reviewAssignment({
                assignmentId: selectedTask as any,
                adminId: user._id,
                status,
                grade: grade.trim(),
                feedback: feedback.trim(),
            });
            setSelectedTask(null);
            setGrade('');
            setFeedback('');
        } catch (error) {
            console.error("Failed to review:", error);
            alert("Failed to submitting review");
        } finally {
            setIsSubmitting(false);
        }
    };

    return (
        <div className="container max-w-5xl mx-auto px-4 py-8 pb-20">
            <BackButton />
            <h1 className="text-3xl md:text-4xl font-bold mb-6 text-gradient font-display">Task Reviews</h1>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                {/* List of Pending Reviews */}
                <div className="space-y-4">
                    <h2 className="font-semibold text-lg mb-2 text-light-text dark:text-dark-text">
                        Pending Submissions ({pendingReviews.length})
                    </h2>

                    {pendingReviews.length === 0 && (
                        <div className="glass p-8 text-center text-light-muted dark:text-dark-muted">
                            No tasks waiting for review.
                        </div>
                    )}

                    {pendingReviews.map((task) => (
                        <div
                            key={task._id}
                            onClick={() => setSelectedTask(task._id)}
                            className={`glass p-4 rounded-xl cursor-pointer transition-all border ${selectedTask === task._id
                                ? 'border-primary-500 ring-1 ring-primary-500'
                                : 'border-transparent hover:border-primary-500/50'
                                }`}
                        >
                            <div className="flex items-center gap-3 mb-3">
                                <div className="w-8 h-8 rounded-full bg-light-bg dark:bg-dark-bg flex items-center justify-center">
                                    <User className="w-4 h-4" />
                                </div>
                                <div>
                                    <p className="font-medium text-light-text dark:text-dark-text">{task.userName}</p>
                                    <p className="text-xs text-light-muted dark:text-dark-muted">
                                        Submitted {new Date(task.updatedAt).toLocaleDateString()}
                                    </p>
                                </div>
                            </div>

                            <div className="mb-2">
                                <p className="text-xs font-semibold uppercase tracking-wider text-light-muted dark:text-dark-muted mb-1">Task</p>
                                <p className="text-sm text-light-text dark:text-dark-text line-clamp-2">{task.instructions}</p>
                            </div>

                            <Badge variant="warning" className="mt-2">Needs Review</Badge>
                        </div>
                    ))}
                </div>

                {/* Review Panel */}
                <div className="glass p-6 rounded-xl h-fit sticky top-24">
                    {selectedTask ? (
                        (() => {
                            const task = pendingReviews.find(t => t._id === selectedTask);
                            if (!task) return null;
                            return (
                                <div className="space-y-6">
                                    <div className="border-b border-light-border dark:border-dark-border pb-4">
                                        <h3 className="font-bold text-xl text-light-text dark:text-dark-text mb-1">
                                            Reviewing {task.userName}'s Work
                                        </h3>
                                        <p className="text-sm text-light-muted dark:text-dark-muted">
                                            Original Task: {task.instructions}
                                        </p>
                                    </div>

                                    <div>
                                        <h4 className="font-semibold text-light-text dark:text-dark-text mb-2">Submission:</h4>
                                        <div className="bg-light-bg dark:bg-dark-bg p-4 rounded-lg text-light-text dark:text-dark-text whitespace-pre-wrap">
                                            {task.submissionContent}
                                        </div>
                                    </div>

                                    <div className="space-y-4 pt-4 border-t border-light-border dark:border-dark-border">
                                        <div>
                                            <label className="block text-sm font-medium mb-1">Grade / Score (Optional)</label>
                                            <input
                                                type="text"
                                                value={grade}
                                                onChange={(e) => setGrade(e.target.value)}
                                                placeholder="e.g. A+, 95/100, Passed"
                                                className="w-full px-4 py-2 rounded-lg bg-light-bg dark:bg-dark-bg border border-light-border dark:border-dark-border"
                                            />
                                        </div>
                                        <div>
                                            <label className="block text-sm font-medium mb-1">Feedback (Optional)</label>
                                            <textarea
                                                value={feedback}
                                                onChange={(e) => setFeedback(e.target.value)}
                                                placeholder="Great work! Just one thing..."
                                                className="w-full px-4 py-2 rounded-lg bg-light-bg dark:bg-dark-bg border border-light-border dark:border-dark-border min-h-[100px]"
                                            />
                                        </div>

                                        <div className="flex gap-3 pt-2">
                                            <Button
                                                className="flex-1 bg-red-500 hover:bg-red-600 text-white"
                                                onClick={() => handleReview('rejected')}
                                                disabled={isSubmitting}
                                            >
                                                <X className="w-4 h-4 mr-2" />
                                                Reject
                                            </Button>
                                            <Button
                                                className="flex-1 bg-green-500 hover:bg-green-600 text-white"
                                                onClick={() => handleReview('approved')}
                                                disabled={isSubmitting}
                                            >
                                                <Check className="w-4 h-4 mr-2" />
                                                Approve
                                            </Button>
                                        </div>
                                    </div>
                                </div>
                            );
                        })()
                    ) : (
                        <div className="h-full flex flex-col items-center justify-center text-light-muted dark:text-dark-muted py-12">
                            <MessageSquare className="w-12 h-12 mb-4 opacity-50" />
                            <p>Select a submission to review</p>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
}
