import { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { motion, AnimatePresence } from 'framer-motion';
import { Moon, Sun, LogOut, LayoutDashboard, Search } from 'lucide-react';
import { useAuth } from '@/hooks/useAuth';
import { useTheme } from '@/context/ThemeContext';
import { Button } from '@/components/ui/Button';

import { MemberTicker } from '@/components/users/MemberTicker';

export function Header() {
    const { user, logout } = useAuth();
    const { theme, toggleTheme } = useTheme();
    const navigate = useNavigate();
    const [mobileMenuOpen, setMobileMenuOpen] = useState(false);

    const handleLogout = () => {
        logout();
        navigate('/');
    };

    return (
        <header className="fixed top-2 md:top-0 left-0 right-0 z-40 px-3 md:px-0 transition-all duration-300">
            <div className="mx-auto max-w-7xl md:max-w-full relative">
                <div className="glass shadow-2xl rounded-2xl md:rounded-b-3xl md:rounded-t-none border border-light-border/50 dark:border-dark-border/50 overflow-hidden">
                    <div className="px-4 md:px-12 h-14 md:h-20 flex items-center justify-between">
                        {/* Logo */}
                        <Link
                            to="/"
                            state={{ manual: true }}
                            className="flex items-center space-x-2"
                        >
                            <motion.div
                                whileHover={{ scale: 1.05 }}
                                className="flex items-center gap-2"
                            >
                                <img
                                    src="https://ik.imagekit.io/h3qzmviwv/baptizo/posts/images/Baptizo-logo.png"
                                    alt="Baptizo"
                                    className="h-8 md:h-12 w-auto rounded-xl shadow-lg"
                                />
                                <span className="text-xl md:text-3xl font-black text-gradient font-display tracking-tight">Baptizo</span>
                            </motion.div>
                        </Link>

                        {/* Desktop Navigation */}
                        <nav className="hidden md:flex items-center space-x-8">
                            {user ? (
                                <>
                                    <Link
                                        to="/feed"
                                        className="text-light-text dark:text-dark-text hover:text-primary-500 transition-all font-semibold text-lg"
                                    >
                                        Feed
                                    </Link>
                                    <Link
                                        to="/profile"
                                        className="text-light-text dark:text-dark-text hover:text-primary-500 transition-all font-semibold text-lg"
                                    >
                                        Profile
                                    </Link>
                                    {user.role === 'admin' || user.role === 'super_admin' ? (
                                        <Link
                                            to="/admin"
                                            className="px-4 py-2 rounded-xl bg-primary-500/10 text-primary-500 hover:bg-primary-500 hover:text-white transition-all font-bold flex items-center gap-2"
                                        >
                                            <LayoutDashboard className="w-5 h-5" />
                                            Admin
                                        </Link>
                                    ) : null}
                                    {user.role === 'board_member' && (
                                        <Link
                                            to="/board"
                                            className="px-4 py-2 rounded-xl bg-secondary-500/10 text-secondary-500 hover:bg-secondary-500 hover:text-white transition-all font-bold flex items-center gap-2"
                                        >
                                            <LayoutDashboard className="w-5 h-5" />
                                            Board
                                        </Link>
                                    )}
                                </>
                            ) : (
                                <>
                                    <Link
                                        to="/login"
                                        className="text-light-text dark:text-dark-text hover:text-primary-500 transition-all font-semibold text-lg"
                                    >
                                        Login
                                    </Link>
                                    <Link to="/register">
                                        <Button size="lg" className="rounded-xl font-bold px-8 shadow-xl shadow-primary-500/20">Get Started</Button>
                                    </Link>
                                </>
                            )}
                        </nav>

                        {/* Right Section */}
                        <div className="flex items-center space-x-2 md:space-x-4">
                            {/* Search Icon (visible when logged in) */}
                            {user && (
                                <motion.button
                                    whileTap={{ scale: 0.9 }}
                                    onClick={() => navigate('/search')}
                                    className="p-2 md:p-3 rounded-xl hover:bg-light-card/50 dark:hover:bg-dark-card/50 transition-all border border-transparent hover:border-light-border dark:hover:border-dark-border shadow-sm"
                                    title="Search Members"
                                >
                                    <Search className="w-5 h-5 md:w-6 md:h-6 text-primary-500" />
                                </motion.button>
                            )}

                            {/* Theme Toggle */}
                            <motion.button
                                whileTap={{ scale: 0.9 }}
                                onClick={toggleTheme}
                                className="p-2 md:p-3 rounded-xl hover:bg-light-card/50 dark:hover:bg-dark-card/50 transition-all border border-transparent hover:border-light-border dark:hover:border-dark-border shadow-sm"
                            >
                                {theme === 'dark' ? (
                                    <Sun className="w-5 h-5 md:w-6 md:h-6 text-yellow-500" />
                                ) : (
                                    <Moon className="w-5 h-5 md:w-6 md:h-6 text-slate-700" />
                                )}
                            </motion.button>

                            {/* User Menu (Desktop) */}
                            {user && (
                                <div className="hidden md:flex items-center space-x-3">
                                    <div className="flex items-center space-x-3 p-1.5 rounded-xl bg-light-card/30 dark:bg-dark-card/30 border border-light-border/20 dark:border-dark-border/20 shadow-inner">
                                        <div className="w-8 h-8 rounded-lg bg-gradient-to-br from-primary-500 to-secondary-500 flex items-center justify-center text-white font-bold overflow-hidden">
                                            {user.avatarUrl ? (
                                                <img src={user.avatarUrl} alt={user.name} className="w-full h-full object-cover" />
                                            ) : (
                                                user.name[0]
                                            )}
                                        </div>
                                        <div className="flex flex-col">
                                            <span className="text-sm font-bold text-light-text dark:text-dark-text pr-2 leading-none">
                                                {user.name}
                                            </span>
                                            {(user.role === 'board_member' || user.role === 'admin' || user.role === 'super_admin') && (
                                                <span className="text-[9px] font-bold text-secondary-500 tracking-wide mt-0.5">
                                                    {user.role === 'board_member' ? 'BOARD MEMBER' : user.role.replace('_', ' ').toUpperCase()}
                                                </span>
                                            )}
                                        </div>
                                    </div>
                                    <motion.button
                                        whileHover={{ scale: 1.05 }}
                                        whileTap={{ scale: 0.95 }}
                                        onClick={handleLogout}
                                        className="p-3 rounded-xl bg-red-500/10 text-red-500 hover:bg-red-500 hover:text-white transition-all shadow-sm"
                                    >
                                        <LogOut className="w-5 h-5" />
                                    </motion.button>
                                </div>
                            )}

                            {/* Premium Hamburger Menu */}
                            <button
                                onClick={() => setMobileMenuOpen(!mobileMenuOpen)}
                                className="md:hidden flex flex-col items-center justify-center w-10 h-10 space-y-1.5 focus:outline-none z-50 group"
                                aria-label="Toggle menu"
                            >
                                <motion.span
                                    animate={mobileMenuOpen ? { rotate: 45, y: 7.5 } : { rotate: 0, y: 0 }}
                                    className="w-6 h-0.5 bg-light-text dark:bg-dark-text rounded-full transition-all group-hover:bg-primary-500"
                                />
                                <motion.span
                                    animate={mobileMenuOpen ? { opacity: 0, x: -20 } : { opacity: 1, x: 0 }}
                                    className="w-6 h-0.5 bg-light-text dark:bg-dark-text rounded-full transition-all group-hover:bg-primary-500"
                                />
                                <motion.span
                                    animate={mobileMenuOpen ? { rotate: -45, y: -7.5 } : { rotate: 0, y: 0 }}
                                    className="w-6 h-0.5 bg-light-text dark:bg-dark-text rounded-full transition-all group-hover:bg-primary-500"
                                />
                            </button>
                        </div>
                    </div>

                    {/* Mobile Navigation Dropdown */}
                    <AnimatePresence>
                        {mobileMenuOpen && (
                            <motion.div
                                initial={{ opacity: 0, height: 0 }}
                                animate={{ opacity: 1, height: 'auto' }}
                                exit={{ opacity: 0, height: 0 }}
                                className="md:hidden border-t border-light-border/20 dark:border-dark-border/20 bg-light-bg/50 dark:bg-dark-bg/50 backdrop-blur-xl"
                            >
                                <nav className="flex flex-col p-4 space-y-2">
                                    {user ? (
                                        <>
                                            <Link
                                                to="/feed"
                                                className="px-4 py-3 rounded-xl hover:bg-primary-500/10 text-light-text dark:text-dark-text transition-all font-semibold flex items-center justify-between group"
                                                onClick={() => setMobileMenuOpen(false)}
                                            >
                                                Feed
                                                <div className="w-2 h-2 rounded-full bg-primary-500 opacity-0 group-hover:opacity-100 transition-opacity" />
                                            </Link>
                                            <Link
                                                to="/profile"
                                                className="px-4 py-3 rounded-xl hover:bg-primary-500/10 text-light-text dark:text-dark-text transition-all font-semibold flex items-center justify-between group"
                                                onClick={() => setMobileMenuOpen(false)}
                                            >
                                                Profile
                                                <div className="w-2 h-2 rounded-full bg-primary-500 opacity-0 group-hover:opacity-100 transition-opacity" />
                                            </Link>

                                            {/* Mobile Member Ticker */}
                                            <div className="px-4 pt-2 pb-1">
                                                <MemberTicker />
                                            </div>

                                            {user.role === 'admin' || user.role === 'super_admin' ? (
                                                <Link
                                                    to="/admin"
                                                    className="px-4 py-3 rounded-xl bg-primary-500/10 text-primary-500 hover:bg-primary-500 hover:text-white transition-all font-bold flex items-center gap-3"
                                                    onClick={() => setMobileMenuOpen(false)}
                                                >
                                                    <LayoutDashboard className="w-5 h-5" />
                                                    Admin Dashboard
                                                </Link>
                                            ) : null}

                                            {user.role === 'board_member' && (
                                                <Link
                                                    to="/board"
                                                    className="px-4 py-3 rounded-xl bg-secondary-500/10 text-secondary-500 hover:bg-secondary-500 hover:text-white transition-all font-bold flex items-center gap-3"
                                                    onClick={() => setMobileMenuOpen(false)}
                                                >
                                                    <LayoutDashboard className="w-5 h-5" />
                                                    Board Portal
                                                </Link>
                                            )}

                                            <div className="h-px bg-light-border/20 dark:border-dark-border/20 my-2" />

                                            <div className="px-4 py-3 flex items-center justify-between bg-light-card/30 dark:bg-dark-card/30 rounded-xl border border-light-border/10">
                                                <div className="flex items-center gap-3">
                                                    <div className="w-8 h-8 rounded-lg bg-gradient-to-br from-primary-500 to-secondary-500 flex items-center justify-center text-white font-bold text-xs overflow-hidden">
                                                        {user.avatarUrl ? (
                                                            <img src={user.avatarUrl} alt={user.name} className="w-full h-full object-cover" />
                                                        ) : (
                                                            user.name[0]
                                                        )}
                                                    </div>
                                                    <span className="font-bold text-sm text-light-text dark:text-dark-text">
                                                        {user.name}
                                                    </span>
                                                </div>
                                                <button
                                                    onClick={() => {
                                                        handleLogout();
                                                        setMobileMenuOpen(false);
                                                    }}
                                                    className="p-2 rounded-lg text-red-500 hover:bg-red-500 hover:text-white transition-all"
                                                >
                                                    <LogOut className="w-5 h-5" />
                                                </button>
                                            </div>
                                        </>
                                    ) : (
                                        <>
                                            <Link
                                                to="/login"
                                                className="px-4 py-3 rounded-xl hover:bg-primary-500/10 text-light-text dark:text-dark-text transition-all font-semibold"
                                                onClick={() => setMobileMenuOpen(false)}
                                            >
                                                Login
                                            </Link>
                                            <Link
                                                to="/register"
                                                onClick={() => setMobileMenuOpen(false)}
                                                className="px-4 pt-2"
                                            >
                                                <Button className="w-full rounded-xl font-bold py-6">Get Started</Button>
                                            </Link>
                                        </>
                                    )}
                                </nav>
                            </motion.div>
                        )}
                    </AnimatePresence>
                </div>
            </div>
        </header>
    );
}
