import { useState } from 'react';
import { useMutation } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { Modal } from '@/components/ui/Modal';
import { Button } from '@/components/ui/Button';
import { Id } from '../../../convex/_generated/dataModel';
import { useAuth } from '@/hooks/useAuth';
import { Award } from 'lucide-react';

interface GradeResponseModalProps {
    isOpen: boolean;
    onClose: () => void;
    responseId: Id<"responses">;
    userName: string;
    content: string;
}

export function GradeResponseModal({ isOpen, onClose, responseId, userName, content }: GradeResponseModalProps) {
    const { user } = useAuth();
    const gradeResponse = useMutation(api.responses.gradeResponse);

    const [grade, setGrade] = useState<number>(0);
    const [feedback, setFeedback] = useState('');
    const [isSubmitting, setIsSubmitting] = useState(false);

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        if (!user || user.role !== 'admin' && user.role !== 'super_admin') return;

        setIsSubmitting(true);
        try {
            await gradeResponse({
                adminId: user._id,
                responseId,
                grade,
                feedback: feedback.trim(),
            });
            onClose();
        } catch (error) {
            console.error("Failed to grade response:", error);
            alert("Failed to save grade. Please try again.");
        } finally {
            setIsSubmitting(false);
        }
    };

    return (
        <Modal isOpen={isOpen} onClose={onClose} title={`Grade Response from ${userName}`}>
            <form onSubmit={handleSubmit} className="space-y-4">
                <div className="bg-light-bg dark:bg-dark-bg p-4 rounded-lg text-sm text-light-text dark:text-dark-text border border-light-border dark:border-dark-border max-h-40 overflow-y-auto">
                    <p className="whitespace-pre-wrap">{content}</p>
                </div>

                <div>
                    <label className="block text-sm font-medium text-light-text dark:text-dark-text mb-1">
                        Score (0-100)
                    </label>
                    <div className="flex items-center gap-4">
                        <input
                            type="range"
                            min="0"
                            max="100"
                            value={grade}
                            onChange={(e) => setGrade(parseInt(e.target.value))}
                            className="flex-1 h-2 bg-gray-200 rounded-lg appearance-none cursor-pointer dark:bg-gray-700"
                        />
                        <input
                            type="number"
                            min="0"
                            max="100"
                            value={grade}
                            onChange={(e) => setGrade(Math.min(100, Math.max(0, parseInt(e.target.value) || 0)))}
                            className="w-16 p-2 rounded-lg bg-light-bg dark:bg-dark-bg border border-light-border dark:border-dark-border text-center font-bold"
                        />
                    </div>
                </div>

                <div>
                    <label className="block text-sm font-medium text-light-text dark:text-dark-text mb-1">
                        Feedback (Optional)
                    </label>
                    <textarea
                        value={feedback}
                        onChange={(e) => setFeedback(e.target.value)}
                        placeholder="Great job! Next time try to..."
                        className="w-full min-h-[100px] p-3 rounded-lg bg-light-bg dark:bg-dark-bg border border-light-border dark:border-dark-border focus:ring-2 focus:ring-primary-500 text-light-text dark:text-dark-text"
                    />
                </div>

                <div className="flex justify-end gap-2 pt-2">
                    <Button type="button" variant="ghost" onClick={onClose}>
                        Cancel
                    </Button>
                    <Button
                        type="submit"
                        variant="primary"
                        disabled={isSubmitting}
                        className="flex items-center gap-2"
                    >
                        <Award className="w-4 h-4" />
                        {isSubmitting ? 'Saving...' : 'Save Grade'}
                    </Button>
                </div>
            </form>
        </Modal>
    );
}
