import { useState } from 'react';
import { useAuth } from '@/hooks/useAuth';
import { Navigate } from 'react-router-dom';
import { useQuery, useMutation } from 'convex/react';
import { api } from '../../convex/_generated/api';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/Card';
import { Badge } from '@/components/ui/Badge';
import { Button } from '@/components/ui/Button';
import { Modal } from '@/components/ui/Modal';
import {
    Trophy,
    DollarSign,
    Phone,
    Mail,
    Eye,
    EyeOff,
    Award,
    ChevronDown,
    Check,
} from 'lucide-react';
import { BackButton } from '@/components/ui/BackButton';
import { formatPhoneNumber } from '@/utils/phoneNetwork';
import { formatAccountNumber } from '@/utils/bankingUtils';
import { Id } from '../../convex/_generated/dataModel';

export function AdminRewardResponses() {
    const { user, isLoading } = useAuth();
    const [selectedPostId, setSelectedPostId] = useState<Id<"posts"> | null>(null);
    const [visibleAccounts, setVisibleAccounts] = useState<Set<number>>(new Set());
    const [expandedResponses, setExpandedResponses] = useState<Set<number>>(new Set());
    const [processingIds, setProcessingIds] = useState<Set<string>>(new Set());
    const [showPicker, setShowPicker] = useState(false);

    const markAsPaid = useMutation(api.responses.markResponseAsPaid);

    // Get admin's posts that have responses enabled
    const adminPosts = useQuery(
        api.posts.getPosts,
        user?.role === 'admin' || user?.role === 'super_admin' ? {} : 'skip'
    );

    // Get responses for selected post
    const responses = useQuery(
        api.responses.getResponsesByPost,
        selectedPostId && user?._id ? { postId: selectedPostId, adminId: user._id } : 'skip'
    );

    if (isLoading) {
        return (
            <div className="flex items-center justify-center min-h-screen">
                <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary-500" />
            </div>
        );
    }

    if (!user || (user.role !== 'admin' && user.role !== 'super_admin')) {
        return <Navigate to="/feed" replace />;
    }

    // Filter admin's own posts only
    const myPosts = adminPosts?.filter(p => p.authorId === user._id) || [];
    const selectedPost = myPosts.find(p => p._id === selectedPostId);

    const toggleAccountVisibility = (position: number) => {
        setVisibleAccounts((prev) => {
            const newSet = new Set(prev);
            if (newSet.has(position)) {
                newSet.delete(position);
            } else {
                newSet.add(position);
            }
            return newSet;
        });
    };

    const toggleResponse = (position: number) => {
        setExpandedResponses((prev) => {
            const newSet = new Set(prev);
            if (newSet.has(position)) {
                newSet.delete(position);
            } else {
                newSet.add(position);
            }
            return newSet;
        });
    };

    return (
        <div className="min-h-screen py-8">
            <div className="container mx-auto px-4 sm:px-6 lg:px-8 max-w-6xl">
                <BackButton />
                <div className="mb-6 md:mb-8">
                    <h1 className="text-3xl md:text-4xl font-bold text-gradient font-display mb-2">
                        Response Rewards
                    </h1>
                    <p className="text-sm md:text-base text-light-muted dark:text-dark-muted">
                        View responders by position and manage rewards
                    </p>
                </div>

                {/* Post Selector */}
                <Card glass className="mb-8">
                    <CardContent className="p-6">
                        <label className="block text-sm font-medium mb-2 text-light-text dark:text-dark-text">
                            Select Your Post
                        </label>
                        <div className="md:hidden">
                            <button
                                onClick={() => setShowPicker(true)}
                                className="w-full flex items-center justify-between px-4 py-3 rounded-lg bg-light-bg dark:bg-dark-bg border border-light-border dark:border-dark-border text-light-text dark:text-dark-text"
                            >
                                <span className="truncate">
                                    {selectedPost ? `[${selectedPost.category}] ${selectedPost.content.substring(0, 40)}...` : 'Choose a post...'}
                                </span>
                                <ChevronDown className="w-4 h-4 text-light-muted" />
                            </button>
                        </div>
                        <div className="hidden md:block">
                            <select
                                value={selectedPostId || ''}
                                onChange={(e) => setSelectedPostId(e.target.value as Id<"posts">)}
                                className="w-full px-4 py-3 rounded-lg bg-light-bg dark:bg-dark-bg border border-light-border dark:border-dark-border text-light-text dark:text-dark-text focus:outline-none focus:ring-2 focus:ring-primary-500"
                            >
                                <option value="">Choose a post...</option>
                                {myPosts.map((post) => (
                                    <option key={post._id} value={post._id}>
                                        [{post.category}] {post.content.substring(0, 60)}
                                        {post.content.length > 60 ? '...' : ''}
                                        {' '}({new Date(post.createdAt).toLocaleDateString()})
                                    </option>
                                ))}
                            </select>
                        </div>
                        {myPosts.length === 0 && (
                            <p className="text-sm text-light-muted dark:text-dark-muted mt-2">
                                You haven't created any posts yet.
                            </p>
                        )}
                    </CardContent>
                </Card>

                {/* Stats */}
                {selectedPost && responses && (
                    <div className="grid grid-cols-1 sm:grid-cols-3 gap-3 md:gap-4 mb-6 md:mb-8">
                        <Card glass>
                            <CardContent className="p-4">
                                <div className="flex items-center justify-between">
                                    <div>
                                        <p className="text-sm text-light-muted dark:text-dark-muted">Total Responses</p>
                                        <p className="text-2xl font-bold text-light-text dark:text-dark-text">
                                            {responses.length}
                                        </p>
                                    </div>
                                    <Trophy className="w-8 h-8 text-accent-500 opacity-20" />
                                </div>
                            </CardContent>
                        </Card>

                        <Card glass>
                            <CardContent className="p-4">
                                <div className="flex items-center justify-between">
                                    <div>
                                        <p className="text-sm text-light-muted dark:text-dark-muted">Graded</p>
                                        <p className="text-2xl font-bold text-green-500">
                                            {responses.filter(r => r.grade !== undefined).length}
                                        </p>
                                    </div>
                                    <Award className="w-8 h-8 text-green-500 opacity-20" />
                                </div>
                            </CardContent>
                        </Card>

                        <Card glass>
                            <CardContent className="p-4">
                                <div className="flex items-center justify-between">
                                    <div>
                                        <p className="text-sm text-light-muted dark:text-dark-muted">Pending</p>
                                        <p className="text-2xl font-bold text-orange-500">
                                            {responses.filter(r => r.grade === undefined).length}
                                        </p>
                                    </div>
                                    <DollarSign className="w-8 h-8 text-orange-500 opacity-20" />
                                </div>
                            </CardContent>
                        </Card>
                    </div>
                )}

                {/* Responses List */}
                <div className="space-y-4">
                    {!selectedPostId ? (
                        <Card glass>
                            <CardContent className="p-12 text-center text-light-muted">
                                <Trophy className="w-16 h-16 mx-auto mb-4 opacity-20" />
                                <p>Select a post above to view responses and manage rewards</p>
                            </CardContent>
                        </Card>
                    ) : responses && responses.length > 0 ? (
                        responses.map((response) => {
                            const showAccount = visibleAccounts.has(response.position);
                            const isExpanded = expandedResponses.has(response.position);

                            return (
                                <Card key={response._id} glass>
                                    <CardHeader>
                                        <div className="flex items-center justify-between">
                                            <CardTitle className="flex items-center gap-3">
                                                <div className={`w-12 h-12 rounded-full text-white flex items-center justify-center text-lg font-bold shadow-lg ${response.position === 1 ? 'bg-gradient-to-br from-yellow-400 to-yellow-600' :
                                                    response.position === 2 ? 'bg-gradient-to-br from-gray-300 to-gray-500' :
                                                        response.position === 3 ? 'bg-gradient-to-br from-amber-600 to-amber-800' :
                                                            'bg-gradient-to-br from-primary-500 to-secondary-500'
                                                    }`}>
                                                    #{response.position}
                                                </div>
                                                <div>
                                                    <p className="text-light-text dark:text-dark-text">{response.user.name}</p>
                                                    <p className="text-xs text-light-muted dark:text-dark-muted font-normal">
                                                        {new Date(response.createdAt).toLocaleString()}
                                                    </p>
                                                </div>
                                            </CardTitle>
                                            {response.grade !== undefined && (
                                                <Badge variant="primary">
                                                    Grade: {response.grade}/100
                                                </Badge>
                                            )}
                                        </div>
                                    </CardHeader>
                                    <CardContent>
                                        <div className="grid md:grid-cols-2 gap-6">
                                            {/* Payment Info */}
                                            <div className="space-y-3">
                                                <h3 className="font-semibold text-light-text dark:text-dark-text mb-3 flex items-center gap-2">
                                                    <DollarSign className="w-5 h-5 text-accent-500" />
                                                    Payment Information
                                                </h3>

                                                {/* Email */}
                                                <div className="flex items-center gap-2">
                                                    <Mail className="w-4 h-4 text-light-muted dark:text-dark-muted" />
                                                    <span className="text-sm text-light-text dark:text-dark-text">
                                                        {response.user.email}
                                                    </span>
                                                </div>

                                                {/* Phone */}
                                                <div className="flex flex-wrap items-center gap-2">
                                                    <div className="flex items-center gap-2">
                                                        <Phone className="w-4 h-4 text-light-muted dark:text-dark-muted" />
                                                        <span className="text-sm text-light-text dark:text-dark-text font-mono">
                                                            {formatPhoneNumber(response.user.phone)}
                                                        </span>
                                                    </div>
                                                    <Badge variant="default" className="text-[10px] md:text-xs">{response.user.network}</Badge>
                                                </div>

                                                {/* Bank Details */}
                                                <div className="pt-2 border-t border-light-border dark:border-dark-border">
                                                    {response.user.bankName ? (
                                                        <div className="space-y-2 bg-light-bg dark:bg-dark-bg p-3 rounded-lg">
                                                            <div>
                                                                <p className="text-xs text-light-muted dark:text-dark-muted">Bank</p>
                                                                <p className="text-sm font-medium text-light-text dark:text-dark-text">
                                                                    {response.user.bankName}
                                                                </p>
                                                            </div>
                                                            <div>
                                                                <p className="text-xs text-light-muted dark:text-dark-muted">Account Number</p>
                                                                <div className="flex items-center justify-between gap-2 overflow-hidden">
                                                                    <p className="text-sm font-mono text-light-text dark:text-dark-text truncate">
                                                                        {showAccount
                                                                            ? response.user.accountNumber
                                                                            : formatAccountNumber(response.user.accountNumber || '')}
                                                                    </p>
                                                                    <button
                                                                        onClick={() => toggleAccountVisibility(response.position)}
                                                                        className="flex-shrink-0 p-1 text-light-muted dark:text-dark-muted hover:text-primary-500 transition-colors"
                                                                    >
                                                                        {showAccount ? <EyeOff className="w-4 h-4" /> : <Eye className="w-4 h-4" />}
                                                                    </button>
                                                                </div>
                                                            </div>
                                                            <div>
                                                                <p className="text-xs text-light-muted dark:text-dark-muted">Account Name</p>
                                                                <p className="text-sm text-light-text dark:text-dark-text">
                                                                    {response.user.accountName}
                                                                </p>
                                                            </div>
                                                        </div>
                                                    ) : (
                                                        <p className="text-sm text-light-muted dark:text-dark-muted italic">
                                                            No banking info provided
                                                        </p>
                                                    )}
                                                </div>
                                            </div>

                                            {/* Response Content */}
                                            <div className="space-y-3">
                                                <div className="flex items-center justify-between">
                                                    <h3 className="font-semibold text-light-text dark:text-dark-text">
                                                        Response Content
                                                    </h3>
                                                    <button
                                                        onClick={() => toggleResponse(response.position)}
                                                        className="text-primary-500 hover:text-primary-600 transition-colors flex items-center gap-1 text-sm"
                                                    >
                                                        {isExpanded ? 'Collapse' : 'Expand'}
                                                        <ChevronDown className={`w-4 h-4 transition-transform ${isExpanded ? 'rotate-180' : ''}`} />
                                                    </button>
                                                </div>
                                                <div className={`bg-light-bg dark:bg-dark-bg p-4 rounded-lg text-sm text-light-text dark:text-dark-text ${isExpanded ? '' : 'line-clamp-4'
                                                    }`}>
                                                    {response.content}
                                                </div>
                                                {response.feedback && (
                                                    <div className="pt-2 border-t border-light-border dark:border-dark-border">
                                                        <p className="text-xs text-light-muted dark:text-dark-muted mb-1">Admin Feedback</p>
                                                        <p className="text-sm text-light-text dark:text-dark-text italic">
                                                            "{response.feedback}"
                                                        </p>
                                                    </div>
                                                )}
                                            </div>

                                            {/* Mark as Paid Button */}
                                            <div className="pt-3 border-t border-light-border dark:border-dark-border">
                                                {response.rewardStatus === "paid" ? (
                                                    <div className="text-center p-3 rounded-lg bg-green-50 dark:bg-green-900/20 border border-green-200 dark:border-green-800">
                                                        <p className="text-sm text-green-700 dark:text-green-400 font-medium flex items-center justify-center gap-2">
                                                            <Check className="w-4 h-4" />
                                                            Reward Paid {response.rewardPaidAt && `on ${new Date(response.rewardPaidAt).toLocaleDateString()}`}
                                                        </p>
                                                    </div>
                                                ) : (
                                                    <Button
                                                        onClick={async () => {
                                                            if (!user) return;
                                                            setProcessingIds(prev => new Set(prev).add(response._id));
                                                            try {
                                                                await markAsPaid({
                                                                    responseId: response._id,
                                                                    adminId: user._id,
                                                                });
                                                            } catch (error) {
                                                                console.error('Failed to mark as paid:', error);
                                                                alert('Failed to mark as paid');
                                                            } finally {
                                                                setProcessingIds(prev => {
                                                                    const newSet = new Set(prev);
                                                                    newSet.delete(response._id);
                                                                    return newSet;
                                                                });
                                                            }
                                                        }}
                                                        variant="primary"
                                                        className="w-full flex items-center justify-center gap-2"
                                                        disabled={processingIds.has(response._id)}
                                                    >
                                                        <DollarSign className="w-4 h-4" />
                                                        {processingIds.has(response._id) ? 'Processing...' : 'Mark as Paid'}
                                                    </Button>
                                                )}
                                            </div>
                                        </div>
                                    </CardContent>
                                </Card>
                            );
                        })
                    ) : (
                        <Card glass>
                            <CardContent className="p-12 text-center text-light-muted">
                                <p>No responses yet for this post</p>
                            </CardContent>
                        </Card>
                    )}
                </div>
            </div>

            {/* Post Picker Modal for Mobile */}
            <Modal
                isOpen={showPicker}
                onClose={() => setShowPicker(false)}
                title="Select a Post"
            >
                <div className="space-y-3 max-h-[60vh] overflow-y-auto p-1">
                    {myPosts.map((post) => (
                        <button
                            key={post._id}
                            onClick={() => {
                                setSelectedPostId(post._id);
                                setShowPicker(false);
                            }}
                            className={`w-full text-left p-4 rounded-xl border-2 transition-all ${selectedPostId === post._id
                                ? 'border-primary-500 bg-primary-50/50 dark:bg-primary-900/10'
                                : 'border-light-border dark:border-dark-border hover:border-primary-500'
                                }`}
                        >
                            <div className="flex items-center gap-2 mb-1">
                                <Badge variant={post.category === 'Ancient Culture' ? 'primary' : 'secondary'}>
                                    {post.category}
                                </Badge>
                                <span className="text-xs text-light-muted">
                                    {new Date(post.createdAt).toLocaleDateString()}
                                </span>
                            </div>
                            <p className="text-sm text-light-text dark:text-dark-text line-clamp-2">
                                {post.content}
                            </p>
                        </button>
                    ))}
                    {myPosts.length === 0 && (
                        <p className="text-center py-8 text-light-muted">No posts found.</p>
                    )}
                </div>
            </Modal>
        </div >
    );
}

export default AdminRewardResponses;
