import { useState, useEffect } from 'react';
import { ChevronUp, ChevronDown } from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';

export function ScrollProgress() {
    const [scrollProgress, setScrollProgress] = useState(0);
    const [isVisible, setIsVisible] = useState(false);

    useEffect(() => {
        const handleScroll = () => {
            const totalScroll = document.documentElement.scrollHeight - window.innerHeight;
            const currentScroll = window.pageYOffset;
            const progress = (currentScroll / totalScroll) * 100;

            setScrollProgress(progress);
            setIsVisible(currentScroll > 300);
        };

        window.addEventListener('scroll', handleScroll);
        return () => window.removeEventListener('scroll', handleScroll);
    }, []);

    const scrollToTop = () => {
        window.scrollTo({ top: 0, behavior: 'smooth' });
    };

    const scrollToBottom = () => {
        window.scrollTo({ top: document.documentElement.scrollHeight, behavior: 'smooth' });
    };

    return (
        <AnimatePresence>
            {isVisible && (
                <div className="fixed bottom-24 right-4 md:right-6 z-50 flex flex-col gap-2 md:gap-3">
                    {/* Scroll to Top */}
                    <motion.button
                        initial={{ scale: 0, opacity: 0 }}
                        animate={{ scale: 1, opacity: 1 }}
                        exit={{ scale: 0, opacity: 0 }}
                        onClick={scrollToTop}
                        className="p-2 md:p-3 rounded-full glass border border-primary-500/30 text-primary-500 shadow-lg hover:bg-primary-500 hover:text-white transition-all group relative overflow-hidden"
                    >
                        <div className="absolute inset-0 bg-primary-500/10 opacity-0 group-hover:opacity-100 transition-opacity" />
                        <ChevronUp className="w-5 h-5 md:w-6 md:h-6" />
                    </motion.button>

                    {/* Progress Circle Wrapper */}
                    <div className="relative p-0.5 md:p-1">
                        <svg className="w-10 h-10 md:w-12 md:h-12 transform -rotate-90">
                            {/* Background Circle */}
                            <circle
                                cx="20"
                                cy="20"
                                r="18"
                                stroke="currentColor"
                                strokeWidth="3"
                                fill="transparent"
                                className="text-light-border dark:text-dark-border opacity-30 md:hidden"
                            />
                            <circle
                                cx="24"
                                cy="24"
                                r="20"
                                stroke="currentColor"
                                strokeWidth="4"
                                fill="transparent"
                                className="text-light-border dark:text-dark-border opacity-30 hidden md:block"
                            />
                            {/* Progress Circle Mobile */}
                            <motion.circle
                                cx="20"
                                cy="20"
                                r="18"
                                stroke="currentColor"
                                strokeWidth="3"
                                fill="transparent"
                                strokeDasharray="113.1"
                                animate={{ strokeDashoffset: 113.1 - (113.1 * scrollProgress) / 100 }}
                                className="text-primary-500 md:hidden"
                                strokeLinecap="round"
                            />
                            {/* Progress Circle Desktop */}
                            <motion.circle
                                cx="24"
                                cy="24"
                                r="20"
                                stroke="currentColor"
                                strokeWidth="4"
                                fill="transparent"
                                strokeDasharray="125.66"
                                animate={{ strokeDashoffset: 125.66 - (125.66 * scrollProgress) / 100 }}
                                className="text-primary-500 hidden md:block"
                                strokeLinecap="round"
                            />
                        </svg>

                        <div className="absolute inset-0 flex items-center justify-center text-[8px] md:text-[10px] font-bold text-primary-500">
                            {Math.round(scrollProgress)}%
                        </div>
                    </div>

                    {/* Scroll to Bottom */}
                    <motion.button
                        initial={{ scale: 0, opacity: 0 }}
                        animate={{ scale: 1, opacity: 1 }}
                        exit={{ scale: 0, opacity: 0 }}
                        onClick={scrollToBottom}
                        className="p-2 md:p-3 rounded-full glass border border-primary-500/30 text-primary-500 shadow-lg hover:bg-primary-500 hover:text-white transition-all group relative overflow-hidden"
                    >
                        <div className="absolute inset-0 bg-primary-500/10 opacity-0 group-hover:opacity-100 transition-opacity" />
                        <ChevronDown className="w-5 h-5 md:w-6 md:h-6" />
                    </motion.button>
                </div>
            )}
        </AnimatePresence>
    );
}
