import { User } from 'lucide-react';
import { getOptimizedImageUrl } from '@/lib/imagekit';
import { cn } from '@/lib/cn';

interface AvatarProps {
    src?: string;
    alt?: string;
    size?: 'xs' | 'sm' | 'md' | 'lg' | 'xl';
    className?: string;
}

export function Avatar({ src, alt = 'Avatar', size = 'md', className }: AvatarProps) {
    const sizeClasses = {
        xs: 'w-6 h-6',
        sm: 'w-8 h-8',
        md: 'w-10 h-10',
        lg: 'w-14 h-14',
        xl: 'w-20 h-20',
    };

    const iconSizes = {
        xs: 'w-3 h-3',
        sm: 'w-4 h-4',
        md: 'w-5 h-5',
        lg: 'w-7 h-7',
        xl: 'w-10 h-10',
    };

    return (
        <div
            className={cn(
                'rounded-full overflow-hidden bg-gradient-to-br from-primary-400 to-primary-600 flex items-center justify-center',
                sizeClasses[size],
                className
            )}
        >
            {src ? (
                <img
                    src={getOptimizedImageUrl(src, 200, 200)}
                    alt={alt}
                    className="w-full h-full object-cover"
                />
            ) : (
                <User className={cn('text-white', iconSizes[size])} />
            )}
        </div>
    );
}
