import { useState } from 'react';
import { useQuery } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { useAuth } from '@/hooks/useAuth';
import { PostCard } from './PostCard';
import { PostCardSkeleton } from '@/components/ui/Skeletons';
import { AnimatePresence } from 'framer-motion';

interface PostFeedProps {
    category?: 'Ancient Culture' | 'Wisdom of The Now';
    onCommentClick?: (post: any) => void;
    selectedPostId?: string;
    onlyOfficial?: boolean;
}

export function PostFeed(props: PostFeedProps) {
    const { category, onCommentClick } = props;
    const { user: _user } = useAuth();
    const [selectedCategory, setSelectedCategory] = useState<'Ancient Culture' | 'Wisdom of The Now' | undefined>(category);

    const posts = useQuery(api.posts.getPosts, {
        category: selectedCategory,
        limit: 50,
        onlyOfficial: props.onlyOfficial,
        userId: _user?._id, // Pass userId to check like status
    });

    if (posts === undefined) {
        return (
            <div className="space-y-4">
                <PostCardSkeleton />
                <PostCardSkeleton />
                <PostCardSkeleton />
            </div>
        );
    }

    return (
        <div className="space-y-6">
            {/* Category Filter */}
            {!category && (
                <div className="grid grid-cols-3 gap-2">
                    <button
                        onClick={() => setSelectedCategory(undefined)}
                        className={`px-2 md:px-4 py-2 rounded-lg font-medium transition-all text-sm md:text-base ${selectedCategory === undefined
                            ? 'bg-primary-500 text-white shadow-lg shadow-primary-500/30'
                            : 'bg-light-card dark:bg-dark-card text-light-text dark:text-dark-text border border-light-border dark:border-dark-border'
                            }`}
                    >
                        All Post
                    </button>
                    <button
                        onClick={() => setSelectedCategory('Ancient Culture')}
                        className={`px-2 md:px-4 py-2 rounded-lg font-medium transition-all text-sm md:text-base ${selectedCategory === 'Ancient Culture'
                            ? 'bg-primary-500 text-white shadow-lg shadow-primary-500/30'
                            : 'bg-light-card dark:bg-dark-card text-light-text dark:text-dark-text border border-light-border dark:border-dark-border'
                            }`}
                    >
                        Ancient
                    </button>
                    <button
                        onClick={() => setSelectedCategory('Wisdom of The Now')}
                        className={`px-2 md:px-4 py-2 rounded-lg font-medium transition-all text-sm md:text-base ${selectedCategory === 'Wisdom of The Now'
                            ? 'bg-secondary-500 text-white shadow-lg shadow-secondary-500/30'
                            : 'bg-light-card dark:bg-dark-card text-light-text dark:text-dark-text border border-light-border dark:border-dark-border'
                            }`}
                    >
                        Wisdom
                    </button>
                </div>
            )}

            {/* Posts */}
            {posts.length === 0 ? (
                <div className="text-center py-12 glass rounded-xl">
                    <p className="text-light-muted dark:text-dark-muted">
                        No posts yet. Be the first to share your learning!
                    </p>
                </div>
            ) : (
                <AnimatePresence mode="popLayout">
                    <div className="space-y-4">
                        {posts.map((post) => (
                            <PostCard
                                key={post._id}
                                post={post as any}
                                onCommentClick={() => onCommentClick?.(post)}
                                hasUserLiked={(post as any).hasLiked}
                            />
                        ))}
                    </div>
                </AnimatePresence>
            )}
        </div>
    );
}
