import { useQuery } from 'convex/react';
import { api } from '../../convex/_generated/api';
import { useAuth } from '@/hooks/useAuth';
import { Navigate } from 'react-router-dom';
import { PostFeed } from '@/components/posts/PostFeed';
import { AnnouncementCard } from '@/components/announcements/AnnouncementCard';

import { useNavigate } from 'react-router-dom';

export function Feed() {
    const { user, isLoading } = useAuth();
    const navigate = useNavigate();
    // const userLikes = useQuery(api.posts.getUserLikes, user ? { userId: user._id } : 'skip');
    const announcements = useQuery(api.announcements.getAnnouncements, user ? { category: "General", userId: user._id } : 'skip');

    if (isLoading) {
        return (
            <div className="flex items-center justify-center min-h-screen">
                <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary-500" />
            </div>
        );
    }

    if (!user) {
        return <Navigate to="/login" replace />;
    }

    // Debug: Log announcements
    console.log('Announcements:', announcements);

    return (
        <div className="min-h-screen py-8">
            <div className="container mx-auto px-4 sm:px-6 lg:px-8 max-w-4xl">
                {/* Announcement Cards */}
                {announcements && announcements.length > 0 && (
                    <div className="mb-8 space-y-6">
                        {announcements.map((announcement) => (
                            <AnnouncementCard
                                key={announcement._id}
                                id={announcement._id}
                                title={announcement.title}
                                description={announcement.description}
                                imageUrl={announcement.imageUrl}
                                creatorRole={announcement.creatorRole}
                                isActive={announcement.isActive}
                            />
                        ))}
                    </div>
                )}

                {/* Simple Feed - No PostForm, just posts */}
                <PostFeed
                    onCommentClick={(post: any) => navigate(`/post/${post._id}`)}
                />
            </div>
        </div>
    );
}
