import { motion } from 'framer-motion';
import { Sparkles } from 'lucide-react';

import { cn } from '@/lib/cn';

interface RewardBadgeProps {
    position: number; // 1-5
    className?: string;
}

const positionLabels = {
    1: '1st',
    2: '2nd',
    3: '3rd',
    4: '4th',
    5: '5th',
};

const positionColors = {
    1: 'from-yellow-400 to-yellow-600',
    2: 'from-gray-300 to-gray-500',
    3: 'from-orange-400 to-orange-600',
    4: 'from-blue-400 to-blue-600',
    5: 'from-green-400 to-green-600',
};

export function RewardBadge({ position, className }: RewardBadgeProps) {
    if (position < 1 || position > 5) return null;

    return (
        <motion.div
            initial={{ scale: 0, rotate: -180 }}
            animate={{ scale: 1, rotate: 0 }}
            transition={{ type: 'spring', stiffness: 260, damping: 20 }}
            className={cn('inline-flex items-center gap-1.5', className)}
        >
            <div className={cn(
                'px-3 py-1 rounded-full flex items-center gap-1.5 shadow-lg',
                'bg-gradient-to-r text-white font-bold text-sm',
                positionColors[position as keyof typeof positionColors]
            )}>
                <Sparkles className="w-4 h-4" />
                <span>SENT</span>
                <span className="text-xs opacity-90">
                    ({positionLabels[position as keyof typeof positionLabels]})
                </span>
            </div>
        </motion.div>
    );
}
