import { useNavigate } from 'react-router-dom';
import { Avatar } from '@/components/ui/Avatar';
import { Badge } from '@/components/ui/Badge';
import { Id } from '../../../convex/_generated/dataModel';
import { ChevronRight, Trophy } from 'lucide-react';

interface ResponseItem {
    _id: Id<"responses">;
    userId: Id<"users">;
    postId: Id<"posts">;
    content: string;
    grade?: number;
    feedback?: string;
    createdAt: number;
    userName: string;
    userAvatar?: string;
    rewardStatus?: string;
}

interface ResponseListProps {
    responses: ResponseItem[];
}

export function ResponseList({ responses }: ResponseListProps) {
    const navigate = useNavigate();

    if (!responses || responses.length === 0) return null;

    // Only show top 2 in the card
    const preview = responses.slice(0, 2);
    const postId = responses[0].postId;

    return (
        <div className="mt-4 space-y-3 border-t border-light-border dark:border-dark-border pt-4">
            <h4 className="text-xs font-bold text-light-muted dark:text-dark-muted uppercase tracking-wider flex items-center justify-between">
                Official Responses
                {responses.length > 0 && (
                    <span className="text-primary-500">{responses.length} total</span>
                )}
            </h4>

            <div className="space-y-2">
                {preview.map((response, index) => (
                    <ResponseItemCard
                        key={response._id}
                        response={response}
                        rank={index + 1}
                        tier={index < 5 ? 'gold' : 'silver'}
                        onClick={() => navigate(`/post/${postId}/responses`)}
                    />
                ))}
            </div>

            {responses.length > 0 && (
                <button
                    onClick={() => navigate(`/post/${postId}/responses`)}
                    className="w-full py-2.5 text-sm text-primary-500 hover:text-primary-600 dark:text-primary-400 font-bold flex items-center justify-center gap-2 hover:bg-primary-50 dark:hover:bg-primary-900/10 rounded-xl transition-all border border-transparent hover:border-primary-500/20"
                >
                    View Detailed Responses <ChevronRight className="w-4 h-4" />
                </button>
            )}
        </div>
    );
}

function ResponseItemCard({ response, rank, tier, onClick }: { response: ResponseItem, rank: number, tier: 'gold' | 'silver', onClick?: () => void }) {
    const isGold = tier === 'gold';

    return (
        <div
            onClick={onClick}
            className={`flex items-center gap-3 p-3 rounded-lg border transition-all cursor-pointer hover:shadow-md ${isGold
                ? 'bg-primary-50/50 dark:bg-primary-900/10 border-primary-200 dark:border-primary-800 hover:bg-primary-100 dark:hover:bg-primary-900/20'
                : 'bg-light-bg dark:bg-dark-bg border-light-border dark:border-dark-border hover:bg-light-card dark:hover:bg-dark-card'
                }`}
        >
            <div className={`flex-shrink-0 w-7 h-7 flex items-center justify-center rounded-full text-xs font-bold ${isGold ? 'bg-primary-500 text-white' : 'bg-secondary-500 text-white'
                }`}>
                #{rank}
            </div>

            <div className="flex-shrink-0">
                <Avatar src={response.userAvatar} alt={response.userName} size="sm" />
            </div>

            <div className="flex-1 min-w-0">
                <div className="flex items-center gap-2">
                    <span className="font-semibold text-sm text-light-text dark:text-dark-text truncate">
                        {response.userName}
                    </span>
                    {response.rewardStatus === 'paid' && (
                        <Badge variant="primary" className="text-[10px] flex items-center gap-1 animate-pulse">
                            <Trophy className="w-3 h-3" />
                            Winner
                        </Badge>
                    )}
                </div>
            </div>

            <ChevronRight className="w-4 h-4 text-light-muted dark:text-dark-muted flex-shrink-0" />
        </div>
    );
}
