import { useState } from 'react';
import { useQuery } from 'convex/react';
import { api } from '../../convex/_generated/api';
import { useAuth } from '@/hooks/useAuth';
import { Navigate } from 'react-router-dom';
import { Card, CardContent } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
import { Badge } from '@/components/ui/Badge';
import { GradeResponseModal } from '@/components/responses/GradeResponseModal';
import { ClipboardList, CheckCircle } from 'lucide-react';
import { Id } from '../../convex/_generated/dataModel';
import { BackButton } from '@/components/ui/BackButton';

export function AdminResponseManager() {
    const { user, isLoading } = useAuth();
    const canAccess = user && (user.role === 'admin' || user.role === 'super_admin');
    const pendingResponses = useQuery(api.responses.getPendingResponses, canAccess ? { adminId: user._id } : 'skip');

    const [selectedResponse, setSelectedResponse] = useState<{
        _id: Id<"responses">;
        userName: string;
        content: string;
    } | null>(null);

    if (isLoading) return <div className="p-8 text-center text-light-muted">Loading...</div>;

    if (!user || (user.role !== 'admin' && user.role !== 'super_admin')) {
        return <Navigate to="/feed" replace />;
    }

    return (
        <div className="min-h-screen py-8">
            <div className="container mx-auto px-4 sm:px-6 lg:px-8 max-w-4xl">
                <BackButton />
                <div className="mb-6 md:mb-8">
                    <h1 className="text-3xl md:text-4xl font-bold text-gradient font-display mb-2">
                        Official Response Reviews
                    </h1>
                    <p className="text-sm md:text-base text-light-muted dark:text-dark-muted">
                        Review and grade responses to Official Posts.
                    </p>
                </div>

                <div className="grid gap-6">
                    {pendingResponses && pendingResponses.length > 0 ? (
                        (pendingResponses as any[]).map((response: any) => (
                            <Card key={response._id} glass className="hover:border-primary-500/50 transition-colors">
                                <CardContent className="p-6">
                                    <div className="flex flex-col md:flex-row gap-6">
                                        <div className="flex-1 space-y-3">
                                            <div className="flex items-center justify-between">
                                                <div className="flex items-center gap-2">
                                                    <Badge variant="outline" className="text-xs">
                                                        {response.postTitle}
                                                    </Badge>
                                                    <span className="text-xs text-light-muted">
                                                        {new Date(response._creationTime || 0).toLocaleDateString()}
                                                    </span>
                                                </div>
                                            </div>

                                            <div>
                                                <h3 className="font-semibold text-lg text-light-text dark:text-dark-text mb-1">
                                                    {response.userName}
                                                </h3>
                                                <div className="bg-light-bg dark:bg-dark-bg p-4 rounded-lg text-sm text-light-text dark:text-dark-text whitespace-pre-wrap">
                                                    {response.content}
                                                </div>
                                            </div>
                                        </div>

                                        <div className="flex flex-col justify-center items-end gap-2 md:w-48 border-t md:border-t-0 md:border-l border-light-border dark:border-dark-border pt-4 md:pt-0 md:pl-6">
                                            <Button
                                                variant="primary"
                                                onClick={() => setSelectedResponse({
                                                    _id: response._id,
                                                    userName: response.userName,
                                                    content: response.content
                                                })}
                                                className="w-full flex items-center justify-center gap-2"
                                            >
                                                <CheckCircle className="w-4 h-4" />
                                                Grade
                                            </Button>
                                        </div>
                                    </div>
                                </CardContent>
                            </Card>
                        ))
                    ) : (
                        <Card glass>
                            <CardContent className="p-12 text-center text-light-muted flex flex-col items-center gap-4">
                                <ClipboardList className="w-12 h-12 text-light-muted opacity-50" />
                                <p>No pending responses to review.</p>
                            </CardContent>
                        </Card>
                    )}
                </div>
            </div>

            {selectedResponse && (
                <GradeResponseModal
                    isOpen={!!selectedResponse}
                    onClose={() => setSelectedResponse(null)}
                    responseId={selectedResponse._id}
                    userName={selectedResponse.userName}
                    content={selectedResponse.content}
                />
            )}
        </div>
    );
}

export default AdminResponseManager;
