import { useState } from 'react';
import { motion } from 'framer-motion';
import { BadgeCheck, Crown, Eye, EyeOff, Trash2 } from 'lucide-react';
import { useAuth } from '@/hooks/useAuth';
import { useMutation } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { Id } from '../../../convex/_generated/dataModel';

interface AnnouncementCardProps {
    id: Id<"announcements">;
    title: string;
    description: string;
    imageUrl: string;
    creatorRole?: string;
    isActive: boolean;
}

export function AnnouncementCard({ id, title, description, imageUrl, creatorRole = 'admin', isActive }: AnnouncementCardProps) {
    const { user } = useAuth();
    const updateAnnouncement = useMutation(api.announcements.updateAnnouncement);
    const deleteAnnouncement = useMutation(api.announcements.deleteAnnouncement);
    const [isDeleting, setIsDeleting] = useState(false);

    const isSuperAdmin = creatorRole === 'super_admin';
    const canManage = user && (user.role === 'admin' || user.role === 'super_admin');

    const handleToggleVisibility = async () => {
        if (!canManage) return;
        try {
            await updateAnnouncement({
                adminId: user._id,
                announcementId: id,
                isActive: !isActive
            });
        } catch (error) {
            console.error("Failed to toggle visibility:", error);
        }
    };

    const handleDelete = async () => {
        if (!canManage || !window.confirm("Are you sure you want to delete this announcement?")) return;
        setIsDeleting(true);
        try {
            await deleteAnnouncement({
                adminId: user._id,
                announcementId: id
            });
        } catch (error) {
            console.error("Failed to delete announcement:", error);
            setIsDeleting(false);
        }
    };

    if (isDeleting) return null;

    return (
        <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: isActive ? 1 : 0.6, y: 0 }}
            className={`glass rounded-xl overflow-hidden shadow-lg hover:shadow-2xl transition-all duration-300 border-2 relative group ${isSuperAdmin
                ? 'border-secondary-500/50 shadow-secondary-500/10'
                : 'border-transparent'
                } ${!isActive ? 'grayscale' : ''}`}
        >
            {/* Admin Controls Overlay */}
            {canManage && (
                <div className="absolute top-4 right-4 z-20 flex items-center gap-2 opacity-0 group-hover:opacity-100 transition-opacity">
                    <button
                        onClick={handleToggleVisibility}
                        className="p-2 rounded-full bg-black/50 text-white hover:bg-black/70 backdrop-blur-md transition-colors"
                        title={isActive ? "Hide from users" : "Show to users"}
                    >
                        {isActive ? <Eye className="w-4 h-4" /> : <EyeOff className="w-4 h-4" />}
                    </button>
                    <button
                        onClick={handleDelete}
                        className="p-2 rounded-full bg-red-500/80 text-white hover:bg-red-600 backdrop-blur-md transition-colors"
                        title="Delete Announcement"
                    >
                        <Trash2 className="w-4 h-4" />
                    </button>
                </div>
            )}

            {/* Hidden Badge for Admins */}
            {!isActive && canManage && (
                <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 z-20 bg-black/80 text-white px-4 py-2 rounded-lg font-bold backdrop-blur-md border border-white/20">
                    HIDDEN
                </div>
            )}

            {/* Image */}
            <div className="relative h-48 overflow-hidden">
                <img
                    src={imageUrl}
                    alt={title}
                    className="w-full h-full object-cover transition-transform duration-300 hover:scale-105"
                />
                <div className={`absolute inset-0 bg-gradient-to-t ${isSuperAdmin ? 'from-secondary-900/80' : 'from-black/60'
                    } to-transparent`} />

                {/* Badge */}
                <div className="absolute bottom-4 left-4 flex items-center gap-2">
                    {isSuperAdmin ? (
                        <span className="px-3 py-1 rounded-full bg-secondary-500 text-white text-xs font-bold flex items-center gap-1.5 shadow-lg">
                            <Crown className="w-3 h-3 fill-current" />
                            OFFICIAL ANNOUNCEMENT
                        </span>
                    ) : (
                        <span className="px-3 py-1 rounded-full bg-primary-500 text-white text-xs font-bold flex items-center gap-1.5 shadow-lg">
                            <BadgeCheck className="w-3 h-3" />
                            ADMIN UPDATE
                        </span>
                    )}
                </div>
            </div>

            {/* Content */}
            <div className={`p-6 ${isSuperAdmin ? 'bg-secondary-500/5 dark:bg-secondary-500/5' : ''}`}>
                <h3 className={`text-2xl font-bold mb-2 ${isSuperAdmin ? 'text-secondary-600 dark:text-secondary-400' : 'text-light-text dark:text-dark-text'
                    }`}>
                    {title}
                </h3>
                <p className="text-light-muted dark:text-dark-muted leading-relaxed">
                    {description}
                </p>
            </div>
        </motion.div>
    );
}
