import { useState } from 'react';
import { motion } from 'framer-motion';
import { useQuery, useMutation } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { useAuth } from '@/hooks/useAuth';
import { Navigate } from 'react-router-dom';
import { BookOpen, PlayCircle, FileText, CheckCircle2, Clock, Award } from 'lucide-react';
import { Card, CardContent } from '@/components/ui/Card';
import { Badge } from '@/components/ui/Badge';
import { Button } from '@/components/ui/Button';
import { BackButton } from '@/components/ui/BackButton';
import { Modal } from '@/components/ui/Modal';

export function TrainingCenter() {
    const { user, isLoading } = useAuth();
    const [selectedModule, setSelectedModule] = useState<any>(null);
    const [selectedCategory, setSelectedCategory] = useState<string | undefined>(undefined);

    const modules = useQuery(
        api.training.getModules,
        { category: selectedCategory as any }
    );

    const myProgress = useQuery(
        api.training.getProgress,
        user ? { userId: user._id } : 'skip'
    );

    const trainingStats = useQuery(
        api.training.getTrainingStats,
        user ? { userId: user._id } : 'skip'
    );

    const startModule = useMutation(api.training.startModule);
    const completeModule = useMutation(api.training.completeModule);

    if (isLoading) {
        return (
            <div className="flex items-center justify-center min-h-screen">
                <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary-500" />
            </div>
        );
    }

    if (!user) {
        return <Navigate to="/login" replace />;
    }

    if (user.role !== 'board_member' && user.role !== 'admin' && user.role !== 'super_admin') {
        return <Navigate to="/feed" replace />;
    }

    const getModuleProgress = (moduleId: string) => {
        return myProgress?.find((p: any) => p.module?._id === moduleId);
    };

    const handleStartModule = async (moduleId: string) => {
        try {
            await startModule({ userId: user._id, moduleId: moduleId as any });
        } catch (error) {
            console.error('Failed to start module:', error);
        }
    };

    const handleCompleteModule = async (moduleId: string) => {
        try {
            await completeModule({ userId: user._id, moduleId: moduleId as any });
            setSelectedModule(null);
        } catch (error) {
            console.error('Failed to complete module:', error);
        }
    };

    const categories = ['Leadership', 'Ministry Skills', 'Spiritual Growth', 'Technical Skills', 'Team Building'];

    return (
        <div className="min-h-screen py-8">
            <div className="container mx-auto px-4 sm:px-6 lg:px-8 max-w-7xl">
                <BackButton />
                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    className="mb-8"
                >
                    <h1 className="text-3xl md:text-4xl font-bold text-gradient mb-2">Training Center</h1>
                    <p className="text-light-muted dark:text-dark-muted">Continue your leadership development journey</p>
                </motion.div>

                <div className="grid md:grid-cols-4 gap-4 mb-8">
                    <Card glass>
                        <CardContent className="p-6">
                            <div className="flex items-center justify-between">
                                <div>
                                    <p className="text-sm text-light-muted dark:text-dark-muted mb-1">Completed</p>
                                    <p className="text-2xl font-bold text-light-text dark:text-dark-text">
                                        {trainingStats?.completed || 0}
                                    </p>
                                </div>
                                <CheckCircle2 className="w-8 h-8 text-green-500" />
                            </div>
                        </CardContent>
                    </Card>

                    <Card glass>
                        <CardContent className="p-6">
                            <div className="flex items-center justify-between">
                                <div>
                                    <p className="text-sm text-light-muted dark:text-dark-muted mb-1">In Progress</p>
                                    <p className="text-2xl font-bold text-light-text dark:text-dark-text">
                                        {trainingStats?.inProgress || 0}
                                    </p>
                                </div>
                                <Clock className="w-8 h-8 text-accent-500" />
                            </div>
                        </CardContent>
                    </Card>

                    <Card glass>
                        <CardContent className="p-6">
                            <div className="flex items-center justify-between">
                                <div>
                                    <p className="text-sm text-light-muted dark:text-dark-muted mb-1">Total Modules</p>
                                    <p className="text-2xl font-bold text-light-text dark:text-dark-text">
                                        {trainingStats?.totalModules || 0}
                                    </p>
                                </div>
                                <BookOpen className="w-8 h-8 text-primary-500" />
                            </div>
                        </CardContent>
                    </Card>

                    <Card glass>
                        <CardContent className="p-6">
                            <div className="flex items-center justify-between">
                                <div>
                                    <p className="text-sm text-light-muted dark:text-dark-muted mb-1">Progress</p>
                                    <p className="text-2xl font-bold text-light-text dark:text-dark-text">
                                        {trainingStats?.completionPercentage || 0}%
                                    </p>
                                </div>
                                <Award className="w-8 h-8 text-secondary-500" />
                            </div>
                        </CardContent>
                    </Card>
                </div>

                <div className="flex gap-2 mb-6 overflow-x-auto pb-2">
                    <button
                        onClick={() => setSelectedCategory(undefined)}
                        className={`px-4 py-2 rounded-lg font-medium transition-all whitespace-nowrap ${selectedCategory === undefined
                            ? 'bg-primary-500 text-white shadow-lg shadow-primary-500/30'
                            : 'bg-light-card dark:bg-dark-card text-light-text dark:text-dark-text border border-light-border dark:border-dark-border'
                            }`}
                    >
                        All Categories
                    </button>
                    {categories.map((cat) => (
                        <button
                            key={cat}
                            onClick={() => setSelectedCategory(cat)}
                            className={`px-4 py-2 rounded-lg font-medium transition-all whitespace-nowrap ${selectedCategory === cat
                                ? 'bg-secondary-500 text-white shadow-lg shadow-secondary-500/30'
                                : 'bg-light-card dark:bg-dark-card text-light-text dark:text-dark-text border border-light-border dark:border-dark-border'
                                }`}
                        >
                            {cat}
                        </button>
                    ))}
                </div>

                <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-6">
                    {modules?.map((module: any) => {
                        const progress = getModuleProgress(module._id);
                        return (
                            <motion.div key={module._id} whileHover={{ y: -5 }}>
                                <Card glass className="h-full">
                                    <CardContent className="p-6">
                                        <div className="flex items-start justify-between mb-3">
                                            <Badge variant="secondary" className="text-xs">{module.category}</Badge>
                                            {progress && (
                                                <Badge variant={progress.status === 'completed' ? 'primary' : 'default'} className="text-xs">
                                                    {progress.status === 'completed' ? 'Completed' : 'In Progress'}
                                                </Badge>
                                            )}
                                        </div>
                                        <h3 className="font-semibold text-lg text-light-text dark:text-dark-text mb-2">
                                            {module.title}
                                        </h3>
                                        <p className="text-sm text-light-muted dark:text-dark-muted mb-4 line-clamp-3">
                                            {module.description}
                                        </p>
                                        <div className="flex items-center gap-4 mb-4 text-xs text-light-muted dark:text-dark-muted">
                                            {module.videoUrl && (
                                                <div className="flex items-center gap-1">
                                                    <PlayCircle className="w-4 h-4" />
                                                    Video
                                                </div>
                                            )}
                                            {module.documentUrl && (
                                                <div className="flex items-center gap-1">
                                                    <FileText className="w-4 h-4" />
                                                    Document
                                                </div>
                                            )}
                                            {module.duration && <span>{module.duration} min</span>}
                                        </div>
                                        <Button
                                            variant={progress?.status === 'completed' ? 'ghost' : 'primary'}
                                            size="sm"
                                            className="w-full"
                                            onClick={() => {
                                                setSelectedModule(module);
                                                if (!progress) {
                                                    handleStartModule(module._id);
                                                }
                                            }}
                                        >
                                            {progress?.status === 'completed' ? 'Review' : progress ? 'Continue' : 'Start Module'}
                                        </Button>
                                    </CardContent>
                                </Card>
                            </motion.div>
                        );
                    })}
                </div>

                {selectedModule && (
                    <Modal
                        isOpen={!!selectedModule}
                        onClose={() => setSelectedModule(null)}
                        title={selectedModule.title}
                    >
                        <div className="space-y-4">
                            <Badge variant="secondary">{selectedModule.category}</Badge>
                            <p className="text-light-muted dark:text-dark-muted">{selectedModule.description}</p>

                            {selectedModule.videoUrl && (
                                <div className="aspect-video rounded-lg overflow-hidden bg-black">
                                    <iframe
                                        width="100%"
                                        height="100%"
                                        src={selectedModule.videoUrl}
                                        title={selectedModule.title}
                                        frameBorder="0"
                                        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                                        allowFullScreen
                                    ></iframe>
                                </div>
                            )}

                            {selectedModule.content && (
                                <div className="prose dark:prose-invert max-w-none">
                                    <div dangerouslySetInnerHTML={{ __html: selectedModule.content }} />
                                </div>
                            )}

                            {selectedModule.documentUrl && (
                                <a
                                    href={selectedModule.documentUrl}
                                    target="_blank"
                                    rel="noopener noreferrer"
                                    className="flex items-center gap-2 text-primary-500 hover:text-primary-600"
                                >
                                    <FileText className="w-4 h-4" />
                                    Download Course Material
                                </a>
                            )}

                            <div className="flex gap-3 pt-4">
                                <Button variant="ghost" onClick={() => setSelectedModule(null)}>
                                    Close
                                </Button>
                                {getModuleProgress(selectedModule._id)?.status !== 'completed' && (
                                    <Button
                                        variant="primary"
                                        onClick={() => handleCompleteModule(selectedModule._id)}
                                    >
                                        Mark as Complete
                                    </Button>
                                )}
                            </div>
                        </div>
                    </Modal>
                )}
            </div>
        </div>
    );
}
