import { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { Heart, MessageCircle, Pin, Trash2, MoreVertical, Shield, Edit2, Check, ClipboardCheck, AlertCircle, ToggleLeft, ToggleRight, Download, Share2 } from 'lucide-react';
import { useMutation, useQuery } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { useAuth } from '@/hooks/useAuth';
import { Avatar } from '@/components/ui/Avatar';
import { Button } from '@/components/ui/Button';

import { RewardBadge } from '@/components/rewards/RewardBadge';
import { formatDistanceToNow } from 'date-fns';
import { Id } from '../../../convex/_generated/dataModel';
import { TaskAssignModal } from '../tasks/TaskAssignModal';
import { ResponseModal } from '../responses/ResponseModal';
import { ResponseList } from '../responses/ResponseList';
import { useNavigate } from 'react-router-dom';
import { ImageLightbox } from '../ui/ImageLightbox';
import { AnimatePresence } from 'framer-motion';

interface PostCardProps {
    post: {
        _id: Id<"posts">;
        content: string;
        category: 'Ancient Culture' | 'Wisdom of The Now';
        isPinned: boolean;
        likeCount: number;
        commentCount: number;
        isResponseEnabled?: boolean;
        createdAt: number;
        imageUrl?: string;
        videoUrl?: string;
        author: {
            _id: Id<"users">;
            name: string;
            role: 'admin' | 'member' | 'super_admin';
            avatarUrl?: string;
        } | null;
        rewardPosition: number | null;
        hasLiked?: boolean;
    };
    onLike?: () => void;
    onCommentClick?: () => void;
    hasUserLiked?: boolean;
}

export function PostCard({ post, onLike, onCommentClick, hasUserLiked = false }: PostCardProps) {
    const { user } = useAuth();
    const navigate = useNavigate();
    const likePost = useMutation(api.posts.likePost);
    const pinPost = useMutation(api.posts.pinPost);
    const deletePost = useMutation(api.posts.deletePost);
    const editPost = useMutation(api.editDelete.editPost);

    const [showMenu, setShowMenu] = useState(false);
    const [isLiking, setIsLiking] = useState(false);
    const [showLightbox, setShowLightbox] = useState(false);
    const [isEditing, setIsEditing] = useState(false);
    const [editContent, setEditContent] = useState(post.content);
    const [showAssignModal, setShowAssignModal] = useState(false);
    const [showResponseModal, setShowResponseModal] = useState(false);

    // Optimistic Heart State
    const [localLiked, setLocalLiked] = useState(hasUserLiked);
    const [localLikeCount, setLocalLikeCount] = useState(post.likeCount);

    const isAdmin = user?.role === 'admin' || user?.role === 'super_admin';
    const isOfficialPost = post.author?.role === 'admin' || post.author?.role === 'super_admin';

    const postResponses = useQuery(api.responses.getPostResponses, isOfficialPost ? { postId: post._id } : 'skip');
    const responseCount = postResponses?.length || 0;
    const isVacancyFilled = responseCount >= 10;
    const hasUserResponded = postResponses?.some(r => r.userId === user?._id);
    const responsesEnabled = post.isResponseEnabled;

    const toggleResponseStatus = useMutation(api.posts.toggleResponseStatus);
    const [isTogglingResponse, setIsTogglingResponse] = useState(false);

    const handleToggleResponse = async () => {
        if (!user || !isAdmin) return;
        setIsTogglingResponse(true);
        try {
            await toggleResponseStatus({
                adminId: user._id,
                postId: post._id,
                isEnabled: !responsesEnabled
            });
        } catch (error) {
            console.error("Failed to toggle response status", error);
        } finally {
            setIsTogglingResponse(false);
        }
    };

    const postAuthorIsSuperAdmin = post.author?.role === 'super_admin';
    const postAuthorIsAdmin = post.author?.role === 'admin';

    const handleLike = async () => {
        if (!user || isLiking) return;
        const newLiked = !localLiked;
        setLocalLiked(newLiked);
        setLocalLikeCount(prev => newLiked ? prev + 1 : prev - 1);
        setIsLiking(true);
        try {
            await likePost({ userId: user._id, postId: post._id });
            onLike?.();
        } catch (error) {
            setLocalLiked(!newLiked);
            setLocalLikeCount(prev => !newLiked ? prev + 1 : prev - 1);
        } finally {
            setIsLiking(false);
        }
    };

    const handleEdit = async () => {
        if (!user || !editContent.trim()) return;
        try {
            await editPost({ userId: user._id, postId: post._id, content: editContent.trim() });
            setIsEditing(false);
        } catch (error) {
            console.error('Failed to edit post:', error);
        }
    };

    const handleDelete = async () => {
        if (!user || !isAdmin) return;
        if (confirm('Are you sure you want to delete this post?')) {
            try {
                await deletePost({ adminId: user._id, postId: post._id });
            } catch (console) { }
        }
        setShowMenu(false);
    };

    const handlePin = async () => {
        if (!user || !isAdmin) return;
        try {
            await pinPost({ adminId: user._id, postId: post._id, isPinned: !post.isPinned });
        } catch (error) { }
    };

    const handleDownload = async () => {
        if (!post.imageUrl) return;
        try {
            const response = await fetch(post.imageUrl);
            const blob = await response.blob();
            const url = window.URL.createObjectURL(blob);
            const a = document.createElement('a');
            a.href = url;
            a.download = `baptizo-post-${post._id}.jpg`;
            document.body.appendChild(a);
            a.click();
            window.URL.revokeObjectURL(url);
            document.body.removeChild(a);
        } catch (error) { }
    };

    const handleShare = async () => {
        const shareUrl = `${window.location.origin}/post/${post._id}`;
        if (navigator.share) {
            try { await navigator.share({ title: 'Baptizo Post', text: post.content, url: shareUrl }); } catch (err) { }
        } else {
            try { await navigator.clipboard.writeText(shareUrl); alert('Link copied!'); } catch (err) { }
        }
    };

    useEffect(() => {
        setLocalLiked(hasUserLiked);
        setLocalLikeCount(post.likeCount);
    }, [hasUserLiked, post.likeCount]);

    return (
        <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -20 }}
            className={`
                group relative bg-white dark:bg-[#1A1A1A] rounded-2xl border border-gray-100 dark:border-gray-800 
                shadow-sm hover:shadow-lg transition-all duration-300 overflow-hidden
                ${post.isPinned ? 'ring-2 ring-primary-500/20 dark:ring-primary-500/10' : ''}
            `}
        >
            {/* Pinned Indicator */}
            {post.isPinned && (
                <div className="absolute top-0 right-0 p-2 bg-primary-500 text-white rounded-bl-xl z-20 shadow-md">
                    <Pin className="w-4 h-4 fill-current" />
                </div>
            )}

            {/* Header Section */}
            <div className="p-5 flex items-start gap-4">
                <Avatar
                    src={post.author?.avatarUrl}
                    alt={post.author?.name || 'User'}
                    size="md"
                    className={`ring-2 ring-offset-2 ring-offset-white dark:ring-offset-[#1A1A1A] ${postAuthorIsSuperAdmin ? 'ring-yellow-500' : postAuthorIsAdmin ? 'ring-primary-500' : 'ring-gray-200 dark:ring-gray-700'
                        }`}
                />

                <div className="flex-1 min-w-0 pt-0.5">
                    <div className="flex items-center gap-2 mb-0.5">
                        <h4 className="font-bold text-gray-900 dark:text-gray-100 truncate text-[15px]">
                            {post.author?.name || 'Unknown'}
                        </h4>

                        {postAuthorIsSuperAdmin && (
                            <Shield className="w-4 h-4 text-yellow-500 fill-yellow-500" />
                        )}
                        {postAuthorIsAdmin && !postAuthorIsSuperAdmin && (
                            <Shield className="w-4 h-4 text-primary-500" />
                        )}
                        {post.rewardPosition && (
                            <RewardBadge position={post.rewardPosition} />
                        )}
                    </div>

                    <div className="flex items-center gap-2 text-xs text-gray-500 dark:text-gray-400">
                        <span>{formatDistanceToNow(post.createdAt, { addSuffix: true })}</span>
                        <span></span>
                        <span className={`px-2 py-0.5 rounded-full ${post.category === 'Ancient Culture'
                            ? 'bg-orange-100 text-orange-600 dark:bg-orange-900/30 dark:text-orange-400'
                            : 'bg-blue-100 text-blue-600 dark:bg-blue-900/30 dark:text-blue-400'
                            } font-medium`}>
                            {post.category}
                        </span>
                    </div>
                </div>

                {isAdmin && (
                    <div className="relative">
                        <button
                            onClick={() => setShowMenu(!showMenu)}
                            className="p-2 -mr-2 rounded-full hover:bg-gray-100 dark:hover:bg-gray-800 text-gray-400 transition-colors"
                        >
                            <MoreVertical className="w-5 h-5" />
                        </button>

                        <AnimatePresence>
                            {showMenu && (
                                <motion.div
                                    initial={{ opacity: 0, scale: 0.95, y: -10 }}
                                    animate={{ opacity: 1, scale: 1, y: 0 }}
                                    exit={{ opacity: 0, scale: 0.95, y: -10 }}
                                    className="absolute right-0 mt-1 w-48 bg-white dark:bg-[#252525] rounded-xl shadow-xl border border-gray-100 dark:border-gray-700 z-50 overflow-hidden"
                                >
                                    <div className="p-1">
                                        <button onClick={() => { setShowMenu(false); setShowAssignModal(true); }} className="w-full flex items-center gap-2 px-3 py-2 text-sm rounded-lg hover:bg-gray-50 dark:hover:bg-white/5 text-gray-700 dark:text-gray-200">
                                            <Edit2 className="w-4 h-4" /> Assign Task
                                        </button>
                                        <button onClick={handlePin} className="w-full flex items-center gap-2 px-3 py-2 text-sm rounded-lg hover:bg-gray-50 dark:hover:bg-white/5 text-gray-700 dark:text-gray-200">
                                            <Pin className="w-4 h-4" /> {post.isPinned ? 'Unpin' : 'Pin'}
                                        </button>
                                        <button onClick={handleDelete} className="w-full flex items-center gap-2 px-3 py-2 text-sm rounded-lg hover:bg-red-50 dark:hover:bg-red-900/20 text-red-600">
                                            <Trash2 className="w-4 h-4" /> Delete
                                        </button>
                                    </div>
                                    {isOfficialPost && (
                                        <div className="border-t border-gray-100 dark:border-gray-700 p-1 bg-gray-50/50 dark:bg-white/5">
                                            <button 
                                                onClick={() => { handleToggleResponse(); setShowMenu(false); }} 
                                                disabled={isTogglingResponse}
                                                className={`w-full flex items-center gap-2 px-3 py-2 text-sm rounded-lg hover:bg-white dark:hover:bg-white/5 text-gray-700 dark:text-gray-200 ${isTogglingResponse ? 'opacity-50 cursor-not-allowed' : ''}`}
                                            >
                                                {responsesEnabled ? <ToggleRight className="w-4 h-4 text-green-500" /> : <ToggleLeft className="w-4 h-4 text-gray-400" />}
                                                {responsesEnabled ? "Disable Responses" : "Enable Responses"}
                                            </button>
                                        </div>
                                    )}
                                </motion.div>
                            )}
                        </AnimatePresence>
                    </div>
                )}
            </div>

            {/* Content Body */}
            <div className="px-5 pb-3">
                {isEditing ? (
                    <div className="space-y-3">
                        <textarea
                            value={editContent}
                            onChange={(e) => setEditContent(e.target.value)}
                            className="w-full p-4 rounded-xl bg-gray-50 dark:bg-[#222] border-transparent focus:border-primary-500 focus:ring-0 text-base"
                            rows={4}
                        />
                        <div className="flex justify-end gap-2">
                            <Button variant="ghost" size="sm" onClick={() => setIsEditing(false)}>Cancel</Button>
                            <Button variant="primary" size="sm" onClick={handleEdit}>Save</Button>
                        </div>
                    </div>
                ) : (
                    <div onClick={() => navigate(`/post/${post._id}`)} className="cursor-pointer">
                        <p className="text-[15px] leading-relaxed text-gray-800 dark:text-gray-200 whitespace-pre-wrap">
                            {post.content.length > 250 ? `${post.content.substring(0, 250)}...` : post.content}
                            {post.content.length > 250 && <span className="text-primary-500 font-medium ml-1">Read more</span>}
                        </p>
                    </div>
                )}
            </div>

            {/* Media */}
            {(post.imageUrl || post.videoUrl) && (
                <div className="px-5 pb-4">
                    {post.imageUrl && (
                        <div
                            className="rounded-xl overflow-hidden cursor-pointer border border-gray-100 dark:border-gray-800 bg-gray-50 dark:bg-[#222]"
                            onClick={() => setShowLightbox(true)}
                        >
                            <motion.img
                                layoutId={`post-image-${post._id}`}
                                src={post.imageUrl}
                                alt="Post content"
                                className="w-full h-auto max-h-[500px] object-cover hover:scale-[1.02] transition-transform duration-500"
                            />
                        </div>
                    )}
                    {post.videoUrl && (
                        <div className="rounded-xl overflow-hidden bg-black aspect-video">
                            <video src={post.videoUrl} controls className="w-full h-full" />
                        </div>
                    )}
                </div>
            )}

            {/* Actions Bar */}
            <div className="px-5 py-3 border-t border-gray-100 dark:border-gray-800 flex items-center justify-between">
                <div className="flex items-center gap-6">
                    <button
                        onClick={handleLike}
                        disabled={isLiking}
                        className="flex items-center gap-2 group transition-colors"
                    >
                        <div className={`p-2 rounded-full transition-colors ${localLiked ? 'bg-red-50 dark:bg-red-900/20 text-red-500' : 'text-gray-400 group-hover:bg-gray-100 dark:group-hover:bg-gray-800 dark:group-hover:text-gray-300'}`}>
                            <Heart className={`w-5 h-5 ${localLiked ? 'fill-current' : ''}`} />
                        </div>
                        <span className={`text-sm font-medium ${localLiked ? 'text-red-600 dark:text-red-400' : 'text-gray-500'}`}>
                            {localLikeCount}
                        </span>
                    </button>

                    <button
                        onClick={onCommentClick}
                        className="flex items-center gap-2 group transition-colors"
                    >
                        <div className="p-2 rounded-full text-gray-400 group-hover:bg-blue-50 dark:group-hover:bg-blue-900/20 group-hover:text-blue-500 transition-colors">
                            <MessageCircle className="w-5 h-5" />
                        </div>
                        <span className="text-sm font-medium text-gray-500 group-hover:text-blue-600 dark:group-hover:text-blue-400">
                            {post.commentCount}
                        </span>
                    </button>

                    <button
                        onClick={handleShare}
                        className="p-2 rounded-full text-gray-400 hover:bg-gray-100 dark:hover:bg-gray-800 hover:text-gray-600 dark:hover:text-gray-300 transition-colors"
                    >
                        <Share2 className="w-5 h-5" />
                    </button>
                </div>

                {post.imageUrl && (
                    <button
                        onClick={handleDownload}
                        className="p-2 rounded-full text-gray-400 hover:bg-gray-100 dark:hover:bg-gray-800 transition-colors"
                    >
                        <Download className="w-5 h-5" />
                    </button>
                )}
            </div>

            {/* Official Response Status - Cleaned up */}
            {isOfficialPost && (responsesEnabled || hasUserResponded || isVacancyFilled) && (
                <div className="px-5 py-3 bg-gray-50/50 dark:bg-white/5 border-t border-gray-100 dark:border-gray-800">
                    {!isAdmin && !hasUserResponded && !isVacancyFilled && responsesEnabled && (
                        <button
                            onClick={() => setShowResponseModal(true)}
                            className="w-full py-2.5 rounded-xl bg-primary-500 hover:bg-primary-600 text-white font-medium flex items-center justify-center gap-2 transition-all shadow-lg shadow-primary-500/20 active:scale-[0.98]"
                        >
                            <ClipboardCheck className="w-4 h-4" />
                            Submit Official Response
                        </button>
                    )}

                    {/* Status Indicators */}
                    <div className="flex flex-wrap gap-2 text-xs font-medium mt-1">
                        {isVacancyFilled && <span className="px-2 py-1 rounded-md bg-orange-100 dark:bg-orange-900/30 text-orange-600 dark:text-orange-400 flex items-center gap-1"><AlertCircle className="w-3 h-3" /> Vacancy Filled</span>}
                        {hasUserResponded && <span className="px-2 py-1 rounded-md bg-green-100 dark:bg-green-900/30 text-green-600 dark:text-green-400 flex items-center gap-1"><Check className="w-3 h-3" /> Response Submitted</span>}
                        {postResponses && postResponses.length > 0 && <span className="px-2 py-1 text-gray-500">{postResponses.length} responses</span>}
                    </div>

                    {isOfficialPost && postResponses && postResponses.length > 0 && (
                        <div className="mt-3">
                            <ResponseList responses={postResponses} />
                        </div>
                    )}
                </div>
            )}

            {/* Modals & Lightbox */}
            <AnimatePresence>
                {showLightbox && post.imageUrl && <ImageLightbox layoutId={`post-image-${post._id}`} src={post.imageUrl} alt="Full post" onClose={() => setShowLightbox(false)} />}
            </AnimatePresence>

            {isAdmin && <TaskAssignModal isOpen={showAssignModal} onClose={() => setShowAssignModal(false)} postId={post._id} />}
            {isOfficialPost && <ResponseModal isOpen={showResponseModal} onClose={() => setShowResponseModal(false)} postId={post._id} postContent={post.content} />}
        </motion.div>
    );
}
