import { useState } from 'react';
import { useQuery } from 'convex/react';
import { api } from '../../convex/_generated/api';
import { useAuth } from '@/hooks/useAuth';
import { Card, CardContent } from '@/components/ui/Card';
import { Input } from '@/components/ui/Input';
import { Badge } from '@/components/ui/Badge';
import { Search, Copy, Check } from 'lucide-react';
import { BackButton } from '@/components/ui/BackButton';

export function AdminMemberInfo() {
    const { user } = useAuth();
    const [searchQuery, setSearchQuery] = useState('');
    const [copiedId, setCopiedId] = useState<string | null>(null);

    // Only fetch if user is admin
    const allUsers = useQuery(
        api.users.getAllUsers,
        user && (user.role === 'admin' || user.role === 'super_admin')
            ? { adminId: user._id }
            : 'skip'
    );

    const filteredUsers = allUsers?.filter(u =>
        u.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
        u.email.toLowerCase().includes(searchQuery.toLowerCase()) ||
        (u.phone && u.phone.includes(searchQuery)) ||
        (u.profession && u.profession.toLowerCase().includes(searchQuery.toLowerCase()))
    ) || [];

    const copyToClipboard = (text: string, id: string) => {
        navigator.clipboard.writeText(text);
        setCopiedId(id);
        setTimeout(() => setCopiedId(null), 2000);
    };

    if (!user || (user.role !== 'admin' && user.role !== 'super_admin')) {
        return <div className="p-8 text-center text-red-500">Unauthorized Access</div>;
    }

    return (
        <div className="min-h-screen py-8 px-4">
            <div className="max-w-7xl mx-auto">
                <div className="flex items-center gap-4 mb-8">
                    <BackButton />
                    <div>
                        <h1 className="text-3xl font-bold text-gradient">Member Directory</h1>
                        <p className="text-light-muted dark:text-dark-muted">View member details, professions, and bank info</p>
                    </div>
                </div>

                <div className="mb-6">
                    <div className="relative max-w-md">
                        <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-light-muted dark:text-dark-muted" />
                        <Input
                            placeholder="Search by name, email, phone, or profession..."
                            value={searchQuery}
                            onChange={(e) => setSearchQuery(e.target.value)}
                            className="pl-10"
                        />
                    </div>
                </div>

                <div className="grid gap-6">
                    {filteredUsers.map((member) => (
                        <Card key={member._id} glass className="overflow-hidden">
                            <CardContent className="p-6">
                                <div className="flex flex-col md:flex-row gap-6">
                                    {/* User Info */}
                                    <div className="md:w-1/3 flex flex-col gap-4 border-b md:border-b-0 md:border-r border-light-border/20 dark:border-dark-border/20 pb-6 md:pb-0 md:pr-6">
                                        <div className="flex items-center gap-4">
                                            <div className="w-16 h-16 rounded-2xl bg-gradient-to-br from-primary-500 to-secondary-500 p-0.5">
                                                <div className="w-full h-full rounded-2xl bg-light-bg dark:bg-dark-bg flex items-center justify-center overflow-hidden">
                                                    {member.avatarUrl ? (
                                                        <img src={member.avatarUrl} alt={member.name} className="w-full h-full object-cover" />
                                                    ) : (
                                                        <span className="text-2xl font-bold">{member.name[0]}</span>
                                                    )}
                                                </div>
                                            </div>
                                            <div>
                                                <h3 className="text-xl font-bold">{member.name}</h3>
                                                <Badge variant={member.role === 'board_member' ? 'secondary' : 'default'} className="mt-1">
                                                    {member.role.replace('_', ' ').toUpperCase()}
                                                </Badge>
                                            </div>
                                        </div>

                                        <div className="space-y-2">
                                            <div>
                                                <span className="text-xs font-bold text-light-muted dark:text-dark-muted uppercase tracking-wider">Profession</span>
                                                <p className="font-medium">{member.profession || 'Not specified'}</p>
                                            </div>
                                            <div>
                                                <span className="text-xs font-bold text-light-muted dark:text-dark-muted uppercase tracking-wider">Email</span>
                                                <p className="font-medium break-all">{member.email}</p>
                                            </div>
                                            <div>
                                                <span className="text-xs font-bold text-light-muted dark:text-dark-muted uppercase tracking-wider">Phone / Network</span>
                                                <div className="flex items-center gap-2">
                                                    <p className="font-medium">{member.phone || 'N/A'}</p>
                                                    {member.network && <Badge variant="outline" className="text-xs py-0 h-5">{member.network}</Badge>}
                                                </div>
                                            </div>
                                            <div className="flex items-center gap-2">
                                                <span className="text-sm font-medium">WhatsApp:</span>
                                                {member.whatsappVerified ? (
                                                    <Badge variant="success" className="bg-green-500/10 text-green-500 border-green-500/20">Verified</Badge>
                                                ) : (
                                                    <Badge variant="secondary" className="bg-gray-500/10 text-gray-500 border-gray-500/20">Unverified</Badge>
                                                )}
                                            </div>
                                        </div>
                                    </div>

                                    {/* Bank Details */}
                                    <div className="flex-1 md:pl-6">
                                        <h4 className="text-lg font-bold mb-4 flex items-center gap-2">
                                            <span className="w-1 h-6 bg-accent-500 rounded-full"></span>
                                            Bank Details
                                        </h4>
                                        {member.bankDetails ? (
                                            <div className="grid sm:grid-cols-2 gap-4">
                                                <div className="p-4 rounded-xl bg-light-bg/50 dark:bg-dark-bg/50 border border-light-border/20 dark:border-dark-border/20">
                                                    <span className="text-xs text-light-muted dark:text-dark-muted uppercase">Bank Name</span>
                                                    <p className="font-bold text-lg">{member.bankDetails.bankName}</p>
                                                </div>
                                                <div className="p-4 rounded-xl bg-light-bg/50 dark:bg-dark-bg/50 border border-light-border/20 dark:border-dark-border/20 relative group">
                                                    <span className="text-xs text-light-muted dark:text-dark-muted uppercase">Account Number</span>
                                                    <div className="flex items-center justify-between">
                                                        <p className="font-mono font-bold text-lg tracking-wider">{member.bankDetails.accountNumber}</p>
                                                        <button
                                                            onClick={() => copyToClipboard(member.bankDetails!.accountNumber, `acc-${member._id}`)}
                                                            className="p-1.5 rounded-lg hover:bg-light-card dark:hover:bg-dark-card transition-colors"
                                                            title="Copy Account Number"
                                                        >
                                                            {copiedId === `acc-${member._id}` ? <Check className="w-4 h-4 text-green-500" /> : <Copy className="w-4 h-4 text-light-muted dark:text-dark-muted" />}
                                                        </button>
                                                    </div>
                                                </div>
                                                <div className="sm:col-span-2 p-4 rounded-xl bg-light-bg/50 dark:bg-dark-bg/50 border border-light-border/20 dark:border-dark-border/20 relative group">
                                                    <span className="text-xs text-light-muted dark:text-dark-muted uppercase">Account Name</span>
                                                    <div className="flex items-center justify-between">
                                                        <p className="font-bold text-lg">{member.bankDetails.accountName}</p>
                                                        <button
                                                            onClick={() => copyToClipboard(member.bankDetails!.accountName, `name-${member._id}`)}
                                                            className="p-1.5 rounded-lg hover:bg-light-card dark:hover:bg-dark-card transition-colors"
                                                            title="Copy Account Name"
                                                        >
                                                            {copiedId === `name-${member._id}` ? <Check className="w-4 h-4 text-green-500" /> : <Copy className="w-4 h-4 text-light-muted dark:text-dark-muted" />}
                                                        </button>
                                                    </div>
                                                </div>
                                            </div>
                                        ) : (
                                            <div className="h-full flex flex-col items-center justify-center text-center p-8 bg-light-bg/30 dark:bg-dark-bg/30 rounded-xl border border-dashed border-light-border/30 dark:border-dark-border/30">
                                                <p className="text-light-muted dark:text-dark-muted mb-2">No bank details provided yet.</p>
                                                <Badge variant="outline">Pending Update</Badge>
                                            </div>
                                        )}
                                    </div>
                                </div>
                            </CardContent>
                        </Card>
                    ))}

                    {filteredUsers.length === 0 && (
                        <div className="text-center py-12">
                            <p className="text-xl text-light-muted dark:text-dark-muted">No members found matching your search.</p>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
}
