import React, { useState } from 'react';
import { useQuery, useMutation } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { useAuth } from '@/hooks/useAuth';
import { Avatar } from '@/components/ui/Avatar';
import { Badge } from '@/components/ui/Badge';
import { Send, Shield, User as UserIcon } from 'lucide-react';
import { formatDistanceToNow } from 'date-fns';
import { Id } from '../../../convex/_generated/dataModel';
import { CommentSkeleton } from '../skeletons/CommentSkeleton';

interface CommentSectionProps {
    postId: Id<"posts">;
}

export function CommentSection({ postId }: CommentSectionProps) {
    const { user } = useAuth();
    const [commentText, setCommentText] = useState('');
    const [isSubmitting, setIsSubmitting] = useState(false);
    const [replyingTo, setReplyingTo] = useState<Id<"comments"> | null>(null);

    const comments = useQuery(api.comments.getComments, { postId });
    const commentsEnabled = useQuery(api.comments.getCommentSettings, {});
    const createComment = useMutation(api.comments.createComment);

    const handleSubmit = async (e: React.FormEvent, parentId?: Id<"comments">) => {
        e.preventDefault();
        const content = parentId ? commentText : commentText; // In a real app we'd have separate states

        if (!user || !content.trim() || isSubmitting) return;

        setIsSubmitting(true);
        try {
            await createComment({
                postId,
                authorId: user._id,
                content: content.trim(),
                parentId,
            });
            setCommentText('');
            setReplyingTo(null);
        } catch (error) {
            console.error('Failed to create comment:', error);
            alert(error instanceof Error ? error.message : 'Failed to post comment');
        } finally {
            setIsSubmitting(false);
        }
    };

    // Helper to organize comments into threads
    const getThreadedComments = () => {
        if (!comments) return [];
        const threads: any[] = [];
        const map = new Map();

        // First pass: map all comments
        comments.forEach(c => {
            map.set(c._id, { ...c, replies: [] });
        });

        // Second pass: organize into tree
        comments.forEach(c => {
            if (c.parentId) {
                const parent = map.get(c.parentId);
                if (parent) {
                    parent.replies.push(map.get(c._id));
                }
            } else {
                threads.push(map.get(c._id));
            }
        });

        return threads;
    };

    const threadedComments = getThreadedComments();

    const CommentItem = ({ comment, isReply = false }: { comment: any, isReply?: boolean }) => (
        <div className={`flex gap-3 ${isReply ? 'ml-10 mt-2' : ''}`}>
            <Avatar
                src={comment.author?.avatarUrl}
                alt={comment.author?.name || 'User'}
                size="sm"
            />
            <div className="flex-1">
                <div className="bg-light-card dark:bg-dark-card rounded-lg p-3">
                    <div className="flex items-center gap-2 mb-1">
                        <span className="font-semibold text-sm text-light-text dark:text-dark-text">
                            {comment.author?.name || 'Unknown User'}
                        </span>
                        {comment.author?.role === 'admin' ? (
                            <Badge variant="primary" className="text-xs flex items-center gap-1">
                                <Shield className="w-3 h-3" />
                                Admin
                            </Badge>
                        ) : (
                            <Badge variant="default" className="text-xs flex items-center gap-1">
                                <UserIcon className="w-3 h-3" />
                                Member
                            </Badge>
                        )}
                        <span className="text-xs text-light-muted dark:text-dark-muted">
                            {formatDistanceToNow(comment.createdAt, { addSuffix: true })}
                        </span>
                    </div>
                    <p className="text-sm text-light-text dark:text-dark-text">
                        {comment.content}
                    </p>
                </div>

                {/* Reply Button */}
                {!isReply && user && (
                    <button
                        onClick={() => setReplyingTo(replyingTo === comment._id ? null : comment._id)}
                        className="text-xs text-light-muted dark:text-dark-muted hover:text-primary-500 mt-1 ml-2 font-medium"
                    >
                        Reply
                    </button>
                )}

                {/* Reply Input */}
                {replyingTo === comment._id && (
                    <form
                        onSubmit={(e) => handleSubmit(e, comment._id)}
                        className="mt-2 flex gap-2"
                    >
                        <input
                            type="text"
                            value={commentText}
                            onChange={(e) => setCommentText(e.target.value)}
                            placeholder={`Reply to ${comment.author?.name}...`}
                            className="flex-1 px-3 py-1.5 text-sm rounded-lg border border-light-border dark:border-dark-border bg-light-card dark:bg-dark-card text-light-text dark:text-dark-text focus:ring-2 focus:ring-primary-500"
                            autoFocus
                        />
                        <button
                            type="submit"
                            disabled={!commentText.trim() || isSubmitting}
                            className="px-3 py-1.5 rounded-lg bg-primary-500 hover:bg-primary-600 text-white text-xs font-medium"
                        >
                            Reply
                        </button>
                    </form>
                )}

                {/* Nested Replies */}
                {comment.replies?.length > 0 && (
                    <div className="space-y-2 mt-2">
                        {comment.replies.map((reply: any) => (
                            <CommentItem key={reply._id} comment={reply} isReply={true} />
                        ))}
                    </div>
                )}
            </div>
        </div>
    );

    if (commentsEnabled === undefined || commentsEnabled === false) {
        return (
            <div className="mt-4 p-4 rounded-lg bg-light-card dark:bg-dark-card text-center">
                <p className="text-sm text-light-muted dark:text-dark-muted">
                    Comments are currently disabled
                </p>
            </div>
        );
    }

    return (
        <div className="mt-4 pt-4 border-t border-light-border dark:border-dark-border space-y-4">
            {/* Main Comment Input */}
            {user && (
                <form onSubmit={(e) => handleSubmit(e)} className="flex items-start gap-2 md:gap-3">
                    <div className="mt-1 flex-shrink-0">
                        <Avatar
                            src={user.avatarUrl}
                            alt={user.name}
                            size="sm"
                        />
                    </div>
                    <div className="flex-1 min-w-0 relative">
                        <textarea
                            value={replyingTo ? '' : commentText}
                            onChange={(e) => {
                                setReplyingTo(null);
                                setCommentText(e.target.value);
                            }}
                            placeholder="Write a comment..."
                            className="w-full px-4 py-2.5 pr-12 rounded-2xl border border-light-border dark:border-dark-border bg-light-card dark:bg-dark-card text-light-text dark:text-dark-text focus:ring-2 focus:ring-primary-500 focus:border-transparent resize-none overflow-hidden min-h-[44px] max-h-32 text-sm md:text-base transition-all"
                            disabled={isSubmitting}
                            rows={1}
                            onInput={(e) => {
                                const target = e.target as HTMLTextAreaElement;
                                target.style.height = 'auto';
                                target.style.height = `${target.scrollHeight}px`;
                            }}
                        />
                        <button
                            type="submit"
                            disabled={(!replyingTo && !commentText.trim()) || isSubmitting}
                            className="absolute right-1.5 bottom-1.5 p-2 rounded-xl bg-primary-500 hover:bg-primary-600 text-white transition-all disabled:opacity-30 disabled:cursor-not-allowed flex items-center justify-center shadow-lg shadow-primary-500/20 active:scale-95"
                        >
                            <Send className="w-4 h-4 md:w-5 md:h-5" />
                        </button>
                    </div>
                </form>
            )}

            {/* Comments List */}
            {!comments ? (
                <div className="space-y-4">
                    <CommentSkeleton />
                    <CommentSkeleton />
                    <CommentSkeleton />
                </div>
            ) : threadedComments.length > 0 ? (
                <div className="space-y-4">
                    {threadedComments.map((comment) => (
                        <CommentItem key={comment._id} comment={comment} />
                    ))}
                </div>
            ) : (
                <div className="text-center py-8 text-light-muted dark:text-dark-muted">
                    <p>No comments yet. Be the first to share your thoughts!</p>
                </div>
            )}
        </div>
    );
}
