import { Link, useLocation } from 'react-router-dom';
import { Sparkles, BookOpen, Lightbulb, Bell, ClipboardList, MessageSquare } from 'lucide-react';
import { useQuery } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { MemberTicker } from '@/components/users/MemberTicker';

export function DesktopNav() {
    const location = useLocation();

    const unreadCount = useQuery(api.notifications.getUnreadCount) || 0;

    const navItems = [
        { path: '/feed', label: 'Feed', icon: Sparkles },
        { path: '/ancient-culture', label: 'Ancient Culture', icon: BookOpen },
        { path: '/wisdom-now', label: 'Wisdom of The Now', icon: Lightbulb },
        { path: '/tasks', label: 'My Tasks', icon: ClipboardList },
        { path: '/my-responses', label: 'My Responses', icon: MessageSquare },
        { path: '/notifications', label: 'Notifications', icon: Bell, badge: unreadCount },
    ];

    return (
        <nav className="hidden md:block sticky top-20 z-40 bg-light-bg/80 dark:bg-dark-bg/80 backdrop-blur-md border-b border-light-border dark:border-dark-border">
            <div className="w-full px-4 sm:px-6 lg:px-8">
                <div className="flex items-center justify-between py-3 flex-nowrap overflow-x-auto no-scrollbar gap-4">
                    {/* Items */}
                    <div className="flex items-center gap-1 xl:gap-2 flex-nowrap">
                        {navItems.map((item) => {
                            const Icon = item.icon;
                            const isActive = location.pathname === item.path;

                            return (
                                <Link
                                    key={item.path}
                                    to={item.path}
                                    className={`
                                        flex items-center gap-2 px-3 xl:px-4 py-2 rounded-lg font-medium transition-all relative whitespace-nowrap
                                        ${isActive
                                            ? 'bg-primary-500 text-white shadow-lg shadow-primary-500/30'
                                            : 'text-light-text dark:text-dark-text hover:bg-light-card dark:hover:bg-dark-card'
                                        }
                                    `}
                                >
                                    <div className="relative">
                                        <Icon className="w-5 h-5 flex-shrink-0" />
                                        {item.badge !== undefined && item.badge > 0 && (
                                            <span className="absolute -top-2 -right-2 bg-red-500 text-white text-[10px] font-bold px-1.5 py-0.5 rounded-full min-w-[18px] text-center border-2 border-light-bg dark:border-dark-bg">
                                                {item.badge > 99 ? '99+' : item.badge}
                                            </span>
                                        )}
                                    </div>
                                    <span className="text-sm xl:text-base">{item.label}</span>
                                </Link>
                            );
                        })}
                    </div>

                    {/* Ticker */}
                    <div className="hidden 2xl:block flex-shrink-0 ml-4">
                        <MemberTicker />
                    </div>
                </div>
            </div>
        </nav>
    );
}
