import { Link, useNavigate, useLocation } from 'react-router-dom';
import { useEffect } from 'react';
import { motion } from 'framer-motion';
import { Button } from '@/components/ui/Button';
import { Card } from '@/components/ui/Card';
import { Badge } from '@/components/ui/Badge';
import { BookOpen, Users, Trophy, Sparkles, ArrowRight } from 'lucide-react';
import { useAuth } from '@/hooks/useAuth';

export function Home() {
    const { user } = useAuth();
    const navigate = useNavigate();
    const location = useLocation();

    useEffect(() => {
        // If user is logged in and didn't manually navigate to home, redirect to feed
        if (user && !(location.state as any)?.manual) {
            navigate('/feed', { replace: true });
        }
    }, [user, navigate, location]);

    const features = [
        {
            icon: BookOpen,
            title: 'Ancient Culture',
            subtitle: 'Prophetic Wisdom',
            description: 'Dive deep into prophetic wisdom and systems from the Old Testament. Understand the foundations of spiritual authority.',
            image: '/assets/images/ancient-culture.png',
            color: 'from-primary-500 to-primary-700'
        },
        {
            icon: Sparkles,
            title: 'Wisdom of The Now',
            subtitle: 'Modern Insights',
            description: 'Explore spiritual identity and insights from the New Testament. Discover your place in the modern spiritual landscape.',
            image: '/assets/images/wisdom-now.png',
            color: 'from-secondary-500 to-secondary-700'
        }
    ];

    const benefits = [
        {
            icon: Users,
            title: 'Vibrant Community',
            description: 'Connect with a global network of believers seeking deeper truth.'
        },
        {
            icon: Trophy,
            title: 'Real Impact',
            description: 'Earn recognition and rewards for active participation and growth.'
        },
        {
            icon: ArrowRight,
            title: 'Guided Path',
            description: 'Structured learning paths designed for progressive spiritual maturity.'
        }
    ];

    return (
        <div className="min-h-screen bg-light-bg dark:bg-dark-bg selection:bg-primary-500/30 relative overflow-hidden">
            {/* Animated Background Mesh */}
            <div className="absolute inset-0 z-0 pointer-events-none">
                <motion.div
                    animate={{
                        scale: [1, 1.2, 1],
                        rotate: [0, 90, 0],
                        opacity: [0.3, 0.5, 0.3]
                    }}
                    transition={{ duration: 20, repeat: Infinity, ease: "linear" }}
                    className="absolute -top-[20%] -right-[10%] w-[60%] h-[60%] bg-primary-500/20 blur-[120px] rounded-full"
                />
                <motion.div
                    animate={{
                        scale: [1.2, 1, 1.2],
                        rotate: [0, -90, 0],
                        opacity: [0.2, 0.4, 0.2]
                    }}
                    transition={{ duration: 25, repeat: Infinity, ease: "linear" }}
                    className="absolute top-[20%] -left-[10%] w-[50%] h-[50%] bg-secondary-500/20 blur-[100px] rounded-full"
                />
            </div>

            {/* Hero Section */}
            <section className="relative min-h-[90vh] flex items-center pt-24 md:pt-0">
                <div className="container mx-auto px-4 sm:px-6 lg:px-8 relative z-10">
                    <div className="grid lg:grid-cols-2 gap-12 lg:gap-20 items-center">
                        <motion.div
                            initial={{ opacity: 0, y: 30 }}
                            animate={{ opacity: 1, y: 0 }}
                            transition={{ duration: 1 }}
                            className="text-center lg:text-left"
                        >
                            <Badge variant="primary" className="mb-6 px-4 py-1.5 text-[10px] md:text-sm uppercase tracking-widest font-black">
                                The Pinnacle of Spiritual Growth
                            </Badge>
                            <h1 className="text-4xl sm:text-5xl md:text-8xl font-black font-display leading-[1.1] md:leading-tight mb-6">
                                Elevate Your <br />
                                <span className="text-gradient">Spirituality</span>
                            </h1>

                            {/* Desktop CTA Text */}
                            <p className="hidden lg:block text-xl md:text-2xl text-light-muted dark:text-dark-muted mb-10 leading-relaxed max-w-2xl">
                                Join a professional community dedicated to exploring ancient prophetic wisdom and modern spiritual insights. Experience growth like never before.
                            </p>

                            <div className="hidden lg:flex flex-col sm:flex-row items-center gap-6">
                                {user ? (
                                    <Link to="/feed">
                                        <Button size="lg" className="h-14 px-10 text-lg group shadow-2xl shadow-primary-500/20 rounded-2xl">
                                            Enter the Feed
                                            <ArrowRight className="w-5 h-5 ml-2 group-hover:translate-x-1 transition-transform" />
                                        </Button>
                                    </Link>
                                ) : (
                                    <>
                                        <Link to="/register">
                                            <Button size="lg" className="h-14 px-10 text-lg shadow-2xl shadow-primary-500/20 rounded-2xl">
                                                Start Your Journey
                                            </Button>
                                        </Link>
                                        <Link to="/login">
                                            <Button size="lg" variant="outline" className="h-14 px-10 text-lg backdrop-blur-md rounded-2xl border-light-border/50 dark:border-dark-border/50">
                                                Sign In
                                            </Button>
                                        </Link>
                                    </>
                                )}
                            </div>
                        </motion.div>

                        {/* Hero Image Montage */}
                        <div className="relative mt-12 lg:mt-0">
                            <motion.div
                                initial={{ opacity: 0, scale: 0.9, rotate: -5 }}
                                animate={{ opacity: 1, scale: 1, rotate: 0 }}
                                transition={{ duration: 1.2, ease: "easeOut" }}
                                className="grid grid-cols-2 gap-3 md:gap-6"
                            >
                                <div className="space-y-3 md:space-y-6 pt-8 md:pt-16">
                                    <motion.div
                                        whileHover={{ scale: 1.05, rotate: -2 }}
                                        className="rounded-2xl md:rounded-[2.5rem] overflow-hidden shadow-2xl border-2 md:border-4 border-white/10 relative group"
                                    >
                                        <div className="absolute inset-0 bg-gradient-to-t from-black/60 to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-300" />
                                        <img src="https://ik.imagekit.io/h3qzmviwv/baptizo/posts/images/Image_fx%20(2).png" alt="Spiritual Insight" className="w-full h-full object-cover transition-transform duration-700 group-hover:scale-110" />
                                    </motion.div>
                                    <motion.div
                                        whileHover={{ scale: 1.05, rotate: 2 }}
                                        className="rounded-2xl md:rounded-[2.5rem] overflow-hidden shadow-2xl border-2 md:border-4 border-white/10 relative group"
                                    >
                                        <div className="absolute inset-0 bg-gradient-to-t from-black/60 to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-300" />
                                        <img src="https://ik.imagekit.io/h3qzmviwv/baptizo/posts/images/Image_fx%20(5).png" alt="Ancient Wisdom" className="w-full h-full object-cover transition-transform duration-700 group-hover:scale-110" />
                                    </motion.div>
                                </div>
                                <div className="flex items-center">
                                    <motion.div
                                        whileHover={{ scale: 1.05, rotate: 2 }}
                                        className="rounded-2xl md:rounded-[2.5rem] overflow-hidden shadow-2xl border-2 md:border-4 border-white/10 relative group h-[80%] md:h-[90%]"
                                    >
                                        <div className="absolute inset-0 bg-gradient-to-t from-black/60 to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-300" />
                                        <img src="https://ik.imagekit.io/h3qzmviwv/baptizo/posts/images/Image_fx%20(6).png" alt="Community Growth" className="w-full h-full object-cover transition-transform duration-700 group-hover:scale-110" />
                                    </motion.div>
                                </div>
                            </motion.div>

                            {/* Mobile CTA (Below Images) */}
                            <motion.div
                                initial={{ opacity: 0, y: 20 }}
                                animate={{ opacity: 1, y: 0 }}
                                transition={{ delay: 0.5 }}
                                className="lg:hidden mt-12 text-center"
                            >
                                <p className="text-base sm:text-lg text-light-muted dark:text-dark-muted mb-8 leading-relaxed px-2 font-medium">
                                    Join a professional community dedicated to exploring ancient prophetic wisdom and modern spiritual insights. Experience growth like never before.
                                </p>
                                <div className="flex flex-col gap-4 px-6 sm:px-20">
                                    {user ? (
                                        <Link to="/feed">
                                            <Button size="lg" className="h-14 w-full text-lg shadow-2xl shadow-primary-500/20 rounded-2xl">
                                                Enter the Feed
                                                <ArrowRight className="w-5 h-5 ml-2" />
                                            </Button>
                                        </Link>
                                    ) : (
                                        <>
                                            <Link to="/register">
                                                <Button size="lg" className="h-14 w-full text-lg shadow-2xl shadow-primary-500/20 rounded-2xl">
                                                    Start Your Journey
                                                </Button>
                                            </Link>
                                            <Link to="/login">
                                                <Button size="lg" variant="outline" className="h-14 w-full text-lg backdrop-blur-md rounded-2xl border-light-border/50 dark:border-dark-border/50">
                                                    Sign In
                                                </Button>
                                            </Link>
                                        </>
                                    )}
                                </div>
                            </motion.div>
                        </div>
                    </div>
                </div>

                {/* Scroll Indicator */}
                <motion.div
                    animate={{ y: [0, 10, 0] }}
                    transition={{ repeat: Infinity, duration: 2 }}
                    className="absolute bottom-10 left-1/2 -translate-x-1/2 hidden md:block"
                >
                    <div className="w-6 h-10 rounded-full border-2 border-primary-500/30 flex justify-center p-1 backdrop-blur-sm">
                        <motion.div
                            animate={{ opacity: [0, 1, 0], y: [0, 15, 0] }}
                            transition={{ duration: 1.5, repeat: Infinity }}
                            className="w-1.5 h-1.5 rounded-full bg-primary-500"
                        />
                    </div>
                </motion.div>
            </section>

            {/* Core Pillars Section */}
            <section className="py-24 md:py-32 relative">
                <div className="container mx-auto px-4">
                    <div className="text-center mb-16 md:mb-24">
                        <motion.div
                            initial={{ opacity: 0, scale: 0.9 }}
                            whileInView={{ opacity: 1, scale: 1 }}
                            viewport={{ once: true }}
                        >
                            <h2 className="text-3xl md:text-6xl font-black font-display mb-6 tracking-tight">Explore Our Core Pillars</h2>
                            <div className="w-24 h-1.5 bg-gradient-to-r from-primary-500 to-secondary-500 mx-auto rounded-full mb-8" />
                            <p className="text-lg md:text-xl text-light-muted dark:text-dark-muted max-w-2xl mx-auto font-medium">
                                We bridge the gap between ancient foundations and contemporary applications.
                            </p>
                        </motion.div>
                    </div>

                    <div className="grid lg:grid-cols-2 gap-8 md:gap-12">
                        {features.map((feature, index) => (
                            <motion.div
                                key={feature.title}
                                initial={{ opacity: 0, x: index % 2 === 0 ? -40 : 40 }}
                                whileInView={{ opacity: 1, x: 0 }}
                                transition={{ delay: index * 0.2, duration: 0.8 }}
                                viewport={{ once: true }}
                                className="group relative"
                            >
                                <div className={`absolute inset-0 bg-gradient-to-br ${feature.color} opacity-0 group-hover:opacity-10 transition-opacity rounded-[2.5rem] blur-3xl -z-10`} />
                                <Card glass className="overflow-hidden border border-white/10 dark:border-white/5 shadow-2xl hover:shadow-primary-500/10 transition-all duration-500 h-full flex flex-col rounded-[2rem] md:rounded-[2.5rem]">
                                    <div className="relative h-64 md:h-80 overflow-hidden">
                                        <img
                                            src={feature.image}
                                            alt={feature.title}
                                            className="w-full h-full object-cover group-hover:scale-110 transition-transform duration-1000"
                                        />
                                        <div className="absolute inset-0 bg-gradient-to-t from-dark-bg/80 via-transparent to-transparent opacity-60" />
                                        <div className="absolute top-6 left-6">
                                            <Badge className="bg-white/10 backdrop-blur-xl border-white/20 text-white px-4 py-2 rounded-xl font-bold uppercase tracking-wider text-[10px]">
                                                {feature.subtitle}
                                            </Badge>
                                        </div>
                                    </div>
                                    <div className="p-8 md:p-12 flex-1 flex flex-col">
                                        <div className={`w-16 h-16 rounded-2xl bg-gradient-to-br ${feature.color} flex items-center justify-center mb-8 shadow-xl transform group-hover:rotate-6 transition-transform`}>
                                            <feature.icon className="w-8 h-8 text-white" />
                                        </div>
                                        <h3 className="text-3xl md:text-4xl font-black mb-4 tracking-tight">{feature.title}</h3>
                                        <p className="text-light-muted dark:text-dark-muted text-lg md:text-xl mb-10 leading-relaxed font-medium">
                                            {feature.description}
                                        </p>
                                        <Link to="/register" className="mt-auto group/btn inline-flex items-center gap-3 text-primary-500 font-black text-lg">
                                            Explore Deeply <ArrowRight className="w-6 h-6 transform group-hover/btn:translate-x-2 transition-transform" />
                                        </Link>
                                    </div>
                                </Card>
                            </motion.div>
                        ))}
                    </div>
                </div>
            </section>

            {/* Benefits Grid */}
            <section className="py-24 relative overflow-hidden">
                <div className="absolute inset-0 bg-dark-bg/5 dark:bg-light-bg/5 backdrop-blur-3xl" />
                <div className="container mx-auto px-4 relative z-10">
                    <div className="grid md:grid-cols-3 gap-12 lg:gap-20">
                        {benefits.map((benefit, index) => (
                            <motion.div
                                key={benefit.title}
                                initial={{ opacity: 0, y: 30 }}
                                whileInView={{ opacity: 1, y: 0 }}
                                transition={{ delay: index * 0.2 }}
                                viewport={{ once: true }}
                                className="group"
                            >
                                <div className="text-center md:text-left">
                                    <div className="inline-flex items-center justify-center w-16 h-16 rounded-2xl bg-primary-100 dark:bg-primary-900/20 text-primary-500 mb-8 transform group-hover:scale-110 group-hover:rotate-3 transition-all shadow-lg">
                                        <benefit.icon className="w-8 h-8" />
                                    </div>
                                    <h4 className="text-2xl md:text-3xl font-black mb-4 tracking-tight">{benefit.title}</h4>
                                    <p className="text-light-muted dark:text-dark-muted text-lg leading-relaxed font-medium">
                                        {benefit.description}
                                    </p>
                                </div>
                            </motion.div>
                        ))}
                    </div>
                </div>
            </section>

            {/* Final CTA */}
            <section className="py-32 md:py-48 relative overflow-hidden">
                <div className="absolute inset-0 z-0 opacity-20">
                    <motion.div
                        animate={{ scale: [1, 1.5, 1], opacity: [0.3, 0.6, 0.3] }}
                        transition={{ duration: 10, repeat: Infinity }}
                        className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-[80%] h-[80%] bg-gradient-to-br from-primary-500 to-secondary-500 rounded-full blur-[150px]"
                    />
                </div>

                <div className="container mx-auto px-4 relative z-10 text-center">
                    <motion.div
                        initial={{ opacity: 0, y: 40 }}
                        whileInView={{ opacity: 1, y: 0 }}
                        viewport={{ once: true }}
                        className="glass relative z-10 rounded-[3rem] p-12 md:p-24 border border-white/10 dark:border-white/5 shadow-[0_32px_120px_-20px_rgba(0,0,0,0.3)] max-w-5xl mx-auto"
                    >
                        <Badge variant="primary" className="mb-8 px-6 py-2 text-xs md:text-sm uppercase tracking-[0.2em] font-black">
                            Unleash Your Potential
                        </Badge>
                        <h2 className="text-4xl md:text-7xl font-black font-display mb-10 tracking-tight leading-[1.1]">
                            Ready for a <br />
                            <span className="text-gradient">Higher Experience?</span>
                        </h2>
                        <p className="text-lg md:text-2xl text-light-muted dark:text-dark-muted mb-12 md:mb-16 max-w-2xl mx-auto leading-relaxed font-medium">
                            Join thousands of believers who have unlocked deeper spiritual dimensions through Baptizo.
                        </p>
                        {!user && (
                            <Link to="/register">
                                <Button size="lg" className="h-16 md:h-20 px-12 md:px-16 text-xl md:text-2xl font-black shadow-2xl shadow-primary-500/30 rounded-2xl md:rounded-3xl hover:scale-105 active:scale-95 transition-all">
                                    Create Your Free Account
                                </Button>
                            </Link>
                        )}
                    </motion.div>
                </div>
            </section>
        </div>
    );
}
