import { motion } from 'framer-motion';
import { useQuery } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { useAuth } from '@/hooks/useAuth';
import { Navigate, Link } from 'react-router-dom';
import { CheckCircle2, Clock, AlertCircle, BookOpen, Trophy, TrendingUp } from 'lucide-react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/Card';
import { Badge } from '@/components/ui/Badge';

export function BoardDashboard() {
    const { user, isLoading } = useAuth();

    const boardStats = useQuery(
        api.boardTasks.getBoardStats,
        user && (user.role === 'board_member' || user.role === 'admin' || user.role === 'super_admin')
            ? { userId: user._id }
            : 'skip'
    );

    const trainingStats = useQuery(
        api.training.getTrainingStats,
        user ? { userId: user._id } : 'skip'
    );

    const myTasks = useQuery(
        api.boardTasks.getMyTasks,
        user ? { userId: user._id } : 'skip'
    );

    if (isLoading) {
        return (
            <div className="flex items-center justify-center min-h-screen">
                <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary-500" />
            </div>
        );
    }

    if (!user) {
        return <Navigate to="/login" replace />;
    }

    if (user.role !== 'board_member' && user.role !== 'admin' && user.role !== 'super_admin') {
        return <Navigate to="/feed" replace />;
    }

    const upcomingTasks = myTasks
        ?.filter(t => t.status !== 'completed' && t.status !== 'cancelled')
        .sort((a, b) => (a.dueDate || Infinity) - (b.dueDate || Infinity))
        .slice(0, 3) || [];

    return (
        <div className="min-h-screen py-8">
            <div className="container mx-auto px-4 sm:px-6 lg:px-8 max-w-7xl">
                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    className="mb-8"
                >
                    <h1 className="text-3xl md:text-4xl font-bold text-gradient mb-2">
                        Board Hub
                    </h1>
                    <p className="text-light-muted dark:text-dark-muted">
                        Welcome, {user.name}! Your leadership center for Photizo.
                    </p>
                </motion.div>

                <div className="grid grid-cols-2 lg:grid-cols-4 gap-4 mb-8">
                    <Card glass>
                        <CardContent className="p-6">
                            <div className="flex items-center justify-between mb-2">
                                <CheckCircle2 className="w-8 h-8 text-primary-500" />
                                <Badge variant="primary">{boardStats?.myTasks || 0}</Badge>
                            </div>
                            <h3 className="text-sm font-medium text-light-muted dark:text-dark-muted">My Tasks</h3>
                            <p className="text-xs text-light-muted dark:text-dark-muted mt-1">
                                {boardStats?.myCompleted || 0} completed
                            </p>
                        </CardContent>
                    </Card>

                    <Card glass>
                        <CardContent className="p-6">
                            <div className="flex items-center justify-between mb-2">
                                <Clock className="w-8 h-8 text-accent-500" />
                                <Badge variant="default">{boardStats?.myInProgress || 0}</Badge>
                            </div>
                            <h3 className="text-sm font-medium text-light-muted dark:text-dark-muted">In Progress</h3>
                            <p className="text-xs text-light-muted dark:text-dark-muted mt-1">Active tasks</p>
                        </CardContent>
                    </Card>

                    <Card glass>
                        <CardContent className="p-6">
                            <div className="flex items-center justify-between mb-2">
                                <AlertCircle className="w-8 h-8 text-red-500" />
                                <Badge variant="default">{boardStats?.overdueTasks || 0}</Badge>
                            </div>
                            <h3 className="text-sm font-medium text-light-muted dark:text-dark-muted">Overdue</h3>
                            <p className="text-xs text-light-muted dark:text-dark-muted mt-1">Need attention</p>
                        </CardContent>
                    </Card>

                    <Card glass>
                        <CardContent className="p-6">
                            <div className="flex items-center justify-between mb-2">
                                <BookOpen className="w-8 h-8 text-secondary-500" />
                                <Badge variant="secondary">{trainingStats?.completionPercentage || 0}%</Badge>
                            </div>
                            <h3 className="text-sm font-medium text-light-muted dark:text-dark-muted">Training</h3>
                            <p className="text-xs text-light-muted dark:text-dark-muted mt-1">
                                {trainingStats?.completed || 0}/{trainingStats?.totalModules || 0} modules
                            </p>
                        </CardContent>
                    </Card>
                </div>

                <div className="grid lg:grid-cols-3 gap-8">
                    <div className="lg:col-span-2">
                        <Card glass>
                            <CardHeader>
                                <CardTitle className="flex items-center justify-between">
                                    <span>Upcoming Tasks</span>
                                    <Link to="/board/tasks" className="text-sm text-primary-500 hover:text-primary-600 font-normal">
                                        View All 
                                    </Link>
                                </CardTitle>
                            </CardHeader>
                            <CardContent>
                                {upcomingTasks.length === 0 ? (
                                    <div className="text-center py-12">
                                        <Trophy className="w-12 h-12 mx-auto mb-4 text-light-muted dark:text-dark-muted opacity-50" />
                                        <p className="text-light-muted dark:text-dark-muted">All caught up! No pending tasks.</p>
                                    </div>
                                ) : (
                                    <div className="space-y-4">
                                        {upcomingTasks.map((task) => (
                                            <Link key={task._id} to="/board/tasks" className="block">
                                                <motion.div
                                                    whileHover={{ scale: 1.02 }}
                                                    className="p-4 rounded-lg bg-light-bg dark:bg-dark-bg border border-light-border dark:border-dark-border hover:border-primary-500 transition-all"
                                                >
                                                    <div className="flex items-start justify-between mb-2">
                                                        <h4 className="font-semibold text-light-text dark:text-dark-text">{task.title}</h4>
                                                        <Badge
                                                            variant={task.priority === 'urgent' || task.priority === 'high' ? 'primary' : 'default'}
                                                            className="text-xs"
                                                        >
                                                            {task.priority}
                                                        </Badge>
                                                    </div>
                                                    <p className="text-sm text-light-muted dark:text-dark-muted mb-3 line-clamp-2">{task.description}</p>
                                                    <div className="flex items-center justify-between text-xs">
                                                        <span className="text-light-muted dark:text-dark-muted">{task.type}</span>
                                                        {task.dueDate && (
                                                            <span className={`font-medium ${task.dueDate < Date.now() ? 'text-red-500' : 'text-light-muted dark:text-dark-muted'}`}>
                                                                Due: {new Date(task.dueDate).toLocaleDateString()}
                                                            </span>
                                                        )}
                                                    </div>
                                                </motion.div>
                                            </Link>
                                        ))}
                                    </div>
                                )}
                            </CardContent>
                        </Card>
                    </div>

                    <div>
                        <Card glass>
                            <CardHeader>
                                <CardTitle>Quick Actions</CardTitle>
                            </CardHeader>
                            <CardContent className="space-y-3">
                                <Link to="/board/tasks">
                                    <motion.div
                                        whileHover={{ x: 5 }}
                                        className="p-4 rounded-lg bg-gradient-to-r from-primary-500 to-primary-600 text-white cursor-pointer"
                                    >
                                        <div className="flex items-center gap-3">
                                            <CheckCircle2 className="w-5 h-5" />
                                            <div>
                                                <p className="font-semibold">My Tasks</p>
                                                <p className="text-xs opacity-90">View all assignments</p>
                                            </div>
                                        </div>
                                    </motion.div>
                                </Link>

                                <Link to="/board/training">
                                    <motion.div
                                        whileHover={{ x: 5 }}
                                        className="p-4 rounded-lg bg-gradient-to-r from-secondary-500 to-secondary-600 text-white cursor-pointer"
                                    >
                                        <div className="flex items-center gap-3">
                                            <BookOpen className="w-5 h-5" />
                                            <div>
                                                <p className="font-semibold">Training Center</p>
                                                <p className="text-xs opacity-90">Continue learning</p>
                                            </div>
                                        </div>
                                    </motion.div>
                                </Link>

                                {(user.role === 'admin' || user.role === 'super_admin') && (
                                    <Link to="/board/admin">
                                        <motion.div
                                            whileHover={{ x: 5 }}
                                            className="p-4 rounded-lg border-2 border-primary-500 hover:bg-primary-50 dark:hover:bg-primary-900/20 cursor-pointer transition-all"
                                        >
                                            <div className="flex items-center gap-3">
                                                <TrendingUp className="w-5 h-5 text-primary-500" />
                                                <div>
                                                    <p className="font-semibold text-light-text dark:text-dark-text">Admin Panel</p>
                                                    <p className="text-xs text-light-muted dark:text-dark-muted">Manage board</p>
                                                </div>
                                            </div>
                                        </motion.div>
                                    </Link>
                                )}
                            </CardContent>
                        </Card>
                    </div>
                </div>
            </div>
        </div>
    );
}
