import { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { useQuery, useMutation } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { useAuth } from '@/hooks/useAuth';
import { Navigate } from 'react-router-dom';
import { CheckCircle2, Clock, XCircle, Calendar, User, ArrowRight } from 'lucide-react';
import { Card, CardContent } from '@/components/ui/Card';
import { Badge } from '@/components/ui/Badge';
import { Button } from '@/components/ui/Button';
import { BackButton } from '@/components/ui/BackButton';

export function BoardTasks() {
    const { user, isLoading } = useAuth();
    const [activeTab, setActiveTab] = useState<'all' | 'todo' | 'in_progress' | 'completed'>('all');

    const allTasks = useQuery(
        api.boardTasks.getTasks,
        user && (user.role === 'board_member' || user.role === 'admin' || user.role === 'super_admin')
            ? { userId: user._id }
            : 'skip'
    );

    const updateStatus = useMutation(api.boardTasks.updateTaskStatus);

    if (isLoading) {
        return (
            <div className="flex items-center justify-center min-h-screen">
                <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary-500" />
            </div>
        );
    }

    if (!user) {
        return <Navigate to="/login" replace />;
    }

    if (user.role !== 'board_member' && user.role !== 'admin' && user.role !== 'super_admin') {
        return <Navigate to="/feed" replace />;
    }

    const [viewMode, setViewMode] = useState<'my_tasks' | 'all_tasks'>('my_tasks');

    const filteredTasks = allTasks?.filter(task => {
        // Filter by view mode
        if (viewMode === 'my_tasks' && task.assignedTo !== user._id) return false;

        // Filter by tab
        if (activeTab === 'all') return true;
        return task.status === activeTab;
    }) || [];

    const handleStatusChange = async (taskId: string, newStatus: 'todo' | 'in_progress' | 'completed') => {
        if (!user) return;
        try {
            await updateStatus({
                userId: user._id,
                taskId: taskId as any,
                status: newStatus,
            });
        } catch (error) {
            console.error('Failed to update task status:', error);
        }
    };

    const getStatusIcon = (status: string) => {
        switch (status) {
            case 'completed': return <CheckCircle2 className="w-5 h-5 text-green-500" />;
            case 'in_progress': return <Clock className="w-5 h-5 text-accent-500" />;
            case 'cancelled': return <XCircle className="w-5 h-5 text-red-500" />;
            default: return <CheckCircle2 className="w-5 h-5 text-light-muted dark:text-dark-muted" />;
        }
    };

    const getPriorityColor = (priority: string) => {
        switch (priority) {
            case 'urgent': return 'text-red-500 bg-red-100 dark:bg-red-900/30';
            case 'high': return 'text-orange-500 bg-orange-100 dark:bg-orange-900/30';
            case 'medium': return 'text-yellow-500 bg-yellow-100 dark:bg-yellow-900/30';
            default: return 'text-blue-500 bg-blue-100 dark:bg-blue-900/30';
        }
    };

    return (
        <div className="min-h-screen py-8">
            <div className="container mx-auto px-4 sm:px-6 lg:px-8 max-w-6xl">
                <BackButton />
                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    className="mb-8"
                >
                    <h1 className="text-3xl md:text-4xl font-bold text-gradient mb-2">Board Tasks</h1>
                    <p className="text-light-muted dark:text-dark-muted">Manage your assignments and responsibilities</p>
                </motion.div>

                <div className="flex flex-col sm:flex-row gap-4 justify-between items-start sm:items-center mb-6">
                    <div className="flex gap-2 overflow-x-auto pb-2 sm:pb-0 w-full sm:w-auto thin-scrollbar">
                        {[
                            { key: 'all', label: 'All Tasks', count: allTasks?.length || 0 },
                            { key: 'todo', label: 'To Do', count: allTasks?.filter(t => t.status === 'todo').length || 0 },
                            { key: 'in_progress', label: 'In Progress', count: allTasks?.filter(t => t.status === 'in_progress').length || 0 },
                            { key: 'completed', label: 'Completed', count: allTasks?.filter(t => t.status === 'completed').length || 0 },
                        ].map((tab) => (
                            <button
                                key={tab.key}
                                onClick={() => setActiveTab(tab.key as any)}
                                className={`px-4 py-2 rounded-lg font-medium transition-all whitespace-nowrap text-sm ${activeTab === tab.key
                                    ? 'bg-primary-500 text-white shadow-lg shadow-primary-500/30'
                                    : 'bg-light-card dark:bg-dark-card text-light-text dark:text-dark-text border border-light-border dark:border-dark-border hover:bg-light-card/80 dark:hover:bg-dark-card/80'
                                    }`}
                            >
                                {tab.label} ({tab.count})
                            </button>
                        ))}
                    </div>

                    <div className="flex bg-light-card dark:bg-dark-card p-1 rounded-lg border border-light-border dark:border-dark-border">
                        <button
                            onClick={() => setViewMode('my_tasks')}
                            className={`px-3 py-1.5 rounded-md text-sm font-medium transition-all ${viewMode === 'my_tasks'
                                ? 'bg-primary-500 text-white shadow-sm'
                                : 'text-light-muted dark:text-dark-muted hover:text-light-text dark:hover:text-dark-text'
                                }`}
                        >
                            My Tasks
                        </button>
                        <button
                            onClick={() => setViewMode('all_tasks')}
                            className={`px-3 py-1.5 rounded-md text-sm font-medium transition-all ${viewMode === 'all_tasks'
                                ? 'bg-primary-500 text-white shadow-sm'
                                : 'text-light-muted dark:text-dark-muted hover:text-light-text dark:hover:text-dark-text'
                                }`}
                        >
                            All Tasks
                        </button>
                    </div>
                </div>

                <div className="space-y-4">
                    <AnimatePresence mode="popLayout">
                        {filteredTasks.length === 0 ? (
                            <Card glass>
                                <CardContent className="p-12 text-center">
                                    <p className="text-light-muted dark:text-dark-muted">No tasks found in this category</p>
                                </CardContent>
                            </Card>
                        ) : (
                            filteredTasks.map((task) => (
                                <motion.div
                                    key={task._id}
                                    layout
                                    initial={{ opacity: 0, y: 20 }}
                                    animate={{ opacity: 1, y: 0 }}
                                    exit={{ opacity: 0, y: -20 }}
                                >
                                    <Card glass>
                                        <CardContent className="p-6">
                                            <div className="flex items-start justify-between mb-4">
                                                <div className="flex items-start gap-3 flex-1">
                                                    {getStatusIcon(task.status)}
                                                    <div className="flex-1">
                                                        <h3 className="text-lg font-semibold text-light-text dark:text-dark-text mb-2">
                                                            {task.title}
                                                        </h3>
                                                        <p className="text-sm text-light-muted dark:text-dark-muted mb-3">
                                                            {task.description}
                                                        </p>
                                                        <div className="flex flex-wrap gap-2">
                                                            <Badge className={getPriorityColor(task.priority)}>{task.priority}</Badge>
                                                            <Badge variant="default">{task.type}</Badge>
                                                            {task.assignee && (
                                                                <div className="flex items-center gap-1 text-xs text-light-muted dark:text-dark-muted">
                                                                    <User className="w-3 h-3" />
                                                                    {task.assignee.name}
                                                                </div>
                                                            )}
                                                            {task.dueDate && (
                                                                <div className={`flex items-center gap-1 text-xs ${task.dueDate < Date.now() && task.status !== 'completed'
                                                                    ? 'text-red-500 font-semibold'
                                                                    : 'text-light-muted dark:text-dark-muted'
                                                                    }`}>
                                                                    <Calendar className="w-3 h-3" />
                                                                    {new Date(task.dueDate).toLocaleDateString()}
                                                                </div>
                                                            )}
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>

                                            {task.assignedTo === user._id && task.status !== 'completed' && (
                                                <div className="flex gap-2 pt-4 border-t border-light-border dark:border-dark-border">
                                                    {task.status === 'todo' && (
                                                        <Button
                                                            variant="primary"
                                                            size="sm"
                                                            onClick={() => handleStatusChange(task._id, 'in_progress')}
                                                        >
                                                            Start Task <ArrowRight className="w-4 h-4 ml-1" />
                                                        </Button>
                                                    )}
                                                    {task.status === 'in_progress' && (
                                                        <Button
                                                            variant="primary"
                                                            size="sm"
                                                            onClick={() => handleStatusChange(task._id, 'completed')}
                                                        >
                                                            Mark Complete <CheckCircle2 className="w-4 h-4 ml-1" />
                                                        </Button>
                                                    )}
                                                </div>
                                            )}
                                        </CardContent>
                                    </Card>
                                </motion.div>
                            ))
                        )}
                    </AnimatePresence>
                </div>
            </div>
        </div>
    );
}
