import { Link, useLocation } from 'react-router-dom';
import { Home, BookOpen, Sparkles, Bell, MessageSquare } from 'lucide-react';
import { useQuery } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { cn } from '@/lib/cn';

export function MobileNav() {
    const location = useLocation();

    const unreadCount = useQuery(api.notifications.getUnreadCount) || 0;

    const navItems = [
        {
            to: '/feed',
            icon: Home,
            label: 'Feed',
        },
        {
            to: '/ancient-culture',
            icon: BookOpen,
            label: 'Ancient',
        },
        {
            to: '/wisdom-now',
            icon: Sparkles,
            label: 'Wisdom',
        },
        {
            to: '/notifications',
            icon: Bell,
            label: 'Alerts',
            badge: unreadCount,
        },
        {
            to: '/my-responses',
            icon: MessageSquare,
            label: 'Responses',
        },
    ];

    return (
        <nav className="md:hidden fixed bottom-0 left-0 right-0 z-40 border-t border-light-border dark:border-dark-border glass pb-safe">
            <div className="flex items-center justify-around h-16 px-2">
                {navItems.map((item) => {
                    const Icon = item.icon;
                    const isActive = location.pathname === item.to;

                    return (
                        <Link
                            key={item.to}
                            to={item.to}
                            className={cn(
                                'flex flex-col items-center justify-center flex-1 min-w-0 transition-all relative py-1 mx-0.5 rounded-xl',
                                isActive
                                    ? 'text-primary-500 bg-primary-500/5'
                                    : 'text-light-muted dark:text-dark-muted hover:text-primary-500'
                            )}
                        >
                            <div className="relative">
                                <Icon className={cn('w-5 h-5 md:w-6 md:h-6', isActive && 'fill-primary-500/20')} />
                                {item.badge !== undefined && item.badge > 0 && (
                                    <span className="absolute -top-1.5 -right-1.5 bg-red-500 text-white text-[9px] font-bold h-4 min-w-[16px] px-1 flex items-center justify-center rounded-full border-2 border-white dark:border-black shadow-sm touch-none pointer-events-none">
                                        {item.badge > 9 ? '9+' : item.badge}
                                    </span>
                                )}
                            </div>
                            <span className={cn(
                                "text-[10px] font-semibold mt-1 truncate w-full text-center",
                                isActive ? "opacity-100" : "opacity-70"
                            )}>
                                {item.label}
                            </span>
                        </Link>
                    );
                })}
            </div>
        </nav>
    );
}
