import { useParams } from 'react-router-dom';
import { useQuery } from 'convex/react';
import { api } from '../../convex/_generated/api';
import { useAuth } from '@/hooks/useAuth';
import { Navigate } from 'react-router-dom';
import { Avatar } from '@/components/ui/Avatar';
import { Badge } from '@/components/ui/Badge';
import { BackButton } from '@/components/ui/BackButton';
import { Calendar, FileText, MessageCircle, Heart, Shield } from 'lucide-react';
import { motion } from 'framer-motion';
import { Id } from '../../convex/_generated/dataModel';
import { formatDistanceToNow } from 'date-fns';

export function MemberProfile() {
    const { userId } = useParams();
    const { user, isLoading: authLoading } = useAuth();

    const profile = useQuery(
        api.users.getUserProfile,
        userId ? { userId: userId as Id<'users'> } : 'skip'
    );

    if (authLoading) {
        return (
            <div className="flex items-center justify-center min-h-screen">
                <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary-500" />
            </div>
        );
    }

    if (!user) {
        return <Navigate to="/login" replace />;
    }

    if (profile === undefined) {
        return (
            <div className="flex items-center justify-center min-h-screen">
                <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary-500" />
            </div>
        );
    }

    if (profile === null) {
        return (
            <div className="min-h-screen py-8">
                <div className="container mx-auto px-4 sm:px-6 lg:px-8 max-w-4xl">
                    <BackButton />
                    <div className="text-center py-12 glass rounded-xl mt-6">
                        <p className="text-light-muted dark:text-dark-muted">
                            Member not found
                        </p>
                    </div>
                </div>
            </div>
        );
    }

    const isAdmin = profile.role === 'admin' || profile.role === 'super_admin';
    const isSuperAdmin = profile.role === 'super_admin';

    return (
        <div className="min-h-screen py-8">
            <div className="container mx-auto px-4 sm:px-6 lg:px-8 max-w-4xl">
                <BackButton />

                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    className="mt-6 space-y-6"
                >
                    {/* Profile Header */}
                    <div className={`glass rounded-xl p-6 md:p-8 ${isSuperAdmin
                            ? 'border border-yellow-500/30 bg-gradient-to-br from-yellow-500/5 to-transparent'
                            : isAdmin
                                ? 'border border-primary-500/30 bg-gradient-to-br from-primary-500/5 to-transparent'
                                : ''
                        }`}>
                        <div className="flex flex-col md:flex-row items-center gap-6">
                            <Avatar
                                src={profile.avatarUrl}
                                alt={profile.name}
                                size="xl"
                                className={
                                    isSuperAdmin
                                        ? 'ring-4 ring-yellow-500'
                                        : isAdmin
                                            ? 'ring-4 ring-primary-500'
                                            : ''
                                }
                            />

                            <div className="flex-1 text-center md:text-left">
                                <div className="flex flex-col md:flex-row items-center md:items-start gap-3 mb-2">
                                    <h1 className="text-2xl md:text-3xl font-bold text-light-text dark:text-dark-text">
                                        {profile.name}
                                    </h1>
                                    {isSuperAdmin ? (
                                        <Badge
                                            variant="primary"
                                            className="flex items-center gap-1 bg-gradient-to-r from-yellow-500 to-amber-600 text-white border border-yellow-300"
                                        >
                                            <Shield className="w-4 h-4" />
                                            Super Admin
                                        </Badge>
                                    ) : isAdmin ? (
                                        <Badge
                                            variant="primary"
                                            className="flex items-center gap-1 bg-gradient-to-r from-primary-500 to-primary-600 text-white"
                                        >
                                            <Shield className="w-4 h-4" />
                                            Admin
                                        </Badge>
                                    ) : (
                                        <Badge variant="default">Member</Badge>
                                    )}
                                </div>

                                <div className="flex items-center justify-center md:justify-start gap-2 text-sm text-light-muted dark:text-dark-muted">
                                    <Calendar className="w-4 h-4" />
                                    <span>
                                        Joined {formatDistanceToNow(profile.createdAt, { addSuffix: true })}
                                    </span>
                                </div>
                            </div>
                        </div>
                    </div>

                    {/* Stats */}
                    <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                        <div className="glass rounded-lg p-6 text-center">
                            <FileText className="w-8 h-8 mx-auto mb-2 text-primary-500" />
                            <div className="text-2xl font-bold text-light-text dark:text-dark-text">
                                {profile.stats.postsCount}
                            </div>
                            <div className="text-sm text-light-muted dark:text-dark-muted">
                                Posts
                            </div>
                        </div>

                        <div className="glass rounded-lg p-6 text-center">
                            <MessageCircle className="w-8 h-8 mx-auto mb-2 text-secondary-500" />
                            <div className="text-2xl font-bold text-light-text dark:text-dark-text">
                                {profile.stats.responsesCount}
                            </div>
                            <div className="text-sm text-light-muted dark:text-dark-muted">
                                Responses
                            </div>
                        </div>

                        <div className="glass rounded-lg p-6 text-center">
                            <Heart className="w-8 h-8 mx-auto mb-2 text-red-500" />
                            <div className="text-2xl font-bold text-light-text dark:text-dark-text">
                                {profile.stats.likesReceived}
                            </div>
                            <div className="text-sm text-light-muted dark:text-dark-muted">
                                Likes Received
                            </div>
                        </div>
                    </div>
                </motion.div>
            </div>
        </div>
    );
}
