import { useState } from 'react';
import { useAuth } from '@/hooks/useAuth';
import { Navigate } from 'react-router-dom';
import { useQuery, useMutation } from 'convex/react';
import { api } from '../../convex/_generated/api';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/Card';
import { Badge } from '@/components/ui/Badge';
import { Button } from '@/components/ui/Button';
import {
    Trophy,
    DollarSign,
    CheckCircle,
    XCircle,
    BookOpen,
    Sparkles,
    Phone,
    Mail,
    Eye,
    EyeOff,
} from 'lucide-react';
import { BackButton } from '@/components/ui/BackButton';
import { formatPhoneNumber } from '@/utils/phoneNetwork';
import { formatAccountNumber } from '@/utils/bankingUtils';
import { Id } from '../../convex/_generated/dataModel';

export function RewardManagement() {
    const { user, isLoading } = useAuth();
    const [selectedCategory, setSelectedCategory] = useState<'Ancient Culture' | 'Wisdom of The Now'>(
        'Ancient Culture'
    );
    const [visibleAccountNumbers, setVisibleAccountNumbers] = useState<Set<number>>(new Set());

    const responders = useQuery(
        api.rewardActions.getTop5Responders,
        user?.role === 'admin' && user._id ? { category: selectedCategory, adminId: user._id } : 'skip'
    );

    const approveReward = useMutation(api.rewardActions.approveReward);
    const cancelReward = useMutation(api.rewardActions.cancelReward);

    const [processingIds, setProcessingIds] = useState<Set<Id<'rewardActions'>>>(new Set());

    if (isLoading) {
        return (
            <div className="flex items-center justify-center min-h-screen">
                <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary-500" />
            </div>
        );
    }

    if (!user || user.role !== 'admin') {
        return <Navigate to="/feed" replace />;
    }

    const handleApprove = async (rewardActionId: Id<'rewardActions'>) => {
        if (!user) return;

        setProcessingIds((prev) => new Set(prev).add(rewardActionId));

        try {
            await approveReward({
                rewardActionId,
                adminId: user._id,
            });
        } catch (error) {
            console.error('Failed to approve reward:', error);
        } finally {
            setProcessingIds((prev) => {
                const newSet = new Set(prev);
                newSet.delete(rewardActionId);
                return newSet;
            });
        }
    };

    const handleCancel = async (rewardActionId: Id<'rewardActions'>) => {
        if (!user) return;

        if (!confirm('Are you sure you want to cancel this reward? The user will not be notified.')) {
            return;
        }

        setProcessingIds((prev) => new Set(prev).add(rewardActionId));

        try {
            await cancelReward({
                rewardActionId,
                adminId: user._id,
            });
        } catch (error) {
            console.error('Failed to cancel reward:', error);
        } finally {
            setProcessingIds((prev) => {
                const newSet = new Set(prev);
                newSet.delete(rewardActionId);
                return newSet;
            });
        }
    };

    const toggleAccountVisibility = (position: number) => {
        setVisibleAccountNumbers((prev) => {
            const newSet = new Set(prev);
            if (newSet.has(position)) {
                newSet.delete(position);
            } else {
                newSet.add(position);
            }
            return newSet;
        });
    };

    const pendingCount = responders?.filter((r: any) => r.rewardStatus === 'pending').length || 0;
    const approvedCount = responders?.filter((r: any) => r.rewardStatus === 'approved').length || 0;
    const cancelledCount = responders?.filter((r: any) => r.rewardStatus === 'cancelled').length || 0;

    return (
        <div className="min-h-screen py-8">
            <div className="container mx-auto px-4 sm:px-6 lg:px-8 max-w-6xl">
                <BackButton />
                <div className="mb-6 md:mb-8">
                    <h1 className="text-3xl md:text-4xl font-bold text-gradient font-display mb-2">
                        Reward Management
                    </h1>
                    <p className="text-sm md:text-base text-light-muted dark:text-dark-muted">
                        Manage rewards for the top 5 responders to monthly teaching series
                    </p>
                </div>

                {/* Stats */}
                <div className="grid md:grid-cols-4 gap-4 mb-8">
                    <Card glass>
                        <CardContent className="p-4">
                            <div className="flex items-center justify-between">
                                <div>
                                    <p className="text-sm text-light-muted dark:text-dark-muted">Total</p>
                                    <p className="text-2xl font-bold text-light-text dark:text-dark-text">
                                        {responders?.length || 0}
                                    </p>
                                </div>
                                <Trophy className="w-8 h-8 text-accent-500 opacity-20" />
                            </div>
                        </CardContent>
                    </Card>

                    <Card glass>
                        <CardContent className="p-4">
                            <div className="flex items-center justify-between">
                                <div>
                                    <p className="text-sm text-light-muted dark:text-dark-muted">Pending</p>
                                    <p className="text-2xl font-bold text-orange-500">{pendingCount}</p>
                                </div>
                                <DollarSign className="w-8 h-8 text-orange-500 opacity-20" />
                            </div>
                        </CardContent>
                    </Card>

                    <Card glass>
                        <CardContent className="p-4">
                            <div className="flex items-center justify-between">
                                <div>
                                    <p className="text-sm text-light-muted dark:text-dark-muted">Approved</p>
                                    <p className="text-2xl font-bold text-green-500">{approvedCount}</p>
                                </div>
                                <CheckCircle className="w-8 h-8 text-green-500 opacity-20" />
                            </div>
                        </CardContent>
                    </Card>

                    <Card glass>
                        <CardContent className="p-4">
                            <div className="flex items-center justify-between">
                                <div>
                                    <p className="text-sm text-light-muted dark:text-dark-muted">Cancelled</p>
                                    <p className="text-2xl font-bold text-red-500">{cancelledCount}</p>
                                </div>
                                <XCircle className="w-8 h-8 text-red-500 opacity-20" />
                            </div>
                        </CardContent>
                    </Card>
                </div>

                {/* Category Selection */}
                <Card glass className="mb-8">
                    <CardContent className="p-6">
                        <label className="block text-sm font-medium mb-2 text-light-text dark:text-dark-text">
                            Teaching Category
                        </label>
                        <div className="flex flex-wrap gap-2">
                            <button
                                onClick={() => setSelectedCategory('Ancient Culture')}
                                className={`flex-1 min-w-[140px] flex items-center justify-center gap-2 px-4 py-2 rounded-lg font-medium transition-all ${selectedCategory === 'Ancient Culture'
                                    ? 'bg-primary-500 text-white shadow-lg shadow-primary-500/30'
                                    : 'bg-light-card dark:bg-dark-card text-light-text dark:text-dark-text border border-light-border dark:border-dark-border'
                                    }`}
                            >
                                <BookOpen className="w-4 h-4" />
                                Ancient Culture
                            </button>
                            <button
                                onClick={() => setSelectedCategory('Wisdom of The Now')}
                                className={`flex-1 min-w-[140px] flex items-center justify-center gap-2 px-4 py-2 rounded-lg font-medium transition-all ${selectedCategory === 'Wisdom of The Now'
                                    ? 'bg-secondary-500 text-white shadow-lg shadow-secondary-500/30'
                                    : 'bg-light-card dark:bg-dark-card text-light-text dark:text-dark-text border border-light-border dark:border-dark-border'
                                    }`}
                            >
                                <Sparkles className="w-4 h-4" />
                                Wisdom of The Now
                            </button>
                        </div>
                    </CardContent>
                </Card>

                {/* Responders List */}
                <div className="space-y-6">
                    {responders && responders.length > 0 ? (
                        responders.map((responder: any) => {
                            const isProcessing = responder.rewardActionId
                                ? processingIds.has(responder.rewardActionId)
                                : false;
                            const showAccount = visibleAccountNumbers.has(responder.position);

                            return (
                                <Card key={responder.position} glass>
                                    <CardHeader>
                                        <div className="flex items-center justify-between">
                                            <CardTitle className="flex items-center gap-3">
                                                <div className="w-10 h-10 rounded-full bg-gradient-to-br from-primary-500 to-secondary-500 text-white flex items-center justify-center text-lg font-bold">
                                                    #{responder.position}
                                                </div>
                                                {responder.user.name}
                                            </CardTitle>
                                            <Badge
                                                variant={
                                                    responder.rewardStatus === 'approved'
                                                        ? 'primary'
                                                        : responder.rewardStatus === 'cancelled'
                                                            ? 'default'
                                                            : 'secondary'
                                                }
                                            >
                                                {responder.rewardStatus}
                                            </Badge>
                                        </div>
                                    </CardHeader>
                                    <CardContent>
                                        <div className="grid md:grid-cols-2 gap-6">
                                            {/* User Info */}
                                            <div className="space-y-3">
                                                <h3 className="font-semibold text-light-text dark:text-dark-text mb-3">
                                                    Contact Information
                                                </h3>
                                                <div className="flex items-center gap-2">
                                                    <Mail className="w-4 h-4 text-light-muted dark:text-dark-muted" />
                                                    <span className="text-sm text-light-text dark:text-dark-text">
                                                        {responder.user.email}
                                                    </span>
                                                </div>
                                                <div className="flex flex-wrap items-center gap-2">
                                                    <div className="flex items-center gap-2">
                                                        <Phone className="w-4 h-4 text-light-muted dark:text-dark-muted" />
                                                        <span className="text-sm text-light-text dark:text-dark-text">
                                                            {formatPhoneNumber(responder.user.phone)}
                                                        </span>
                                                    </div>
                                                    <Badge variant="default" className="text-[10px] md:text-xs">{responder.user.network}</Badge>
                                                </div>

                                                {/* Banking Info */}
                                                <div className="pt-3 border-t border-light-border dark:border-dark-border">
                                                    <h4 className="font-semibold text-light-text dark:text-dark-text mb-2 flex items-center gap-2">
                                                        <DollarSign className="w-4 h-4 text-accent-500" />
                                                        Banking Information
                                                    </h4>
                                                    {responder.user.bankName ? (
                                                        <div className="space-y-2">
                                                            <div>
                                                                <p className="text-xs text-light-muted dark:text-dark-muted">
                                                                    Bank Name
                                                                </p>
                                                                <p className="text-sm text-light-text dark:text-dark-text">
                                                                    {responder.user.bankName}
                                                                </p>
                                                            </div>
                                                            <div>
                                                                <p className="text-xs text-light-muted dark:text-dark-muted">
                                                                    Account Number
                                                                </p>
                                                                <div className="flex items-center gap-2">
                                                                    <p className="text-sm text-light-text dark:text-dark-text font-mono">
                                                                        {showAccount
                                                                            ? responder.user.accountNumber
                                                                            : formatAccountNumber(
                                                                                responder.user.accountNumber || ''
                                                                            )}
                                                                    </p>
                                                                    <button
                                                                        onClick={() =>
                                                                            toggleAccountVisibility(
                                                                                responder.position
                                                                            )
                                                                        }
                                                                        className="text-light-muted dark:text-dark-muted hover:text-primary-500 transition-colors"
                                                                    >
                                                                        {showAccount ? (
                                                                            <EyeOff className="w-4 h-4" />
                                                                        ) : (
                                                                            <Eye className="w-4 h-4" />
                                                                        )}
                                                                    </button>
                                                                </div>
                                                            </div>
                                                            <div>
                                                                <p className="text-xs text-light-muted dark:text-dark-muted">
                                                                    Account Name
                                                                </p>
                                                                <p className="text-sm text-light-text dark:text-dark-text">
                                                                    {responder.user.accountName}
                                                                </p>
                                                            </div>
                                                        </div>
                                                    ) : (
                                                        <p className="text-sm text-light-muted dark:text-dark-muted italic">
                                                            Banking information not provided
                                                        </p>
                                                    )}
                                                </div>
                                            </div>

                                            {/* Post Info */}
                                            <div className="space-y-3">
                                                <h3 className="font-semibold text-light-text dark:text-dark-text mb-3">
                                                    Response Post
                                                </h3>
                                                {responder.post ? (
                                                    <div className="p-3 rounded-lg bg-light-bg dark:bg-dark-bg">
                                                        <p className="text-sm text-light-text dark:text-dark-text line-clamp-4">
                                                            {responder.post.content}
                                                        </p>
                                                        <p className="text-xs text-light-muted dark:text-dark-muted mt-2">
                                                            {new Date(
                                                                responder.post.createdAt
                                                            ).toLocaleDateString()}
                                                        </p>
                                                    </div>
                                                ) : (
                                                    <p className="text-sm text-light-muted dark:text-dark-muted italic">
                                                        No post found
                                                    </p>
                                                )}

                                                {/* Action Buttons */}
                                                {responder.rewardActionId && (
                                                    <div className="pt-3 border-t border-light-border dark:border-dark-border">
                                                        {responder.rewardStatus === 'pending' ? (
                                                            <div className="flex gap-2">
                                                                <Button
                                                                    onClick={() =>
                                                                        handleApprove(responder.rewardActionId!)
                                                                    }
                                                                    variant="primary"
                                                                    className="flex-1 flex items-center justify-center gap-2"
                                                                    disabled={isProcessing}
                                                                >
                                                                    <CheckCircle className="w-4 h-4" />
                                                                    {isProcessing ? 'Processing...' : 'Reward'}
                                                                </Button>
                                                                <Button
                                                                    onClick={() =>
                                                                        handleCancel(responder.rewardActionId!)
                                                                    }
                                                                    variant="secondary"
                                                                    className="flex-1 flex items-center justify-center gap-2"
                                                                    disabled={isProcessing}
                                                                >
                                                                    <XCircle className="w-4 h-4" />
                                                                    Cancel
                                                                </Button>
                                                            </div>
                                                        ) : (
                                                            <div className="text-center p-3 rounded-lg bg-light-bg dark:bg-dark-bg">
                                                                <p className="text-sm text-light-muted dark:text-dark-muted">
                                                                    {responder.rewardStatus === 'approved'
                                                                        ? ' Reward has been approved and user notified'
                                                                        : ' Reward has been cancelled'}
                                                                </p>
                                                            </div>
                                                        )}
                                                    </div>
                                                )}
                                            </div>
                                        </div>
                                    </CardContent>
                                </Card>
                            );
                        })
                    ) : (
                        <Card glass>
                            <CardContent className="p-8 text-center">
                                <Trophy className="w-16 h-16 text-light-muted dark:text-dark-muted mx-auto mb-4 opacity-20" />
                                <p className="text-light-muted dark:text-dark-muted">
                                    No responders yet for {selectedCategory}
                                </p>
                            </CardContent>
                        </Card>
                    )}
                </div>
            </div>
        </div>
    );
}
