import { useQuery } from 'convex/react';
import { api } from '../../convex/_generated/api';
import { Card, CardContent } from '@/components/ui/Card';
import { Trophy, Medal } from 'lucide-react';
import { BackButton } from '@/components/ui/BackButton';

function Leaderboard() {
    const leaderboard = useQuery(api.responses.getLeaderboard);

    if (!leaderboard) {
        return <div className="p-8 text-center text-light-muted">Loading leaderboard...</div>;
    }

    return (
        <div className="min-h-screen py-8">
            <div className="container mx-auto px-4 sm:px-6 lg:px-8 max-w-4xl">
                <BackButton />
                <div className="mb-6 md:mb-8 text-center">
                    <h1 className="text-3xl md:text-4xl font-bold text-gradient font-display mb-2">
                        Monthly Leaderboard
                    </h1>
                    <p className="text-sm md:text-base text-light-muted dark:text-dark-muted">
                        Top responders for {new Date().toLocaleString('default', { month: 'long', year: 'numeric' })}
                    </p>
                </div>

                <div className="space-y-4">
                    {leaderboard.length === 0 ? (
                        <Card glass>
                            <CardContent className="p-8 text-center">
                                <Trophy className="w-16 h-16 text-primary-200 mx-auto mb-4" />
                                <p className="text-lg text-light-muted">No graded responses yet this month.</p>
                                <p className="text-sm text-light-muted">Be the first to respond to an Official Post!</p>
                            </CardContent>
                        </Card>
                    ) : (
                        leaderboard.map((entry, index) => (
                            <LeaderboardEntry
                                key={entry.userId}
                                entry={entry}
                                rank={index + 1}
                            />
                        ))
                    )}
                </div>
            </div>
        </div>
    );
}

function LeaderboardEntry({ entry, rank }: { entry: any, rank: number }) {
    let RankIcon = null;
    let rankColor = "bg-light-card dark:bg-dark-card";

    if (rank === 1) {
        RankIcon = <Trophy className="w-6 h-6 text-yellow-500" />;
        rankColor = "bg-yellow-50/50 dark:bg-yellow-900/10 border-yellow-200 dark:border-yellow-800";
    } else if (rank === 2) {
        RankIcon = <Medal className="w-6 h-6 text-gray-400" />;
        rankColor = "bg-gray-50/50 dark:bg-gray-900/10 border-gray-200 dark:border-gray-800";
    } else if (rank === 3) {
        RankIcon = <Medal className="w-6 h-6 text-orange-400" />;
        rankColor = "bg-orange-50/50 dark:bg-orange-900/10 border-orange-200 dark:border-orange-800";
    }

    return (
        <div className={`flex items-center gap-4 p-4 rounded-xl border transition-all hover:scale-[1.01] ${rankColor}`}>
            <div className="flex-shrink-0 w-12 text-center font-bold text-xl text-light-muted dark:text-dark-muted flex justify-center">
                {RankIcon || <span>#{rank}</span>}
            </div>

            <div className="flex-shrink-0">
                {/* Fixed Avatar import usage here */}
                <div className="w-12 h-12 rounded-full overflow-hidden bg-gradient-to-br from-primary-400 to-primary-600">
                    {entry.avatarUrl ? (
                        <img src={entry.avatarUrl} alt={entry.name} className="w-full h-full object-cover" />
                    ) : (
                        <div className="w-full h-full flex items-center justify-center text-white font-bold text-lg">
                            {entry.name[0]}
                        </div>
                    )}
                </div>
            </div>

            <div className="flex-1 min-w-0">
                <h3 className="font-bold text-lg text-light-text dark:text-dark-text truncate">
                    {entry.name}
                </h3>
                <p className="text-sm text-light-muted dark:text-dark-muted">
                    {entry.responsesCount} graded response{entry.responsesCount !== 1 ? 's' : ''}
                </p>
            </div>

            <div className="text-right">
                <div className="text-2xl font-bold text-primary-600 dark:text-primary-400">
                    {entry.totalScore}
                </div>
                <div className="text-xs text-light-muted uppercase tracking-wider font-medium">
                    Points
                </div>
            </div>
        </div>
    );
}

export default Leaderboard;
