
export function PostCardSkeleton() {
    return (
        <div className="glass rounded-xl p-6 shadow-md animate-pulse">
            {/* Header */}
            <div className="flex items-start gap-3 mb-4">
                {/* Avatar skeleton */}
                <div className="w-12 h-12 rounded-full bg-light-muted/20 dark:bg-dark-muted/20" />

                <div className="flex-1">
                    {/* Name & badge skeleton */}
                    <div className="flex items-center gap-2 mb-2">
                        <div className="h-4 w-32 bg-light-muted/20 dark:bg-dark-muted/20 rounded" />
                        <div className="h-4 w-16 bg-light-muted/20 dark:bg-dark-muted/20 rounded" />
                    </div>
                    {/* Time & category skeleton */}
                    <div className="flex items-center gap-2">
                        <div className="h-3 w-24 bg-light-muted/20 dark:bg-dark-muted/20 rounded" />
                        <div className="h-3 w-20 bg-light-muted/20 dark:bg-dark-muted/20 rounded" />
                    </div>
                </div>
            </div>

            {/* Content skeleton */}
            <div className="space-y-2 mb-4">
                <div className="h-4 w-full bg-light-muted/20 dark:bg-dark-muted/20 rounded" />
                <div className="h-4 w-5/6 bg-light-muted/20 dark:bg-dark-muted/20 rounded" />
                <div className="h-4 w-4/6 bg-light-muted/20 dark:bg-dark-muted/20 rounded" />
            </div>

            {/* Footer skeleton */}
            <div className="flex items-center gap-4 pt-3 border-t border-light-border dark:border-dark-border">
                <div className="h-8 w-20 bg-light-muted/20 dark:bg-dark-muted/20 rounded-lg" />
                <div className="h-8 w-20 bg-light-muted/20 dark:bg-dark-muted/20 rounded-lg" />
            </div>
        </div>
    );
}

export function AnnouncementCardSkeleton() {
    return (
        <div className="glass rounded-xl overflow-hidden shadow-lg animate-pulse">
            {/* Image skeleton */}
            <div className="h-48 bg-light-muted/20 dark:bg-dark-muted/20" />

            {/* Content skeleton */}
            <div className="p-6">
                <div className="h-6 w-3/4 bg-light-muted/20 dark:bg-dark-muted/20 rounded mb-3" />
                <div className="space-y-2">
                    <div className="h-4 w-full bg-light-muted/20 dark:bg-dark-muted/20 rounded" />
                    <div className="h-4 w-5/6 bg-light-muted/20 dark:bg-dark-muted/20 rounded" />
                </div>
            </div>
        </div>
    );
}
