import { useAuth } from '@/hooks/useAuth';
import { Navigate } from 'react-router-dom';
import { useQuery, useMutation } from 'convex/react';
import { api } from '../../convex/_generated/api';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/Card';
import { Badge } from '@/components/ui/Badge';
import { RewardBadge } from '@/components/rewards/RewardBadge';
import { AvatarUpload } from '@/components/ui/AvatarUpload';
import { User, Trophy, FileText } from 'lucide-react';
import { formatPhoneNumber } from '@/utils/phoneNetwork';
import { BankingCredentials } from '@/components/banking/BankingCredentials';
import { BackButton } from '@/components/ui/BackButton';

export function Profile() {
    const { user, isLoading, updateUser } = useAuth();
    const updateAvatar = useMutation(api.presence.updateAvatar);




    const userStats = user ? useQuery(api.users.getUserStats, { userId: user._id }) : undefined;

    const handleAvatarUpload = async (url: string) => {
        if (!user) return;

        try {
            await updateAvatar({
                userId: user._id,
                avatarUrl: url,
            });
            // Update the user context with new avatar
            updateUser({ avatarUrl: url });
        } catch (error) {
            console.error('Failed to update avatar:', error);
        }
    };



    if (isLoading) {
        return (
            <div className="flex items-center justify-center min-h-screen">
                <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary-500" />
            </div>
        );
    }

    if (!user) {
        return <Navigate to="/login" replace />;
    }

    return (
        <div className="min-h-screen py-8">
            <div className="container mx-auto px-4 sm:px-6 lg:px-8 max-w-4xl">
                <BackButton />
                <div className="mb-6 md:mb-8">
                    <h1 className="text-3xl md:text-4xl font-bold text-gradient font-display mb-2">
                        My Profile
                    </h1>
                    <p className="text-sm md:text-base text-light-muted dark:text-dark-muted">
                        View your account details and community stats
                    </p>
                </div>

                <div className="grid md:grid-cols-2 gap-6">
                    {/* Profile Info */}
                    <Card glass>
                        <CardHeader>
                            <CardTitle className="flex items-center gap-2">
                                <User className="w-5 h-5 text-primary-500" />
                                Profile Information
                            </CardTitle>
                        </CardHeader>
                        <CardContent className="space-y-4">
                            {/* Avatar Upload */}
                            <div className="flex justify-center pb-4 border-b border-light-border dark:border-dark-border">
                                <AvatarUpload
                                    currentAvatarUrl={user.avatarUrl}
                                    onUploadComplete={handleAvatarUpload}
                                    size="lg"
                                />
                            </div>

                            <div>
                                <label className="text-sm text-light-muted dark:text-dark-muted">Name</label>
                                <p className="text-lg font-medium text-light-text dark:text-dark-text">{user.name}</p>
                            </div>
                            <div>
                                <label className="text-sm text-light-muted dark:text-dark-muted">Email</label>
                                <p className="text-lg text-light-text dark:text-dark-text">{user.email}</p>
                            </div>
                            <div>
                                <label className="text-sm text-light-muted dark:text-dark-muted">Phone</label>
                                <div className="flex items-center gap-2">
                                    <p className="text-lg text-light-text dark:text-dark-text">
                                        {formatPhoneNumber(user.phone)}
                                    </p>
                                    <Badge variant="primary">{user.network}</Badge>
                                </div>
                            </div>
                            <div>
                                <label className="text-sm text-light-muted dark:text-dark-muted">Role</label>
                                <div className="mt-1">
                                    <Badge
                                        variant={user.role === 'super_admin' ? 'secondary' : (user.role === 'admin' ? 'primary' : 'default')}
                                    >
                                        {user.role === 'super_admin' ? 'Super Admin' : (user.role === 'admin' ? 'Admin' : 'Member')}
                                    </Badge>
                                </div>
                            </div>
                            <div>
                                <label className="text-sm text-light-muted dark:text-dark-muted">WhatsApp Status</label>
                                <div className="mt-1">
                                    <Badge variant={user.whatsappVerified ? 'success' : 'warning'}>
                                        {user.whatsappVerified ? 'Verified' : 'Not Verified'}
                                    </Badge>
                                </div>
                            </div>
                        </CardContent>
                    </Card>

                    {/* Statistics */}
                    <div className="space-y-6">
                        <Card glass>
                            <CardHeader>
                                <CardTitle className="flex items-center gap-2">
                                    <FileText className="w-5 h-5 text-primary-500" />
                                    Activity Stats
                                </CardTitle>
                            </CardHeader>
                            <CardContent>
                                <div className="space-y-4">
                                    <div className="flex items-center justify-between p-4 rounded-lg bg-light-bg dark:bg-dark-bg">
                                        <span className="text-light-muted dark:text-dark-muted">Total Posts</span>
                                        <span className="text-2xl font-bold text-primary-500">
                                            {userStats?.totalPosts || 0}
                                        </span>
                                    </div>
                                </div>
                            </CardContent>
                        </Card>

                        <Card glass>
                            <CardHeader>
                                <CardTitle className="flex items-center gap-2">
                                    <Trophy className="w-5 h-5 text-accent-500" />
                                    Rewards Earned
                                </CardTitle>
                            </CardHeader>
                            <CardContent>
                                <div className="space-y-3">
                                    {userStats?.rewardPositions && userStats.rewardPositions.length > 0 ? (
                                        <div className="space-y-2">
                                            {userStats.rewardPositions.map((position, index) => (
                                                <div key={index} className="flex items-center justify-between p-3 rounded-lg bg-light-bg dark:bg-dark-bg">
                                                    <span className="text-sm text-light-muted dark:text-dark-muted">
                                                        Reward #{index + 1}
                                                    </span>
                                                    <RewardBadge position={position} />
                                                </div>
                                            ))}
                                            <div className="pt-3 border-t border-light-border dark:border-dark-border">
                                                <div className="flex items-center justify-between">
                                                    <span className="font-medium text-light-text dark:text-dark-text">
                                                        Total Rewards
                                                    </span>
                                                    <span className="text-2xl font-bold text-accent-500">
                                                        {userStats.totalRewards}
                                                    </span>
                                                </div>
                                            </div>
                                        </div>
                                    ) : (
                                        <p className="text-center text-light-muted dark:text-dark-muted py-4">
                                            No rewards yet. Be among the first 5 to post after a teaching session!
                                        </p>
                                    )}
                                </div>
                            </CardContent>
                        </Card>
                    </div>
                </div>

                {/* Banking Information - Full Width */}
                <div className="mt-6">
                    <BankingCredentials />
                </div>
            </div>
        </div>
    );
}
