import React, { useState } from 'react';
import { useMutation } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { Modal } from '@/components/ui/Modal';
import { Button } from '@/components/ui/Button';
import { Id } from '../../../convex/_generated/dataModel';
import { useAuth } from '@/hooks/useAuth';
import { Send, AlertCircle } from 'lucide-react';

interface ResponseModalProps {
    isOpen: boolean;
    onClose: () => void;
    postId: Id<"posts">;
    postContent: string;
}

export function ResponseModal({ isOpen, onClose, postId, postContent }: ResponseModalProps) {
    const { user } = useAuth();
    const submitResponse = useMutation(api.responses.submitResponse);
    const [content, setContent] = useState('');
    const [isSubmitting, setIsSubmitting] = useState(false);
    const [error, setError] = useState('');

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        if (!user || !content.trim()) return;

        setIsSubmitting(true);
        setError('');

        try {
            await submitResponse({
                userId: user._id,
                postId,
                content: content.trim(),
            });
            onClose();
            setContent('');
        } catch (err: any) {
            console.error("Failed to submit response:", err);
            // Handle specific errors like "Vacancy Filled"
            if (err.message.includes("Vacancy Filled")) {
                setError("Sorry, the response slots for this post are full.");
            } else if (err.message.includes("already responded")) {
                setError("You have already responded to this post.");
            } else {
                setError("Failed to submit response. Please try again.");
            }
        } finally {
            setIsSubmitting(false);
        }
    };

    return (
        <Modal isOpen={isOpen} onClose={onClose} title="Submit Official Response">
            <form onSubmit={handleSubmit} className="space-y-4">
                <div className="bg-light-bg dark:bg-dark-bg p-3 rounded-lg text-sm text-light-muted dark:text-dark-muted mb-4 border-l-4 border-primary-500">
                    <p className="font-medium mb-1">Replying to Official Post:</p>
                    <p className="line-clamp-2 italic">"{postContent}"</p>
                </div>

                {error && (
                    <div className="bg-red-50 dark:bg-red-900/20 text-red-600 dark:text-red-400 p-3 rounded-lg flex items-center gap-2 text-sm">
                        <AlertCircle className="w-4 h-4" />
                        {error}
                    </div>
                )}

                <div>
                    <label className="block text-sm font-medium text-light-text dark:text-dark-text mb-1">
                        Your Response
                    </label>
                    <textarea
                        value={content}
                        onChange={(e) => setContent(e.target.value)}
                        placeholder="Type your official response here..."
                        className="w-full min-h-[150px] p-3 rounded-lg bg-light-bg dark:bg-dark-bg border border-light-border dark:border-dark-border focus:ring-2 focus:ring-primary-500 text-light-text dark:text-dark-text resize-y"
                        autoFocus
                    />
                    <p className="text-xs text-light-muted dark:text-dark-muted mt-1 text-right">
                        {content.length} characters
                    </p>
                </div>

                <div className="flex justify-end gap-2 pt-2">
                    <Button type="button" variant="ghost" onClick={onClose}>
                        Cancel
                    </Button>
                    <Button
                        type="submit"
                        variant="primary"
                        disabled={!content.trim() || isSubmitting}
                        className="flex items-center gap-2"
                    >
                        <Send className="w-4 h-4" />
                        {isSubmitting ? 'Submitting...' : 'Submit Response'}
                    </Button>
                </div>
            </form>
        </Modal>
    );
}
