import React from 'react';
import { BrowserRouter as Router, Routes, Route, useLocation } from 'react-router-dom';
import { ConvexProvider, ConvexReactClient } from 'convex/react';
import { ThemeProvider } from './context/ThemeContext';
import { AuthProvider } from './context/AuthContext';
import { Header } from './components/layout/Header';
import { Footer } from './components/layout/Footer';
import { MobileNav } from './components/layout/MobileNav';
import { DesktopNav } from './components/layout/DesktopNav';
import { Home } from './pages/Home';
import { Login } from './pages/Login';
import { Register } from './pages/Register';
import { Feed } from './pages/Feed';
import { AncientCulture } from './pages/AncientCulture';
import { WisdomNow } from './pages/WisdomNow';
import { Profile } from './pages/Profile';
import { AdminDashboard } from './pages/AdminDashboard';
import { RewardManagement } from './pages/RewardManagement';
import { Notifications } from './pages/Notifications';
import UserTasksPage from './pages/UserTasksPage';
import AdminTaskReview from './pages/AdminTaskReview';
import SuperAdminSetup from './pages/SuperAdminSetup';
import AdminResponseManager from './pages/AdminResponseManager';
import AdminRewardResponses from './pages/AdminRewardResponses';
import MyResponses from './pages/MyResponses';
import Leaderboard from './pages/Leaderboard';
import { PostResponses } from './pages/PostResponses';
import { PostDetails } from './pages/PostDetails';
import { SearchPage } from './pages/SearchPage';
import { MemberProfile } from './pages/MemberProfile';
import { About } from './pages/About';
import { Privacy } from './pages/Privacy';
import { Terms } from './pages/Terms';
import { Contact } from './pages/Contact';
import { FAQ } from './pages/FAQ';
import { Guidelines } from './pages/Guidelines';
import { AdminContacts } from './pages/AdminContacts';
import { AdminMemberInfo } from './pages/AdminMemberInfo';
import { BoardDashboard } from './pages/board/BoardDashboard';
import { BoardTasks } from './pages/board/BoardTasks';
import { TrainingCenter } from './pages/board/TrainingCenter';
import { BoardAdmin } from './pages/board/BoardAdmin';
import { MemberOnboarding } from './pages/MemberOnboarding';
import { ScrollProgress } from './components/ui/ScrollProgress';

const convex = new ConvexReactClient(import.meta.env.VITE_CONVEX_URL as string);

import { PageLoader } from './components/ui/PageLoader';
import { useNotifications } from './hooks/useNotifications';
import { InstallPrompt } from '@/components/pwa/InstallPrompt';

function AppContent() {
    const location = useLocation();
    const [isLoading, setIsLoading] = React.useState(true);
    useNotifications(); // Call the hook here

    React.useEffect(() => {
        // Simulate initial load for premium feel
        const timer = setTimeout(() => setIsLoading(false), 2000);
        return () => clearTimeout(timer);
    }, []);

    if (isLoading) {
        return <PageLoader />;
    }

    // Don't show footer on feed pages for social media feel
    const hiddenFooterPaths = ['/feed', '/ancient-culture', '/wisdom-now', '/profile', '/admin', '/notifications', '/tasks', '/leaderboard', '/admin/reward-responses', '/my-responses', '/admin/responses', '/post', '/search'];
    const shouldShowFooter = !hiddenFooterPaths.some(path => location.pathname.startsWith(path));

    // Show mobile nav only when logged in and on feed pages
    const showMobileNav = hiddenFooterPaths.some(path => location.pathname.startsWith(path));

    // Show desktop nav on feed-related pages
    const feedPaths = ['/feed', '/ancient-culture', '/wisdom-now', '/notifications', '/tasks', '/admin/tasks', '/leaderboard', '/search'];
    const showDesktopNav = feedPaths.some(path => location.pathname.startsWith(path));

    return (
        <div className="flex flex-col min-h-screen">
            <Header />
            {showDesktopNav && <DesktopNav />}
            <main className={`flex-1 pt-16 md:pt-20 ${showMobileNav ? 'pb-20 md:pb-0' : ''}`}>
                <Routes>
                    <Route path="/" element={<Home />} />
                    <Route path="/login" element={<Login />} />
                    <Route path="/register" element={<Register />} />
                    <Route path="/feed" element={<Feed />} />
                    <Route path="/ancient-culture" element={<AncientCulture />} />
                    <Route path="/wisdom-now" element={<WisdomNow />} />
                    <Route path="/profile" element={<Profile />} />
                    <Route path="/admin" element={<AdminDashboard />} />
                    <Route path="/admin/rewards" element={<RewardManagement />} />
                    <Route path="/admin/tasks" element={<AdminTaskReview />} />
                    <Route path="/admin/responses" element={<AdminResponseManager />} />
                    <Route path="/admin/reward-responses" element={<AdminRewardResponses />} />
                    <Route path="/admin/contacts" element={<AdminContacts />} />
                    <Route path="/admin/members" element={<AdminMemberInfo />} />
                    <Route path="/my-responses" element={<MyResponses />} />
                    <Route path="/tasks" element={<UserTasksPage />} />
                    <Route path="/leaderboard" element={<Leaderboard />} />
                    <Route path="/notifications" element={<Notifications />} />
                    <Route path="/setup" element={<SuperAdminSetup />} />
                    <Route path="/search" element={<SearchPage />} />
                    <Route path="/member-onboarding" element={<MemberOnboarding />} />

                    {/* Board Member Routes */}
                    <Route path="/board" element={<BoardDashboard />} />
                    <Route path="/board/tasks" element={<BoardTasks />} />
                    <Route path="/board/training" element={<TrainingCenter />} />
                    <Route path="/board/admin" element={<BoardAdmin />} />

                    {/* Footer Pages */}
                    <Route path="/profile/:userId" element={<MemberProfile />} />
                    <Route path="/about" element={<About />} />
                    <Route path="/privacy" element={<Privacy />} />
                    <Route path="/terms" element={<Terms />} />
                    <Route path="/contact" element={<Contact />} />
                    <Route path="/faq" element={<FAQ />} />
                    <Route path="/guidelines" element={<Guidelines />} />
                    <Route path="/post/:postId/responses" element={<PostResponses />} />
                    <Route path="/post/:postId" element={<PostDetails />} />
                </Routes>
            </main>
            {shouldShowFooter && <Footer />}
            {showMobileNav && <MobileNav />}
            <ScrollProgress />
        </div>
    );
}

function App() {
    return (
        <ConvexProvider client={convex}>
            <ThemeProvider>
                <AuthProvider>
                    <Router>
                        <InstallPrompt />
                        <AppContent />
                    </Router>
                </AuthProvider>
            </ThemeProvider>
        </ConvexProvider>
    );
}

export default App;

