import { useState } from 'react';
import { useQuery } from 'convex/react';
import { api } from '../../convex/_generated/api';
import { useAuth } from '@/hooks/useAuth';
import { Navigate, useNavigate } from 'react-router-dom';
import { Avatar } from '@/components/ui/Avatar';
import { Badge } from '@/components/ui/Badge';
import { Search, User } from 'lucide-react';
import { motion } from 'framer-motion';

export function SearchPage() {
    const { user, isLoading } = useAuth();
    const navigate = useNavigate();
    const [searchQuery, setSearchQuery] = useState('');

    const searchResults = useQuery(
        api.users.searchMembers,
        searchQuery.trim().length > 0 ? { query: searchQuery } : 'skip'
    );

    if (isLoading) {
        return (
            <div className="flex items-center justify-center min-h-screen">
                <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary-500" />
            </div>
        );
    }

    if (!user) {
        return <Navigate to="/login" replace />;
    }

    return (
        <div className="min-h-screen py-8">
            <div className="container mx-auto px-4 sm:px-6 lg:px-8 max-w-4xl">
                <motion.div
                    initial={{ opacity: 0, y: -20 }}
                    animate={{ opacity: 1, y: 0 }}
                    className="mb-6 md:mb-8"
                >
                    <h1 className="text-2xl md:text-4xl font-bold text-primary-600 dark:text-primary-400 mb-2">
                        Search Members
                    </h1>
                    <p className="text-light-text dark:text-dark-text text-sm md:text-base">
                        Find and connect with community members
                    </p>
                </motion.div>

                {/* Search Input */}
                <div className="mb-8">
                    <div className="relative">
                        <Search className="absolute left-4 top-1/2 -translate-y-1/2 w-5 h-5 text-light-muted dark:text-dark-muted" />
                        <input
                            type="text"
                            value={searchQuery}
                            onChange={(e) => setSearchQuery(e.target.value)}
                            placeholder="Search by name..."
                            className="w-full pl-12 pr-4 py-3 rounded-lg border border-light-border dark:border-dark-border bg-light-card dark:bg-dark-card text-light-text dark:text-dark-text placeholder-light-muted dark:placeholder-dark-muted focus:ring-2 focus:ring-primary-500 focus:border-transparent outline-none transition-all"
                        />
                    </div>
                </div>

                {/* Search Results */}
                {searchQuery.trim().length === 0 ? (
                    <div className="text-center py-12 glass rounded-xl">
                        <User className="w-12 h-12 mx-auto mb-4 text-light-muted dark:text-dark-muted" />
                        <p className="text-light-muted dark:text-dark-muted">
                            Start typing to search for members
                        </p>
                    </div>
                ) : searchResults === undefined ? (
                    <div className="space-y-3">
                        {[1, 2, 3].map((i) => (
                            <div key={i} className="glass rounded-lg p-4 animate-pulse">
                                <div className="flex items-center gap-3">
                                    <div className="w-12 h-12 rounded-full bg-light-bg dark:bg-dark-bg" />
                                    <div className="flex-1">
                                        <div className="h-4 bg-light-bg dark:bg-dark-bg rounded w-1/3 mb-2" />
                                        <div className="h-3 bg-light-bg dark:bg-dark-bg rounded w-1/4" />
                                    </div>
                                </div>
                            </div>
                        ))}
                    </div>
                ) : searchResults.length === 0 ? (
                    <div className="text-center py-12 glass rounded-xl">
                        <User className="w-12 h-12 mx-auto mb-4 text-light-muted dark:text-dark-muted" />
                        <p className="text-light-muted dark:text-dark-muted">
                            No members found matching "{searchQuery}"
                        </p>
                    </div>
                ) : (
                    <div className="space-y-3">
                        {searchResults.map((member) => (
                            <motion.div
                                key={member._id}
                                initial={{ opacity: 0, y: 10 }}
                                animate={{ opacity: 1, y: 0 }}
                                onClick={() => navigate(`/profile/${member._id}`)}
                                className="glass rounded-lg p-4 hover:shadow-lg transition-all cursor-pointer group"
                            >
                                <div className="flex items-center gap-3">
                                    <Avatar
                                        src={member.avatarUrl}
                                        alt={member.name}
                                        size="md"
                                    />
                                    <div className="flex-1">
                                        <h3 className="font-semibold text-light-text dark:text-dark-text group-hover:text-primary-500 transition-colors">
                                            {member.name}
                                        </h3>
                                        <Badge
                                            variant={member.role === 'admin' || member.role === 'super_admin' ? 'primary' : 'default'}
                                            className="text-xs mt-1"
                                        >
                                            {member.role === 'super_admin' ? 'Super Admin' : member.role === 'admin' ? 'Admin' : 'Member'}
                                        </Badge>
                                    </div>
                                </div>
                            </motion.div>
                        ))}
                    </div>
                )}
            </div>
        </div>
    );
}
