import { useState } from 'react';
import { motion } from 'framer-motion';
import { useQuery, useMutation } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { useAuth } from '@/hooks/useAuth';
import { Navigate } from 'react-router-dom';
import { PlusCircle, Users, Target, BookOpen } from 'lucide-react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
import { BackButton } from '@/components/ui/BackButton';
import { Modal } from '@/components/ui/Modal';
import { Input } from '@/components/ui/Input';
import { Textarea } from '../../components/ui/Textarea';
import { PremiumUserPicker } from '@/components/ui/PremiumUserPicker';
import { PremiumSelect } from '@/components/ui/PremiumSelect';

export function BoardAdmin() {
    const { user, isLoading } = useAuth();
    const [showTaskModal, setShowTaskModal] = useState(false);
    const [showModuleModal, setShowModuleModal] = useState(false);

    // Task form state
    const [taskTitle, setTaskTitle] = useState('');
    const [taskDescription, setTaskDescription] = useState('');
    const [taskType, setTaskType] = useState<string>('Ministry Assignment');
    const [taskPriority, setTaskPriority] = useState<string>('medium');
    const [assignedTo, setAssignedTo] = useState('');
    const [dueDate, setDueDate] = useState('');

    // Module form state
    const [moduleTitle, setModuleTitle] = useState('');
    const [moduleDescription, setModuleDescription] = useState('');
    const [moduleCategory, setModuleCategory] = useState<string>('Leadership');
    const [moduleContent, setModuleContent] = useState('');
    const [moduleVideoUrl, setModuleVideoUrl] = useState('');
    const [moduleDocumentUrl, setModuleDocumentUrl] = useState('');
    const [moduleDuration, setModuleDuration] = useState('');

    const allUsers = useQuery(
        api.users.getAllUsers,
        user && (user.role === 'admin' || user.role === 'super_admin')
            ? { adminId: user._id }
            : 'skip'
    );
    const boardStats = useQuery(
        api.boardTasks.getBoardStats,
        user ? { userId: user._id } : 'skip'
    );
    const trainingStats = useQuery(
        api.training.getTrainingStats,
        user ? { userId: user._id } : 'skip'
    );

    // Fetch all tasks for management
    const taskList = useQuery(
        api.boardTasks.getTasks,
        user ? { userId: user._id } : 'skip'
    );

    const createTask = useMutation(api.boardTasks.createTask);
    const deleteTask = useMutation(api.boardTasks.deleteTask);
    const createModule = useMutation(api.training.createModule);

    const handleDeleteTask = async (taskId: any) => {
        if (!confirm('Are you sure you want to delete this task?')) return;
        try {
            await deleteTask({ adminId: user!._id, taskId });
        } catch (error) {
            console.error('Failed to delete task:', error);
        }
    };

    if (isLoading) {
        return (
            <div className="flex items-center justify-center min-h-screen">
                <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary-500" />
            </div>
        );
    }

    if (!user) {
        return <Navigate to="/login" replace />;
    }

    if (user.role !== 'admin' && user.role !== 'super_admin') {
        return <Navigate to="/board" replace />;
    }

    const boardMembers = allUsers?.filter(u =>
        u.role === 'board_member' || u.role === 'admin' || u.role === 'super_admin'
    ) || [];

    const handleCreateTask = async () => {
        if (!taskTitle || !taskDescription || !assignedTo) return;

        try {
            await createTask({
                adminId: user._id,
                title: taskTitle,
                description: taskDescription,
                type: taskType as any,
                priority: taskPriority as any,
                assignedTo: assignedTo as any,
                dueDate: dueDate ? new Date(dueDate).getTime() : undefined,
            });

            // Reset form
            setTaskTitle('');
            setTaskDescription('');
            setAssignedTo('');
            setDueDate('');
            setShowTaskModal(false);
        } catch (error) {
            console.error('Failed to create task:', error);
        }
    };

    const handleCreateModule = async () => {
        if (!moduleTitle || !moduleDescription) return;

        try {
            const moduleCount = trainingStats?.totalModules || 0;
            await createModule({
                adminId: user._id,
                title: moduleTitle,
                description: moduleDescription,
                category: moduleCategory as any,
                content: moduleContent,
                videoUrl: moduleVideoUrl || undefined,
                documentUrl: moduleDocumentUrl || undefined,
                duration: moduleDuration ? parseInt(moduleDuration) : undefined,
                order: moduleCount + 1,
            });

            // Reset form
            setModuleTitle('');
            setModuleDescription('');
            setModuleContent('');
            setModuleVideoUrl('');
            setModuleDocumentUrl('');
            setModuleDuration('');
            setShowModuleModal(false);
        } catch (error) {
            console.error('Failed to create module:', error);
        }
    };

    return (
        <div className="min-h-screen py-8">
            <div className="container mx-auto px-4 sm:px-6 lg:px-8 max-w-6xl">
                <BackButton />
                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    className="mb-8"
                >
                    <h1 className="text-3xl md:text-4xl font-bold text-gradient mb-2">Board Admin Panel</h1>
                    <p className="text-light-muted dark:text-dark-muted">Manage tasks and training for board members</p>
                </motion.div>

                {/* Stats Overview */}
                <div className="grid md:grid-cols-3 gap-6 mb-8">
                    <Card glass>
                        <CardContent className="p-6">
                            <div className="flex items-center justify-between mb-2">
                                <Users className="w-8 h-8 text-primary-500" />
                                <span className="text-2xl font-bold text-light-text dark:text-dark-text">
                                    {boardMembers.length}
                                </span>
                            </div>
                            <h3 className="text-sm font-medium text-light-muted dark:text-dark-muted">Board Members</h3>
                        </CardContent>
                    </Card>

                    <Card glass>
                        <CardContent className="p-6">
                            <div className="flex items-center justify-between mb-2">
                                <Target className="w-8 h-8 text-accent-500" />
                                <span className="text-2xl font-bold text-light-text dark:text-dark-text">
                                    {boardStats?.totalTasks || 0}
                                </span>
                            </div>
                            <h3 className="text-sm font-medium text-light-muted dark:text-dark-muted">Total Tasks</h3>
                        </CardContent>
                    </Card>

                    <Card glass>
                        <CardContent className="p-6">
                            <div className="flex items-center justify-between mb-2">
                                <BookOpen className="w-8 h-8 text-secondary-500" />
                                <span className="text-2xl font-bold text-light-text dark:text-dark-text">
                                    {trainingStats?.totalModules || 0}
                                </span>
                            </div>
                            <h3 className="text-sm font-medium text-light-muted dark:text-dark-muted">Training Modules</h3>
                        </CardContent>
                    </Card>
                </div>

                {/* Quick Actions */}
                <div className="grid md:grid-cols-2 gap-6">
                    <Card glass>
                        <CardHeader>
                            <CardTitle>Task Management</CardTitle>
                        </CardHeader>
                        <CardContent>
                            <p className="text-light-muted dark:text-dark-muted mb-4">
                                Assign tasks to board members for ministry assignments, training, and administrative duties.
                            </p>
                            <Button
                                variant="primary"
                                onClick={() => setShowTaskModal(true)}
                                className="w-full"
                            >
                                <PlusCircle className="w-4 h-4 mr-2" />
                                Create New Task
                            </Button>
                        </CardContent>
                    </Card>

                    <Card glass>
                        <CardHeader>
                            <CardTitle>Training Modules</CardTitle>
                        </CardHeader>
                        <CardContent>
                            <p className="text-light-muted dark:text-dark-muted mb-4">
                                Create structured learning content to develop board members' leadership and ministry skills.
                            </p>
                            <Button
                                variant="secondary"
                                onClick={() => setShowModuleModal(true)}
                                className="w-full"
                            >
                                <PlusCircle className="w-4 h-4 mr-2" />
                                Create Training Module
                            </Button>
                        </CardContent>
                    </Card>
                </div>

                {/* Recent Tasks List */}
                <div className="mb-8">
                    <Card glass>
                        <CardHeader>
                            <CardTitle>Recent Tasks</CardTitle>
                        </CardHeader>
                        <CardContent>
                            <div className="space-y-4">
                                {taskList && taskList.length > 0 ? (
                                    taskList.map((task: any) => (
                                        <div key={task._id} className="flex flex-col sm:flex-row items-center justify-between p-4 rounded-lg bg-light-bg/50 dark:bg-dark-bg/50 border border-light-border/50 dark:border-dark-border/50 gap-4">
                                            <div className="flex-1 min-w-0">
                                                <div className="flex items-center gap-2 mb-1">
                                                    <h4 className="font-semibold text-light-text dark:text-dark-text truncate">{task.title}</h4>
                                                    <span className={`text-[10px] px-2 py-0.5 rounded-full uppercase font-bold tracking-wider ${task.priority === 'urgent' ? 'bg-red-500/10 text-red-500' :
                                                        task.priority === 'high' ? 'bg-orange-500/10 text-orange-500' :
                                                            task.priority === 'medium' ? 'bg-blue-500/10 text-blue-500' :
                                                                'bg-green-500/10 text-green-500'
                                                        }`}>
                                                        {task.priority}
                                                    </span>
                                                    <span className={`text-[10px] px-2 py-0.5 rounded-full uppercase font-bold tracking-wider ${task.status === 'completed' ? 'bg-green-500/10 text-green-500' :
                                                        task.status === 'in_progress' ? 'bg-blue-500/10 text-blue-500' :
                                                            'bg-gray-500/10 text-gray-500'
                                                        }`}>
                                                        {task.status.replace('_', ' ')}
                                                    </span>
                                                </div>
                                                <p className="text-sm text-light-muted dark:text-dark-muted line-clamp-1">{task.description}</p>
                                                <div className="flex items-center gap-4 mt-2 text-xs text-light-muted dark:text-dark-muted">
                                                    <span>Assigned to: <strong>{task.assignee?.name || 'Unknown'}</strong></span>
                                                    {task.dueDate && (
                                                        <span>Due: {new Date(task.dueDate).toLocaleDateString()}</span>
                                                    )}
                                                </div>
                                            </div>
                                            <Button
                                                variant="danger"
                                                size="sm"
                                                onClick={() => handleDeleteTask(task._id)}
                                            >
                                                Delete
                                            </Button>
                                        </div>
                                    ))
                                ) : (
                                    <div className="text-center py-8 text-light-muted dark:text-dark-muted">
                                        No tasks found. Create one above!
                                    </div>
                                )}
                            </div>
                        </CardContent>
                    </Card>
                </div>

                {/* Create Task Modal */}
                <Modal
                    isOpen={showTaskModal}
                    onClose={() => setShowTaskModal(false)}
                    title="Create New Task"
                >
                    <div className="space-y-4">
                        <Input
                            label="Task Title"
                            value={taskTitle}
                            onChange={(e) => setTaskTitle(e.target.value)}
                            placeholder="e.g., Prepare Sunday Teaching"
                        />

                        <Textarea
                            label="Description"
                            value={taskDescription}
                            onChange={(e) => setTaskDescription(e.target.value)}
                            rows={3}
                            placeholder="Describe the task..."
                        />

                        <div className="grid grid-cols-2 gap-4">
                            <PremiumSelect
                                label="Type"
                                value={taskType}
                                onChange={setTaskType}
                                options={[
                                    { value: "Ministry Assignment", label: "Ministry Assignment" },
                                    { value: "Administrative", label: "Administrative" },
                                    { value: "Training", label: "Training" },
                                    { value: "Personal Development", label: "Personal Development" },
                                    { value: "Team Project", label: "Team Project" }
                                ]}
                            />

                            <PremiumSelect
                                label="Priority"
                                value={taskPriority}
                                onChange={setTaskPriority}
                                options={[
                                    { value: "low", label: "Low" },
                                    { value: "medium", label: "Medium" },
                                    { value: "high", label: "High" },
                                    { value: "urgent", label: "Urgent" }
                                ]}
                            />
                        </div>

                        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                            <div className="relative z-40">
                                <PremiumUserPicker
                                    users={boardMembers}
                                    selectedUserId={assignedTo}
                                    onSelect={setAssignedTo}
                                    label="Assign To"
                                />
                            </div>
                            <Input
                                type="date"
                                label="Due Date"
                                value={dueDate}
                                onChange={(e) => setDueDate(e.target.value)}
                            />
                        </div>
                        <div className="flex gap-3 pt-4">
                            <Button variant="ghost" onClick={() => setShowTaskModal(false)}>
                                Cancel
                            </Button>
                            <Button variant="primary" onClick={handleCreateTask}>
                                Create Task
                            </Button>
                        </div>
                    </div>
                </Modal>

                {/* Create Module Modal */}
                <Modal
                    isOpen={showModuleModal}
                    onClose={() => setShowModuleModal(false)}
                    title="Create Training Module"
                >
                    <div className="space-y-4">
                        <Input
                            label="Module Title"
                            value={moduleTitle}
                            onChange={(e) => setModuleTitle(e.target.value)}
                            placeholder="e.g., Leadership Fundamentals"
                        />

                        <Textarea
                            label="Description"
                            value={moduleDescription}
                            onChange={(e) => setModuleDescription(e.target.value)}
                            rows={2}
                            placeholder="Brief description..."
                        />

                        <PremiumSelect
                            label="Category"
                            value={moduleCategory}
                            onChange={setModuleCategory}
                            options={[
                                { value: "Leadership", label: "Leadership" },
                                { value: "Ministry Skills", label: "Ministry Skills" },
                                { value: "Spiritual Growth", label: "Spiritual Growth" },
                                { value: "Technical Skills", label: "Technical Skills" },
                                { value: "Team Building", label: "Team Building" }
                            ]}
                        />

                        <Textarea
                            label="Content (HTML)"
                            value={moduleContent}
                            onChange={(e) => setModuleContent(e.target.value)}
                            rows={4}
                            placeholder="<h2>Introduction</h2><p>Content here...</p>"
                            className="font-mono text-sm"
                        />

                        <Input
                            label="Video URL (Optional - YouTube, Vimeo embed)"
                            value={moduleVideoUrl}
                            onChange={(e) => setModuleVideoUrl(e.target.value)}
                            placeholder="https://youtube.com/embed/..."
                        />

                        <Input
                            label="Document URL (Optional - Google Drive, Dropbox)"
                            value={moduleDocumentUrl}
                            onChange={(e) => setModuleDocumentUrl(e.target.value)}
                            placeholder="https://drive.google.com/..."
                        />

                        <Input
                            label="Duration (Minutes)"
                            type="number"
                            value={moduleDuration}
                            onChange={(e) => setModuleDuration(e.target.value)}
                            placeholder="30"
                        />

                        <div className="flex gap-3 pt-4">
                            <Button variant="ghost" onClick={() => setShowModuleModal(false)}>
                                Cancel
                            </Button>
                            <Button variant="primary" onClick={handleCreateModule}>
                                Create Module
                            </Button>
                        </div>
                    </div>
                </Modal>
            </div>
        </div>
    );
}
